import React, { useEffect, useState, useRef } from "react";
import { useLocation, useNavigate } from "react-router-dom";
import Table from "../../../components/table";
// import useDivision from "../../store/division";
import Button from "../../../components/button";
import { toast } from "react-toastify";
import { IconButton, Stack, Typography, Grid } from "@mui/material";
import { COLORS } from "../../../helpers/constants";
import AllMatScan from "./AllMatScan"
import CheckCircleOutlineIcon from '@mui/icons-material/CheckCircleOutline';
import ArrowBackIcon from '@mui/icons-material/ArrowBack';
import { t } from "i18next";
import useJob from "../../../store/operatorPortal/job";
import ResModal from './ResModal'
import { set } from "lodash";

const AssemblyScan = () => {
  const location = useLocation()
  const navigate = useNavigate()
  const locationData = location.state
  console.log("locationData assebly", locationData)
  const { OrderMaterialList, createAssembly, currentAssemblyOrderStatus,updateAssemblyOrderStatus, getMachineStatus,AssemblyMaterialList,updateBOM } = useJob()
  const [tableData, setTableData] = useState([]);
  const [loading, setLoading] = useState();
  const [total, setTotal] = useState(0);
  const [edit, setEdit] = useState(false)
  const [modalOpen, setModalOpen] = useState(false);
  const [page, setPage] = useState(0);
  const [data, setData] = useState({
    name: "",
    metadata: {}
  });

  const [payload, setPayload] = useState()
  const [machineId, setMachineId] = useState(localStorage.getItem('machineId') || "");

  const [runningStatus, setRunningStatus] = useState(false)
  const [isConfirmDisabled, setIsConfirmDisabled] = useState(true);
  const [modalMessage, setModalMessage] = useState("");
  const [msgtype, setMsgType] = useState("")
  

  useEffect(() => {
    if (!tableData.length) {
      setIsConfirmDisabled(true);
      return;
    }
  
    // Check if all issued items in every material have scanned: true
    const allMaterialsScanned = tableData.every((material) =>
      material.issued.every((issuedItem) => issuedItem.scanned === true)
    );
  
    setIsConfirmDisabled(allMaterialsScanned); // Disable button if not all are scanned
  }, [tableData]); // Remove payload dependency, as we only check tableData
  


  useEffect(() => {
    console.log("workOrderTask--scan---", locationData)
    fetchMaterials();
  }, [page]);

  const fetchMaterials = async () => {
    setLoading("fetch");
  
    const [_list, _count] = await AssemblyMaterialList(locationData.id, page + 1);
  
    setTableData(() =>
      _list.items.map((item,index) => {
        // Clone and parse scanned list
        const scannedQueue = [...(item.scanned || [])].map((scan) => ({
          ...scan,
          quantity: Number(scan.quantity),
        }));
    
        // Process original issued items
        const updatedIssued = item.issued.map((issuedItem) => {
          const scanIndex = scannedQueue.findIndex(
            (scan) =>
              scan.grn_number === issuedItem.grn_number && scan.quantity > 0
          );
    
          if (scanIndex !== -1) {
            scannedQueue[scanIndex].quantity -= issuedItem.quantity;
            if (scannedQueue[scanIndex].quantity <= 0) {
              scannedQueue.splice(scanIndex, 1);
            }
            return { ...issuedItem, scanned: true };
          }
    
          return { ...issuedItem, scanned: false };
        });
    // Function to find unmatched scanned items
      const findUnmatchedScannedItems = (data) => {
        const issuedGRNs = data.issued.map((item) => item.grn_number);
        const unmatchedScannedItems = data.scanned?.filter((scan) => {
            // Check if the scanned GRN is not in the issued GRN list
            return !issuedGRNs.includes(scan.grn_number)&& scan.shop_floor===true;
        });

        return unmatchedScannedItems;
      };

      const unmatchedItems = findUnmatchedScannedItems(item) || []; // Ensure unmatchedItems is always an array
      const totalQuantity = unmatchedItems.reduce((sum, item) => sum + Number(item.quantity), 0);

      // console.log("Unmatched Scanned Items:",index+1, unmatchedItems);
        // If there's a shop_floor_qty, push it into issued with a placeholder GRN number
        if (item.shop_floor_qty && item.shop_floor_qty > 0) {
          updatedIssued.push({
            quantity: item.shop_floor_qty,
            grn_number: "SHOP_FLOOR", // or any placeholder string
            scanned:totalQuantity>=item.shop_floor_qty?true: false,
            scanQuantity:totalQuantity
          });
        }
    
        return { ...item, issued: updatedIssued };
      })
    );
    
    setLoading(false);
    if(_list?.materials_verified){
          navigate("/assembly-machine-data",{state:locationData.id})
        }
  };
  
  

  const UpdateBOMItems = async (qr_code,val) => {
  let res = await updateBOM(locationData.id, { "qr_code": qr_code,"shop_floor": val }); 
  console.log("UpdateBOMItems respose----", res)
  const [material_name, quantityString, unit, grnNumber, grnDate] = qr_code.split("#");

  if(res){
    setMsgType("success")
    setModalMessage(material_name + " " + t("scanned successfully"));
    setModalOpen('ResModal');
    setTimeout(() => {
      setModalOpen(false);
    }, 1500);
  }
}

function handleModalMsg(material_name,val,ms){
  setMsgType(ms)
  setModalMessage(material_name + " " + val);
  setModalOpen('ResModal');
  setTimeout(() => {
    setModalOpen(false);
    setMsgType("")

  }, 2000);
}

  const SubmitScanned = async () => {
    setModalOpen('detail')
    let newReq ={
        "materials":payload
    }
    let res = await createAssembly(locationData.id,newReq );
    console.log("SubmitScanned updateOrderStatus respose----", res)
    if (res && res.message === 'OK') {
      let req = { "status": "RUNNING" }
      let res = await updateAssemblyOrderStatus(locationData.id, req)
      setRunningStatus(true)
      setTimeout(() => {
        navigate("/assembly-machine-data",{state:locationData.id})
      }, 2000);
    }else{
      setTimeout(() => {
      window.location.reload()
    }, 2000);
    }
  };


  const handleDelete = async (id) => {
    setLoading("fetch");
    await fetchMaterials();
    setLoading(false);
  };
  const handleUpdate = async (id, is_admin) => {
    setLoading("fetch");
    const payload = {
      "is_admin": is_admin === true ? false : true
    }
    await fetchMaterials();
    setLoading(false);
  };

  const LABELS = [
    {
      id: 1,
      title: "Name",
      render: (rowData) => rowData?.material_name,
    },
   
    {
      id: 3,
      title: "Description",
      render: (rowData) => rowData?.material_description || '-'
    },
    {
      id: 2,
      title: "Issued",
      render: (rowData) => rowData?.issued?.map((item)=><Typography sx={{width:'80%',backgroundColor:item.scanned?COLORS.LIGHT_GREEN:COLORS.LGG_RED,mb:0.3}}>GRN NO : {item.grn_number} , QTY : {item.quantity}  {item.scanQuantity?`, Scan Qty :${item.scanQuantity}`:""}</Typography>),
    },
    // {
    //   id: 22,
    //   title: "Shopfloor",
    //   render: (rowData) => rowData?.shop_floor_qty,
    // },
  ];

  const handleKeyPress = (e) => {
    if (e.key === "Enter") {
      handleScan(e.target.value);
      e.target.value = ""; // Clear the input for the next scan
    }
  };
  const handleScan = (data) => {
    handleBarcodeScannerModalSubmit(data);
  };

  const handleBarcodeScannerModalSubmit = (inputData) => {
    const [material_name, quantityString, unit, grnNumber, grnDate] = inputData.split("#");
  
    const quantity = parseFloat(quantityString);
    if (isNaN(quantity)) {
      console.log("Invalid quantity");
      return;
    }
  
    const currentMaterial = tableData.find(
      (item) => item.material_name === material_name
    );
  
    if (!currentMaterial) {
      // toast.warn("Material not found in BOM");
      handleModalMsg(material_name, "Material not found in BOM","");
      return;
    }
  
    // Auto-scroll and focus on the material name
    const materialIndex = tableData.findIndex(
      (item) => item.material_name === material_name
    );
    if (materialIndex !== -1) {
      // toast.success(`${material_name} ${materialIndex}`);
      setTimeout(() => {
        const tableElement = document.querySelector(".table-container"); // Ensure this selector matches your table container
        if (tableElement) {
          const rowElement = tableElement.querySelectorAll("tbody tr")[materialIndex]; // Adjust selector to target rows inside tbody
          if (rowElement) {
            rowElement.scrollIntoView({ behavior: "smooth", block: "center" }); // Smooth scroll to the row
            rowElement.focus({ preventScroll: true }); // Focus without triggering another scroll
          }
        }
      }, 100); // Adjust delay if necessary
    }
  
    setPayload((prevPayload) => {
      const updatedPayload = { ...prevPayload };
    
      const issuedEntries = currentMaterial.issued;
    
      if (!issuedEntries || issuedEntries.length === 0) {
        // toast.warn(`Material not issued for ${material_name}`);
      handleModalMsg(material_name, "Material not issued","");

        return prevPayload;
      }
    
      const scannedGRNandQuantity = {
        quantity: quantity,
        grn_number: grnNumber,
      };
    
      let grnMismatchWarned = false; // Flag to ensure GRN mismatch warning is shown only once

      setTableData((prevItems) =>
        prevItems.map((item) => {
          if (item.material_name !== material_name) return item;
      
          let updateBOMCalled = false; // Flag to ensure UpdateBOMItems is called only once
          let itemUpdated = false; // Move this outside 'issued.map' so it applies to the entire 'issued' array
      
          const updatedIssued = item.issued.map((issuedItem) => {
            if (itemUpdated) {
              return issuedItem; // Already updated one item, return others as is
            }
      
            // Case 1: Direct GRN match and quantity is sufficient
            if (
              issuedItem.grn_number === scannedGRNandQuantity.grn_number &&
              !issuedItem.scanned &&
              scannedGRNandQuantity.quantity >= issuedItem.quantity
            ) {
              if (!updateBOMCalled) {
                UpdateBOMItems(inputData, false);
                updateBOMCalled = true;
              }
              itemUpdated = true; // Now we set itemUpdated true globally
              return { ...issuedItem, scanned: true };
            }
      
            // Handle mismatched GRN numbers (only for the first case)
            if (
              !grnMismatchWarned &&
              issuedItem.grn_number !== scannedGRNandQuantity.grn_number &&
              issuedItem.grn_number !== "SHOP_FLOOR"
            ) {
              // toast.warn(`GRN mismatch: Expected ${issuedItem.grn_number}, got ${scannedGRNandQuantity.grn_number}`);
              handleModalMsg(material_name, `GRN mismatch: Expected ${issuedItem.grn_number}, got ${scannedGRNandQuantity.grn_number}`,"");
              
              grnMismatchWarned = true;
              return issuedItem;
            }
      
            // Case 2: Fallback to SHOP_FLOOR if GRNs don't match but quantity is sufficient
            if (
              issuedItem.grn_number === "SHOP_FLOOR" &&
              scannedGRNandQuantity.grn_number !== "SHOP_FLOOR"
            ) {
              if (!updateBOMCalled) {
                UpdateBOMItems(inputData, true);
                updateBOMCalled = true;
              }
              const currentScanQty = issuedItem.scanQuantity || 0;
              const updatedScanQuantity = currentScanQty + scannedGRNandQuantity.quantity;
      
              const scanned = updatedScanQuantity >= issuedItem.quantity;
      
              return {
                ...issuedItem,
                scanQuantity: updatedScanQuantity,
                scanned,
              };
            }
      
            return issuedItem;
          });
      
          return { ...item, issued: updatedIssued };
        })
      );
      
      
      // Update payload
      if (!updatedPayload[material_name]) {
        updatedPayload[material_name] = [];
      }
      updatedPayload[material_name].push({
        quantity: quantity,
        grn_number: grnNumber,
      });
    
      console.log("Updated Payload Inside setPayload:", updatedPayload);
      return updatedPayload;
    });
    
  
    if (inputRef.current) {
      inputRef.current.focus();
    }
  };
  
  
  useEffect(() => {
    console.log("Updated Payload in useEffect:", payload);
  }, [payload]);

  const inputRef = useRef(null);

  useEffect(() => {
    if (inputRef.current) {
      inputRef.current.focus();
    }
  }, []);

  return (
    <div className="mx-3 my-2" style={{ display: 'flex', flexDirection: 'column', height: '100vh' }}>
      <div style={{ flex: '0 0 auto', position: 'sticky', top: 0, backgroundColor: 'white', zIndex: 1 }}>
        <div style={{ display: 'flex', flexDirection: 'row', gap: 2, alignItems: 'center' }}>
          <IconButton onClick={() => navigate(-1)}>
            <ArrowBackIcon />
          </IconButton>
          <h1 className="mt-4">{locationData.fg_code} &gt; {locationData.work_order_order_id}</h1>
        </div>
        <Stack sx={{ flexDirection: 'row', justifyContent: 'space-between', alignItems: 'center' }}>
          <h1 className="fw-24 mt-1">Please scan the materials and circuits</h1>
          <input ref={inputRef} style={{ height: '1.5rem', width: '26rem' }} type="text" onKeyPress={handleKeyPress} />
        </Stack>
      </div>
      <div style={{ flex: '1 1 auto', overflowY: 'auto' }}>
        <Table
          columns={LABELS}
          rows={tableData.map((row, index) => ({
            ...row,
            key: `row-${index}`, // Add a unique key for each row
          }))}
          loading={loading === "fetch"}
          titleCellClasses="c-gray fw-700"
          page={page}
          setPage={setPage}
          total={total}
          serialize
          aria-label="Assembly Scan Table" // Add accessibility label
        />
      </div>
      <AllMatScan
        open={modalOpen === "detail"}
        onClose={() => { setModalOpen(false); setEdit(false) }}
        edit={edit}
      />
      <Stack
        direction="row"
        alignItems="center"
        justifyContent="space-between"
        mt={4}
        mb={1}
      >
        <Button bordered gray >
          Cancel
        </Button>
        <Button color={'light_green'} onClick={() => 
          // SubmitScanned()
          alert("Please scan all the materials and circuits before confirming.")
          } disabled={!isConfirmDisabled}>
          <Typography color={COLORS.TEXT_GRAY}>
            Confirm
          </Typography>
        </Button>
      </Stack>
      <ResModal 
      msgtype={msgtype}
      open={modalOpen==='ResModal'}
       onClose={() => setModalOpen(false)} message={modalMessage} />
    </div>
  );
};

export default AssemblyScan;
