import React from "react";
import { resolveResponse } from "../api/utils";
import REQUEST, { CONTENT_TYPES } from "../api/index";
import { ENDPOINTS } from "../api/endpoints";
import { useSelector } from "react-redux";
import { REDUCER_TYPES } from "..";
import { useState, useCallback } from "react";

const useMaterialPlanning = () => {
  const { token } = useSelector((state) => state[REDUCER_TYPES.AUTH]);
  const [loading, setLoading] = useState(false);
  const [error, setError] = useState(null);

  const headers = {
    Authorization: `Bearer ${token}`,
    "Content-Type": CONTENT_TYPES.APPLICATION_JSON,
  };

  const matrixCount = async (page = 1,divId) => {
    const client = new REQUEST();
    const res = await client.get(
      `machining/order-matrix/`,
      {},
      headers,
      resolveResponse
    );

    if (res?.data) {
      console.log("res  ",res.data)
      return [res?.data];
    }
    return [[], 0];
  };

  const orderList = async (page = 1,orderid) => {
    const client = new REQUEST();
    const res = await client.get(
      `machining/all-orders/?page=${page}&page_size=10`,
      {},
      headers,
      resolveResponse
    );

    if (res?.data) {
      console.log("res  ",res.data)
      return [res?.data];
    }
    return [[], 0];
  };

  const orderTasks = async (page = 1,orderid) => {
    const client = new REQUEST();
    const res = await client.get(
      `machining/order-tasks/${orderid}/`,
      {},
      headers,
      resolveResponse
    );

    if (res?.data) {
      console.log("res  ",res.data)
      return [res?.data];
    }
    return [[], 0];
  };
  
  const orderAssetList = async (page = 1,orderid) => {
    const client = new REQUEST();
    const res = await client.get(
      `machining/asset-list/${orderid}/`,
      {},
      headers,
      resolveResponse
    );

    if (res?.data) {
      console.log("res  ",res.data)
      return [res?.data];
    }
    return [[], 0];
  };

  const orderAssetLPCList = async (page = 1,orderid,stage_type) => {
    const client = new REQUEST();
    const res = await client.get(
      `machining/asset-list/${orderid}/?stage_type=${stage_type}`,
      {},
      headers,
      resolveResponse
    );

    if (res?.data) {
      console.log("res  ",res.data)
      return [res?.data];
    }
    return [[], 0];
  };

  const machineAssign = async (mapId,payload,stage_id) => {
    const client = new REQUEST();
    const res = await client.patch(
      `machining/machine-assignment/${mapId}/`,
      payload,
      headers,
      (res) => resolveResponse(res, null)
    );
    return res?.data;
  };
  const machineAssignMultiple = async (mapId,payload,stage_id) => {
    const client = new REQUEST();
    const res = await client.patch(
      `machining/machine-assignment/${mapId}/`,
      payload,
      headers,
      (res) => resolveResponse(res, null, null, null, true)
    );
    return res?.data;
  };

  const plants = async (page = 1,companyId) => {
    const client = new REQUEST();
    const res = await client.get(
      `monitoring/company/${companyId}/plants/?page=${page}&page_size=10`,
      {},
      headers,
      resolveResponse
    );

    if (res?.data) {
      console.log("res  ",res.data)
      return [res?.data];
    }
    return [[], 0];
  };

  const divisions = async (page = 1,plantId) => {
    const client = new REQUEST();
    const res = await client.get(
      `monitoring/plant/${plantId}/divisions/?page=${page}&page_size=10`,
      {},
      headers,
      resolveResponse
    );

    if (res?.data) {
      console.log("res  ",res.data)
      return [res?.data];
    }
    return [[], 0];
  };

  const lines = async (page = 1,divisionId) => {
    const client = new REQUEST();
    const res = await client.get(
      `monitoring/division/${divisionId}/lines/?page=${page}&page_size=10`,
      {},
      headers,
      resolveResponse
    );

    if (res?.data) {
      console.log("res  ",res.data)
      return [res?.data];
    }
    return [[], 0];
  };

  const summary = async (page = 1,
    company_id,
    plant_id,
    division_id,
    line_id,
    date,
    shift_id
    ) => {
    const client = new REQUEST();
    const res = await client.get(
      `monitoring/get-daily-summary/?company_id=${company_id}&plant_id=${plant_id}&division_id=${division_id}&line_id=${line_id}&date=${date}&shift_id=${shift_id}&page=${page}&page_size=10`,
      {},
      headers,
      resolveResponse
    );

    if (res?.data) {
      console.log("res  ",res.data)
      return [res?.data];
    }
    return [[], 0];
  };

  const create = async (payload = {}) => {
    const client = new REQUEST();
    const res = await client.post(
      `orders/issue-extra-material/`,
      payload,
      headers,
      (res) => resolveResponse(res, null)
    );
    return res?.data;
  };

  const update = async (failid,payload) => {
    const client = new REQUEST();
    const res = await client.put(
      `orders/update-material-failure/${failid}/`,
      payload,
      headers,
      (res) => resolveResponse(res, null)
    );
    return res?.data;
  };

    const apiRequest = new REQUEST();

    const fetchOrders = useCallback(
      async (
        pageNumber = 1,
        isSortedByCode,
        isSortedByOrderType,
        isSortedByStatus,
        isSortedByOrderId,
        isSortedByName,
        isSortedByQuantity,
        sortingOrder,
        orderIdFilterValue,
        fgCodeFilterValue,
        orderTypeFilterValue,
        orderStatusFilterValue
      ) => {
        let route = `machining/all-orders/?page=${pageNumber}&page_size=10`;
        // let route = `${ENDPOINTS.ORDERS}/all/?page=${pageNumber}`;
  
        // Check if any sorting is applied and add "&sort_by=" to the route if so
        // only if sorting is null, then sorting is not applied
        if (
          isSortedByCode ||
          isSortedByOrderType ||
          isSortedByStatus ||
          isSortedByOrderId ||
          isSortedByName ||
          isSortedByQuantity
        ) {
          // sorting is true (ascending order case)
          route += "&sort_by=";
        } else if (
          isSortedByCode === false ||
          isSortedByOrderType === false ||
          isSortedByStatus === false ||
          isSortedByOrderId === false ||
          isSortedByName === false ||
          isSortedByQuantity === false
        ) {
          // sorting is true (decending order case)
          route += "&sort_by=";
        }
        //Iterate Through sortingOrder Array: Loop through the sortingOrder array to get the column IDs in the desired order.
        //Handle Each Sorting State: Use a switch statement or conditional logic to handle each sorting state based on the column ID.
        sortingOrder.forEach((columnId) => {
          switch (columnId) {
            case "fg_code":
              switch (isSortedByCode) {
                case false:
                  console.log("Code is false");
                  route += route.endsWith("&sort_by=") ? "-fg_code" : ",-fg_code";
                  break;
                case true:
                  console.log("Code is true");
                  route += route.endsWith("&sort_by=") ? "fg_code" : ",fg_code";
                  break;
                default:
                  console.log("Code is an unexpected value");
              }
              break;
            case "order_type":
              switch (isSortedByOrderType) {
                case false:
                  console.log("Order type is false");
                  route += route.endsWith("&sort_by=")
                    ? "-order_type"
                    : ",-order_type";
                  break;
                case true:
                  console.log("Order type is true");
                  route += route.endsWith("&sort_by=")
                    ? "order_type"
                    : ",order_type";
                  break;
                default:
                  console.log("Order type is an unexpected value");
              }
              break;
            case "status":
              switch (isSortedByStatus) {
                case false:
                  console.log("Status is false");
                  route += route.endsWith("&sort_by=") ? "-status" : ",-status";
                  break;
                case true:
                  console.log("Status is true");
                  route += route.endsWith("&sort_by=") ? "status" : ",status";
                  break;
                default:
                  console.log("Status is an unexpected value");
              }
              break;
            case "order_id":
              switch (isSortedByOrderId) {
                case false:
                  console.log("Order ID is false");
                  route += route.endsWith("&sort_by=")
                    ? "-order_id"
                    : ",-order_id";
                  break;
                case true:
                  console.log("Order ID is true");
                  route += route.endsWith("&sort_by=") ? "order_id" : ",order_id";
                  break;
                default:
                  console.log("Order ID is an unexpected value");
              }
              break;
            case "name":
              switch (isSortedByName) {
                case false:
                  console.log("Name is false");
                  route += route.endsWith("&sort_by=") ? "-name" : ",-name";
                  break;
                case true:
                  console.log("Name is true");
                  route += route.endsWith("&sort_by=") ? "name" : ",name";
                  break;
                default:
                  console.log("Name is an unexpected value");
              }
              break;
            case "quantity":
              switch (isSortedByQuantity) {
                case false:
                  console.log("Quantity is false");
                  route += route.endsWith("&sort_by=")
                    ? "-quantity"
                    : ",-quantity";
                  break;
                case true:
                  console.log("Quantity is true");
                  route += route.endsWith("&sort_by=") ? "quantity" : ",quantity";
                  break;
                default:
                  console.log("Quantity is an unexpected value");
              }
              break;
            default:
              console.log("Unknown column ID:", columnId);
          }
        });
  
        // add filters
        if (orderIdFilterValue) {
          route += "&order_id=";
          route += orderIdFilterValue;
        }
        if (fgCodeFilterValue) {
          route += "&fg_code=";
          route += fgCodeFilterValue;
        }
        if (orderTypeFilterValue) {
          route += "&type=";
          route += orderTypeFilterValue;
        }
        if (orderStatusFilterValue) {
          route += "&running_status=";
          route += orderStatusFilterValue;
        }
  
        setLoading(true);
        setError(null);
        try {
          const response = await apiRequest.get(route, {}, headers);
          return response.data;
        } catch (error) {
          setError(error.message || "Error in fetchOrders:");
        } finally {
          setLoading(false);
        }
      },
      [apiRequest, headers]
    );

  return {
    fetchOrders,
    matrixCount,
    orderList,
    orderTasks,
    orderAssetList,
    machineAssign,
    machineAssignMultiple,
    orderAssetLPCList,
    // summary,
    // plants,
    // divisions,
    // lines,
    create,
    update
  };
};

export default useMaterialPlanning;
