import React, { useEffect, useState, useRef } from "react";
import { useLocation, useNavigate } from "react-router-dom";
import Table from "../../components/table";
import Button from "../../components/button";
import AddCircleIcon from '@mui/icons-material/AddCircle';
import Tooltip from '@mui/material/Tooltip';
import { toast } from "react-toastify";
import { IconButton, Stack, Typography, Grid } from "@mui/material";
import AddAssign from './AddAssign'
import useUser from "../../store/users";
import usePendingItems from "../../store/pending_items";
import useQuestion from "../../store/questionBank";
import { Delete } from "@mui/icons-material";
import { COLORS } from "../../helpers/constants";
import { useTranslation } from 'react-i18next';
import { t } from "i18next";
import QrCodeScannerIcon from '@mui/icons-material/QrCodeScanner';
import { setFailItem } from '../../store/pending_items/f_items'
import { useDispatch, useSelector } from "react-redux";
import moment from "moment";
import { formatNumberWithCommas } from "../../helpers/utils";


const StorePendingItems = () => {
  const location = useLocation()
  const navigate = useNavigate()
  const dispatch = useDispatch()
  const pendingData = location.state
  const groupName = useSelector((state) => state?.Failure_item?.data);
  const [users, setUsers] = useState([]);
  const [loading, setLoading] = useState();
  const [total, setTotal] = useState(0);
  const [edit, setEdit] = useState(false)
  const [modalOpen, setModalOpen] = useState("testDetail");
  const [page, setPage] = useState(0);
  const [data, setData] = useState({

  });
  const inputRef = useRef(null);
  const { list, pendinglist, create, update } = usePendingItems();
  const [errors, setErrors] = useState(new Map());
  const [selectQuestions, setSelectedQuestions] = useState([])
  const [skills, setSkills] = useState([])
  const [testList, setTestList] = useState([])
  const [testId, setTestId] = useState('')

  const COLUMNSS = [
    {
      id: 1,
      title: t('orderNo'),
      render: (rowData) =>
        <Typography>{rowData.work_order?.order_id}</Typography>,
    },
    {
      id: 2,
      title: t('FGCode'),
      render: (rowData) => <Typography>{rowData.work_order?.name}</Typography>,
    },
    {
      id: 3,
      title: t('materialName'),
      render: (rowData) => <Typography>{rowData.material?.material_code}</Typography>,
    },
    {
      id: 4,
      title: t('resolutionDate'),
      render: (rowData) => <Typography>{moment(rowData?.work_order?.date_created).format('ll')}</Typography>,
    },
    {
      id: 44,
      title: t('shortQty'),
      render: (rowData) => <Typography>{formatNumberWithCommas(rowData?.pending_quantity, 2)}</Typography>,
    },
    {
      id: 5,
      title: t('status'),
      render: (rowData) => <Typography>{rowData?.status === true ? 'True' : 'False'}</Typography>,
    },
    //   {
    //   id: 8,
    //   title: t('action'),
    //   render: (rowData) => 
    //     <>
    //   {/* <IconButton
    //   sx={{ height: 25, width: 25 }}
    //   onClick={() => handleDelete(rowData.id)}
    // >
    //   <Delete color="error" fontSize="small" />
    // </IconButton> */}
    // <Stack sx={{flexDirection:'row',gap:1}}>
    // <Tooltip title="scan item">
    //   <IconButton
    //   sx={{ height: 25, width: 25 }}
    //   onClick={() => {setModalOpen("addAssign");dispatch(setFailItem(rowData))}}
    // >
    //   <QrCodeScannerIcon color="primary" fontSize="small" />
    // </IconButton>
    // </Tooltip>
    // </Stack>
    // </>

    //   },
  ]

  useEffect(() => {
    //  fetchSkill()
    fetchTestList()
    //  fetchUsers()
  }, [page])
  useEffect(() => {
    if (inputRef.current) {
      inputRef.current.focus();
    }
  }, []);


  const fetchTestList = async () => {
    const [res] = await pendinglist(pendingData.id, page + 1);
    console.log("test master-------", res.count)
    setTotal(res.count)
    setTestList(res.results);
    if (inputRef.current) {
      inputRef.current.focus();
    }
  };

  // const fetchUsers = async () => {
  //   const [_list, _count] =  await userList(page + 1)
  //   const newOptr = _list.filter(item => !item.is_operator && item.status === true);
  //   const newUsers = newOptr.map(item => ({
  //     value: item.username,
  //     key: item.id
  //   }));
  //   setUsers(prevUsers => [...prevUsers,...newUsers]);
  // };

  const handleKeyPress = (e) => {
    let pQR = e.target.value
    if (e.key === "Enter") {
      // handleScan(e.target.value);
      e.target.value = ""; // Clear the input for the next scan
      createMat(pQR)
    }
  };



  const handleUpdate = async (id, is_admin) => {
    setLoading("fetch");
    const payload = {
      "is_admin": is_admin === true ? false : true
    }
    await update(id, payload);
    // await fetchCompany();
    setLoading(false);
  };

  const handleChange = (e) => {
    const { name, value } = e.target;
    setErrors((prev) => {
      prev.delete(name);
      return prev;
    });
    setData((prev) => ({ ...prev, [name]: value }));
  };


  const createMat = async (packetQR) => {
    console.log("data in create mat------", groupName)
    const payload = {
      // material_failure_id : groupName?.id,
      workorder_id: pendingData?.id,
      material_qr: packetQR,
      // material_code: data['material_code'],
      // material_grn : data['material_grn'],
      // material_quantity : data['material_quantity']
    }
    let res = await create(payload)
    console.log("res - create mat------", res)
    fetchTestList()
    // let failid = groupName?.id
    // if(res?.message=== 'OK'){
    //   await update(failid,{status:false,comments:data['comment']})
    // }else{
    //   await update(failid,{status:true,comments:data['comment']})
    // }
    // setModalOpen(false)
  }

  return (
    <div className="mx-3 my-2">
      <div className="flex items-center justify-between">
        <Stack sx={{ flexDirection: 'row', alignItems: 'center', mb: 1 }}>
          {/* <ArrowBackIcon color="primary" sx={{mr:1,cursor:'pointer'}} onClick={()=>navigate(-1)}/> */}
          <Typography className="fw-20-500-36">
            {t('storePendingItems')}
            {/* <span className="fs-30 ml-1 c-gray">({users.length})</span> */}
          </Typography>
        </Stack>
        {/* <Stack sx={{flexDirection:'row',gap:1}}>
        <Button color={'gray'} onClick={() => setModalOpen("createTest")}>
          <Typography className="fw-20-500-24">
          {t('createTest')}
          </Typography>
          </Button>
       </Stack> */}

      </div>
      <Stack sx={{ flexDirection: 'row', justifyContent: 'space-between', alignItems: 'center' }}>
        <h1 className="fw-24 mt-1">Please scan the material and confirm</h1>
        <input ref={inputRef} style={{ height: '1.5rem', width: '26rem' }} type="text" onKeyPress={handleKeyPress} />
      </Stack>
      <Table
        columns={COLUMNSS}
        rows={testList}
        // rowsPerPage={10}
        loading={loading === "fetch"}
        titleCellClasses="c-gray fw-700"
        page={page}
        setPage={setPage}
        paginator
        serialize
        total={total}
        tableBodyColor={COLORS.WHITE}
      />

      <AddAssign
        open={modalOpen === "addAssign"}
        onClose={() => { setModalOpen(false) }}
        onSubmit={createMat}
        users={users}
        testList={testList}
        data={data}
        setData={setData}
        openModal={modalOpen}
      />
    </div>
  );
};

export default StorePendingItems;
