import React, { useState } from "react";
import Radio from '@mui/material/Radio';
import RadioGroup from '@mui/material/RadioGroup';
import FormControlLabel from '@mui/material/FormControlLabel';
import FormControl from '@mui/material/FormControl';
import FormLabel from '@mui/material/FormLabel';
import Modal from "../../components/modal";
import Input from "../../components/input";
import { Stack, Typography } from "@mui/material";
import Button from "../../components/button";
import SchemaExtractor from "../../components/UI/SchemaExtractor";
import { COLORS, SCHEMA_TYPES, VALIDATORS, } from '../../helpers/constants'
import { Validate } from "../../helpers/utils";
import JsonCreator from "../../components/UI/JsonCreator";
import RadioButtonGroup from "../../components/input/radio";
import crossIcon from '../../assets/svg/cross-icon.svg'
import { t } from "i18next";
import { Autocomplete, TextField, Chip } from '@mui/material';
import { Height } from "@mui/icons-material";
import useUser from "../../store/users";


const Add = ({ open, onClose, data, setData, onSubmit, loading, edit, selectedOptions, setSelectedOptions }) => {
const {listByUserName}= useUser()
const[options,setOptions]=useState([])
const [autocompleteError, setAutocompleteError] = useState(false); // State for Autocomplete error

const fetchUser = async (e) => {
    const _list = await listByUserName(e || 'admin'); // Corrected assignment
    console.log("assets list", _list);
    if (Array.isArray(_list)) {
        const newUsers = _list.map(item => ({
            label: item.username || item.fullname || "Unknown", // Fallback for label
            id: item.id
        }));
        setOptions(newUsers); // Replaced appending with overwriting
    } else {
        console.error("Expected _list to be an array, but got:", _list);
    }
}
 
  const USER_SCHEMA = [
    {
      title: t('areaName'),
      placeholder: t('enterAreaName'),
      type: SCHEMA_TYPES.INPUT,
      name: "area_name",
      validator: VALIDATORS.REQUIRED,
    },
    {
      title: t('description'),
      placeholder: t('enterDescription'),
      type: SCHEMA_TYPES.INPUT,
      name: "description",
      validator: VALIDATORS.REQUIRED,
    },
  ];
 
  const [errors, setErrors] = useState(new Map());

  const handleChange = (e) => {
    const { name, value } = e.target;
    setErrors((prev) => {
      prev.delete(name);
      return prev;
    });
    setData((prev) => ({ ...prev, [name]: value }));
  };

  const handleSubmit = async () => {
    const _errors = Validate(USER_SCHEMA, data);
    setErrors(_errors);

    if (selectedOptions.length === 0) {
      setAutocompleteError(true); // Set error if no items are selected
    } else {
      setAutocompleteError(false);
    }

    if (_errors.size || selectedOptions.length === 0) return;
    await onSubmit();
    return;
  };

  // const isCreateButtonDisabled = selectedOptions.length === 0; // Disable button if no items are selected

  return (
    <Modal
      open={open}
      onClose={onClose}
      width="min(700px,90vw)"
      hideCloseIcon={crossIcon}
      wrapperClasses="px-6"
    >
      <h3 className="opacity-80">{edit === true ? t('editTeam') : t('addTeam')} </h3>
      <Stack>
      <SchemaExtractor
        data={data}
        errors={errors}
        onChange={handleChange}
        schema={USER_SCHEMA}kk
      />
      <Stack sx={{width:'100%',flexDirection:'row',position:'relative',py:1}}>
      <Typography sx={{width:'150px'}}>{t('supervisors')}</Typography>
      <Autocomplete
        sx={{ width: '510px' }}
        multiple
        options={options.filter(option => !selectedOptions.some(selected => selected.id === option.id))}
        getOptionLabel={(option) => option.label || ""}
        value={selectedOptions}
        onChange={(event, newValue) => {
          setSelectedOptions(newValue);
          setAutocompleteError(false); // Clear error on selection
        }}
        filterSelectedOptions
        renderInput={(params) => (
          <TextField
          sx={{borderRadius:6,borderColor:'#eee',borderWidth:1, minHeight: 40, maxHeight: 'auto' }}
            {...params}
            onChange={(e) => {
              const inputValue = e.target.value;
              if (inputValue.trim()) {
                fetchUser(inputValue);
              }
            }}
            placeholder={t('searchSupervisor')}
            error={autocompleteError} // Show error state
            helperText={autocompleteError ? t('this Field is required') : ""} // Display error message
          />
        )}
        renderTags={(value, getTagProps) =>
          value.map((option, index) => (
            <Chip
              // variant="outlined"
              label={option.label}
              {...getTagProps({ index })}
              sx={{
                height: "24px",
                fontSize: "0.75rem",
                margin: "1px",
                "& .css-i4bv87-MuiSvgIcon-root": {
                  border: "none!important",
                },
              }}
            />
          ))
        }
      />
      </Stack>
      </Stack>
     
      <Stack
        direction="row"
        alignItems="center"
        justifyContent="space-between"
        mt={4}
        mb={1}
      >
        <Button bordered gray onClick={onClose}>
          {t('cancel')}
        </Button>
        <Button color={'light_green'} onClick={handleSubmit} loading={loading}>
          <Typography className="fw-20-500-24">{t('create')}</Typography>
        </Button>
      </Stack>
    </Modal>
  );
};

export default Add;
