import React, { useEffect, useState } from "react";
import { useLocation, useNavigate } from "react-router-dom";
import { useDispatch } from "react-redux";
import Table from "../../components/table";
import useTeamDashboard from "../../store/teamDashboard";
import useUser from "../../store/users";
import Button from "../../components/button";
import Add from "./Add";
import SchemaExtractor from "../../components/UI/SchemaExtractor";
import { SCHEMA_TYPES, VALIDATORS, } from '../../helpers/constants'
import { Validate } from "../../helpers/utils";
import { toast } from "react-toastify";
import { IconButton, Stack, Typography, Grid, Tab, Tabs, } from "@mui/material";
import { Delete, Checklist } from "@mui/icons-material";
import JsonFormatter from "../../components/UI/JsonFormatter";
import AdminPanelSettingsIcon from '@mui/icons-material/AdminPanelSettings';
import EditIcon from '@mui/icons-material/Edit';
import ExpandMoreIcon from '@mui/icons-material/ExpandMore';
import KeyboardArrowRightIcon from '@mui/icons-material/KeyboardArrowRight';
import AddCircleIcon from '@mui/icons-material/AddCircle';
import Category from "../../components/UI/Category";
import { COLORS } from "../../helpers/constants";
import { t } from "i18next";
import UserManage from "./UserManagement";
import { setTeamItem } from '../../store/teamMaster/team'
import AddUserReport from './AddUserReport'
import TestPortal from "./TestPortal";

import moment from "moment";

const TeamDashboard = () => {
  const navigate = useNavigate()
  const dispatch = useDispatch()
  const [users, setUsers] = useState([]);
  const [loading, setLoading] = useState();
  const [total, setTotal] = useState(0);
  const [edit, setEdit] = useState(false)
  const [modalOpen, setModalOpen] = useState(false);
  const [page, setPage] = useState(0);
  const [data, setData] = useState({
    id: '',
    area_name: '',
    description: '',
    supervisor: []
  });
  const [tab, setTab] = useState(0)
  const [perData, setPerData] = useState({
    opId: ''
  })
  const [assets, setAssets] = useState([])
  const { list, create, update, remove, assetList, areaList, performanceList } = useTeamDashboard();
  const { superUsers, update: updateStatus } = useUser()
  const [errors, setErrors] = useState(new Map());

  const [superVisor, setSuperVisor] = useState([])
  const [rowItem, setRowItem] = useState('')
  const [tableData, setTableData] = useState([])
  const [performanceData, setPerformanceData] = useState([])
 const [ TestData,setTestData] = useState({})

  useEffect(() => {
    fetchCompany();
  }, [page]);

  // useEffect(() => {
  //   fetchAssets();
  // }, []);

  const fetchCompany = async () => {
    setLoading("fetch");
    const [_list] = await areaList(page + 1);
    console.log("area list", _list)
    setUsers(_list);
    setData((prevData) => ({ ...prevData, stations: _list[0]?.id || '' })); // Preserve previous state and handle undefined case
    setTableData(_list[0]);

    const newUsers = _list.map(item => ({
      value: item.name,
      key: item.id
    }));
    setSuperVisor(newUsers);
    // setTotal(_list.count);
    setLoading(false);
  };

  const fetchPerformance = async (aId) => {
    console.log("aId", data)
    setLoading("fetch");
    const [_list] = await performanceList(aId || data.stations);
    console.log("performance list", _list)
    setPerformanceData(_list);
    setLoading(false);
  }

  const fetchAssets = async () => {
    setLoading("fetch");
    const [_list] = await superUsers(page + 1);
    console.log("assets linst", _list)
    setPerformanceData(_list);
    // const newUsers = _list.results.map(item => ({
    //   value: item.username,
    //   key: item.id
    // }));
    // setSuperVisor(prevUsers => [...prevUsers, ...newUsers]);

  };

  const handleAdd = async () => {
    const payload = { ...data }
    const req = {
      name: payload.area_name,
      description: payload.description,
      supervisors: payload.supervisor
    }
    setLoading("fetch");
    let res = await create(req);
    setData({});
    setModalOpen(false);
    await fetchCompany();
    console.log("creat res----", res)
  };

  const handleDelete = async (id) => {
    setLoading("fetch");
    await remove(id);
    setPage(0)
    await fetchCompany();
  };

  const handleUpdate = async (id, status) => {
    setLoading("fetch");
    const payload = {
      "status": status === true ? false : true
    }
    await updateStatus(id, payload);
    await fetchCompany();
    setLoading(false);
  };

  const SCHEMA = [{
    placeholder: t('selectArea'),
    type: SCHEMA_TYPES.SELECT,
    name: "stations",
    validator: VALIDATORS.REQUIRED,
    keyValues: superVisor
  }]

  const COLUMNS = [
    {
      id: 1,
      title: t('user'),
      render: (rowData) => <Typography sx={{ cursor: 'pointer', textTransform: 'capitalize' }} >{rowData.username}</Typography>,
    },
    {
      id: 2,
      title: t('fullname'),
      render: (rowData) => <Typography >{rowData.fullname}</Typography>
    },
    // {
    //   id: 4,
    //   title: t('superVisor'),
    //   render: (rowData) => (
    //     <Stack>
    //       {rowData.supervisors.map((item) => (
    //         <Typography key={item.id}>{item.username}</Typography>
    //       ))}
    //     </Stack>
    //   ),
    // },
    {
      id: 5,
      title: t("action"),
      render: (rowData) => (
        <Stack flexDirection={'row'} gap={2}>
          {/* <IconButton
            sx={{ height: 25, width: 25 }}
            onClick={() => { SetHandleEdit(rowData) }}
          >
            <EditIcon color="primary" fontSize="small" />
          </IconButton> */}
          <Button width={"12%"} color={rowData.status ? "light_green":"RADIAL_RED"}
            onClick={() => { handleUpdate(rowData.id, rowData.status) }}
          >
            {rowData.status ? t('active') : t('inactive')}
          </Button>
          <Button color={"light_green"}
            onClick={() => { 
              setTestData((prevData) => ({
              ...prevData,
              operator:rowData.id
            }));
             setModalOpen('testPortal')

            }}
          >
            { t('testPortal')}
          </Button>

          {/* <IconButton
            sx={{ height: 25, width: 25 }}
            onClick={() => {setModalOpen('addReport')}}
          >
            <Delete color="error" fontSize="small" />
          </IconButton> */}
        </Stack>
      ),
    },
  ];
  const PERFORMANCE = [
    {
      id: 1,
      title: t('user'),
      render: (rowData) => <Typography sx={{ cursor: 'pointer', textTransform: 'capitalize' }} >{rowData.user}</Typography>,
    },
    {
      id: 2,
      title: t('skill'),
      render: (rowData) => <Typography >{rowData.skill.skill_name}</Typography>
    },
    {
      id: 3,
      title: t('skillLevel'),
      render: (rowData) => <Typography >{rowData.skill_level}</Typography>
    },
    {
      id: 4,
      title: t('performance'),
      render: (rowData) => (
        rowData.entries ? (
          <Stack>
            {Object.entries(rowData.entries).map(([date, entry]) => (
              <Typography key={date}>
                {`${moment(date).format('ll')} Plan: ${entry.plan}, Actual: ${entry.actual}, Rejected: ${entry.rejected}`}
              </Typography>
            ))}
          </Stack>
        ) : null
      ),
    },
    {
      id: 5,
      title: t("action"),
      render: (rowData) => (
        <Stack flexDirection={'row'} gap={2}>
          {/* <IconButton
            sx={{ height: 25, width: 25 }}
            onClick={() => { SetHandleEdit(rowData) }}
          >
            <EditIcon color="primary" fontSize="small" />
          </IconButton> */}
          <Button color={"light_green"}
            onClick={() => {
              setPerData((prevData) => ({
                ...prevData,
                opId: rowData.id,
                skill_level: rowData.skill_level,
                skill: rowData.skill.skill_name,
                operator: rowData.user,
                skill_id: rowData.skill.id
              })); 
            if(rowData.status===true){setModalOpen('addReport')} else {toast.warn("Performance already submitted")} 
            }}

          >
            Add Performance
          </Button>


        </Stack>
      ),
    },
  ];


  const SetHandleEdit = (rowData) => {
    console.log("defect rowdata", rowData);

    const { id, name, description, supervisors } = rowData;
    setData(prevData => ({
      ...prevData,
      id,
      area_name: name,
      description,
      supervisor: supervisors.map(item => item.id),
    }));
    setEdit(true);
    setModalOpen("add");
    console.log("----------rowData", data);
  }

  const handleEdit = async () => {
    const payload = { ...data }
    console.log("payload", payload)
    const req = {
      name: payload.area_name,
      description: payload.description,
      supervisors: payload.supervisor
    }
    console.log("creat res----", req)
    setLoading("fetch");
    let res = await update(data.id, req);
    setPage(0)
    setData({});
    setModalOpen(false);
    await fetchCompany();
  };



  const handleChange = (e) => {
    setTableData(users.filter(item => item.id === e.target.value)[0])
    // console.log("e.target handleChange", e)

    const { name, value } = e.target;
    setErrors((prev) => {
      prev.delete(name);
      return prev;
    });
    setData((prev) => ({ ...prev, [name]: value }));
    if (tab === 1) {
      fetchPerformance(value)
    }
    console.log("data", data)
  };


  return (
    <div className="mx-3 my-2">
      <div className="flex items-center justify-between">
        <Typography className="fw-20-500-36">
          {t("teamDashboard")}
        </Typography>
        <Stack sx={{ mb: 1 }}>
          <SchemaExtractor
            data={data}
            errors={errors}
            onChange={handleChange}
            schema={SCHEMA}
          />
        </Stack>
      </div>
      <Tabs value={tab} onChange={(_e, v) => { setTab(v); setPage(0); if (v === 1) { fetchPerformance() } }} className="mb-2 fw-500">
        {[t('operatorList'), t('operatorsPerformance')].map((label, index) => (
          <Tab label={label} className="fw-500" value={index} />
        ))}
      </Tabs>

      {tab === 0 ? (
        <Table
          columns={COLUMNS}
          rows={tableData?.users}
          rowsPerPage={10}
          loading={loading === "fetch"}
          titleCellClasses="c-gray fw-700"
          page={page}
          setPage={setPage}
          // paginator
          serialize
          total={total}
        />
      ) : (
        <Table
          columns={PERFORMANCE}
          rows={performanceData}
          rowsPerPage={10}
          loading={loading === "fetch"}
          titleCellClasses="c-gray fw-700"
          page={page}
          setPage={setPage}
          // paginator
          serialize
          total={total}
        />

      )}
      <Add
        open={modalOpen === "add"}
        onClose={() => { setModalOpen(false); setEdit(false); setData({}); }}
        data={data}
        edit={edit}
        setData={setData}
        onSubmit={edit === true ? handleEdit : handleAdd}
        assetsList={superVisor}
      />
      <UserManage
        open={modalOpen === "UserManage"}
        onClose={() => { setModalOpen(false); setEdit(false); setData({}); }}
        data={data}
        edit={edit}
        setData={setData}
        onSubmit={edit === true ? handleEdit : handleAdd}
        assetsList={superVisor}
        rowItem={rowItem}
        modalOpen={modalOpen}
      />
      <AddUserReport
        open={modalOpen === "addReport"}
        onClose={() => { setModalOpen(false) }}
        onSubmit={fetchPerformance}
        data={perData}
        setData={setPerData}
      // onSubmit2={uploadPerformance}
      />
      <TestPortal
        open={modalOpen === "testPortal"}
        onClose={() => { setModalOpen(false) }}
        // onSubmit={fetchPerformance}
        TestData={TestData}
        setTestData={setTestData}
        />
    </div>
  );
};

export default TeamDashboard;
