import "./styles.css";
import { useLocation, useNavigate, useSearchParams } from "react-router-dom";
import React, { useState, useEffect } from "react";
import Table from "../../../components/table";
import TableComponent from "../components/Table";
// components
import Button from "../../../components/button";
import Breadcrumb from "../../../components/breadcrumb";
import { IconButton, Stack ,Card, CardContent, Typography, Container} from "@mui/material";
import actionIcon from "../../../assets/svg/action-icon.svg";
import ArrowBackIcon from '@mui/icons-material/ArrowBack';
import { COLORS } from "../../../helpers/constants";
// import { useBOM } from "../../../store/materialPlanner";
import useMaterialPlanning from "../../../store/planningMaterial"
import Add from "./Add";
import SchemaExtractor from "../../../components/UI/SchemaExtractor";
import { SCHEMA_TYPES, VALIDATORS, } from '../../../helpers/constants'
import { Validate } from "../../../helpers/utils";
import { t } from "i18next";
import ShowStages from "./showStages";
import AssignStages from './assignStages'


function PlanningLpcMaterialOrder() {
  const navigate = useNavigate();
  const locaiton = useLocation()
  const locationData = locaiton?.state
  // const [searchParams] = useSearchParams();
  const [tableData, setTableData] = useState([]);
  const { orderTasks ,orderAssetList,machineAssign,orderAssetLPCList} = useMaterialPlanning();
  const [loading, setLoading] = useState();
  const [total, setTotal] = useState(0);
  const [page, setPage] = useState(0);
    const [modalOpen, setModalOpen] = useState(false);
  // const [edit, setEdit] = useState(false); 
  const [data, setData] = useState({
  });
   const [assets, setAssets] = useState()
   const [errors, setErrors] = useState(new Map());

   const [stageData,setStageData] = useState([])
   const [name,setName] = useState('')

  useEffect(() => {
    fetchCircuit();
    // fetchAssestList()
  }, [])

  async function fetchCircuit() {
    let [res] = await orderTasks(page, locationData?.id)
    console.log("circuit data----", page, locationData)
    setTableData(res.circuits)
    res.circuits.map((item)=>{
      // setData({...data,[item.map_id]:item.assigned_asset})
      // handleChange(item.map_id,'machine',item.assigned_asset)
      setData((prev) => ({
        ...prev,
        [item.map_id]: { 
          ...prev[item.map_id], 
          'machine': item.assigned_asset 
        }
      }));
    })
  }
  // async function fetchCircuit() {
  //   let [res] = await orderTasks(page, locationData?.id);
  //   console.log("circuit data----", page, locationData);
  //   setTableData(res.circuits);
  
  //   setData((prevData) => {
  //     const newData = { ...prevData };
  //     res.circuits.forEach((item) => {
  //       newData[item.map_id] = item.assigned_asset;
  //     });
  //     return newData;
  //   });
  // }



  const USER_SCHEMA = [
      {
        // title: "Machines",
        placeholder: "Select Machine",
        type: SCHEMA_TYPES.SELECT,
        name: "machine",
        // multi:true,
        validator: VALIDATORS.REQUIRED,
        keyValues: assets,
        width:'200px'
      }]

  const COLUMNS = [
    // {
    //   id: 1,
    //   title: "Circuit Code",
    //   render: (rowData) => rowData.circuit_information?.circuit_code ||'-',
    // },
    {
      id: 2,
      title: t("CircuitName"),
      render: (rowData) => <Typography sx={{textTransform:'capitalize'}}>{rowData?.name||'-'}</Typography>,
    },
    {
      id: 3,
      title: t("qty"),
      render: (rowData) => rowData.quantity,
    },
    {
      id: 4,
      title: t("action"),
      render: (rowData) => (
        // <Stack flexDirection={'row'} key={rowData.id}>
        //   <SchemaExtractor
        //   // data={data}
        //   data={data[rowData.map_id] || {}}
        //   errors={errors}
        //   onChange={(e) =>handleChange(rowData.map_id, e.target.name, e.target.value)}
        //   schema={USER_SCHEMA}
        // />
        <img
          src={actionIcon}
          style={{
            width: "2.78vw",
            height: "2.78vw",
            cursor: "pointer",
          }}
          alt="Action Icon"
          onClick={()=>handleOpenModal(rowData)}
          />
     
      ),
    }
  ];



  async function fetchAssestList(stage_type) {
    let [res] = await orderAssetLPCList(1,locationData?.id,stage_type)
    console.log("asset list-------",res)
    const newList = res.map(item => ({
      value: item.name,
      key: item.id
    }));
    // setAssets(newList)
    return newList
  }

  const handleOpenModal = async (rowData) => {
    setName(rowData.name)
    let map_id = rowData.map_id
    let newData = await Promise.all(
      rowData.stages.map(async (item) => {
        let asset_list = await fetchAssestList(item.stage_name);
        return { ...item, asset_list,map_id }; // Adding `res` to each item
      })
    );
    setStageData(newData)
    console.log("new rowData------",newData); // You can use newData as needed
    setModalOpen("AssignStages")
  };
  

  // const handleChange = (e) => {
  //   const { name, value } = e.target;
  //   setErrors((prev) => {
  //     prev.delete(name);
  //     return prev;
  //   });
  //   setData((prev) => ({ ...prev, [name]: value }));
  // };
  const handleChange = (id, name, value) => {
    console.log("-->","map id",id,"name---",name,"value----",value)
    setErrors((prev) => {
      prev.delete(`${id}-${name}`);
      return new Map(prev);
    });
  
    setData((prev) => ({
      ...prev,
      [id]: { 
        ...prev[id], 
        [name]: value 
      }
    }));
    console.log("machine assignment res--------",data)

    circuitAssign(id,value)
  };

  async function circuitAssign(id,value) {
    let payload ={
      order_type:locationData.order_type,
      asset_id:[value]
    }
    let res = await machineAssign(id,payload)
   
  }

  return (
    <div className="mx-3 my-2">
      <div style={{ display: 'flex', flexDirection: 'row', gap: 2 ,alignItems:'center'}}>
        <IconButton onClick={() => navigate(-1)}>
          <ArrowBackIcon />
        </IconButton>
        <p className="instruction-text">
          {locationData.name} / {locationData.order_id} / {locationData.order_type} / {locationData.running_status}
        </p>
      </div>
      <Table
        columns={COLUMNS}
        rows={tableData}
        rowsPerPage={10}
        loading={loading === "fetch"}
        titleCellClasses="c-gray fw-700"
        page={page}
        setPage={setPage}
        // paginator
        serialize
        total={10}
        tableBodyColor={COLORS.WHITE}
      />

      <AssignStages
       tableData= {stageData}
       open={modalOpen === "AssignStages"}
       onClose={() => { setModalOpen(false)}}
       data={data}
       name={name}
       // edit={edit}
       setData={setData}
      />

       <Add
        open={modalOpen === "add"}
        onClose={() => { setModalOpen(false)}}
        data={data}
        // edit={edit}
        setData={setData}
        // onSubmit={handleAdd}
        assetsList={assets}
      />
      {/* <Button onClick={()=>setModalOpen('showStages')}>
      show Stages
      </Button> */}

      {/* Footer   */}
      {/* <div className="material-planner-layout-footer">
        <Button
          onClick={() => navigate("/planning-material")}
          white={true}
          width="8rem"
          height="2.5rem"
          bordered={true}
          customStyle={{
            border: "1px solid #3E3E3E",
            backgroundColor: "#FFFFFF",
          }}
        >
          Back
        </Button>
        <Button
          // onClick={handleSubmit}
          color="green"
          width="8rem"
          height="2.5rem"
        >
          Plan
        </Button>
      </div> */}
    </div>
  );
}

export default PlanningLpcMaterialOrder;
