import React, { useEffect, useState } from "react";
import { motion } from "framer-motion";
import Table from "../../../components/table";

import Modal from '../../../components/modal'
// import Input from "../../components/input";
import { Stack, Typography, Card, CardContent, Container } from "@mui/material";
import Button from "../../../components/button";
import SchemaExtractor from "../../../components/UI/SchemaExtractor";
import { COLORS, SCHEMA_TYPES, VALIDATORS, } from '../../../helpers/constants'
import { Validate } from "../../../helpers/utils";
// import JsonCreator from "../../components/UI/JsonCreator";
// import RadioButtonGroup from "../../components/input/radio";
import crossIcon from '../../../assets/svg/cross-icon.svg'
import { t } from "i18next";
import useMaterialPlanning from "../../../store/planningMaterial"
import arrowDown from "../../../assets/svg/downArrow.svg"


const AssignStages = ({ open, onClose, onSubmit, loading, edit, assetsList, tableData ,name}) => {

  const { orderAssetList ,machineAssign} = useMaterialPlanning();
  const [data, setData] = useState({
  });

  useEffect(() => {
    console.log("data-tableData-", tableData)
    tableData.map((item)=>{
      // setData({...data,[item.map_id]:item.assigned_asset})
      // handleChange(item.map_id,'machine',item.assigned_asset)
      setData((prev) => ({
        ...prev,
        [item.stage_id]: { 
          ...prev[item.stage_id], 
          'machine': item.assigned_asset 
        }
      }));
    })
  }, [tableData])



  const USER_SCHEMA = [
    // {
    //   title: "Name",
    //   placeholder: "Enter Name",
    //   type: SCHEMA_TYPES.INPUT,
    //   name: "name",
    //   validator: VALIDATORS.REQUIRED,
    // },
    {
      // title: "Machines",
      placeholder: "Select Machine",
      type: SCHEMA_TYPES.SELECT,
      name: "machine",
      // multi:true,
      validator: VALIDATORS.REQUIRED,
      keyValues: assetsList
    },
    // {
    //   title: "Threshold (seconds)",
    //   placeholder: "Enter Name",
    //   type: SCHEMA_TYPES.INPUT,
    //   name: "timeLimit",
    //   validator: VALIDATORS.REQUIRED,
    // },
    // {
    //   title:"Type",
    //   type: SCHEMA_TYPES.RADIO,
    //   name:"type",
    //   keyValues:[{"value":false,"label":"Productive"},{"value":true,"label":"Non-Productive"}]
    // },


  ];
  const [errors, setErrors] = useState(new Map());
  const COLUMNS = [
    // {
    //   id: 1,
    //   title: "Circuit Code",
    //   render: (rowData) => rowData.circuit_information?.circuit_code ||'-',
    // },
    // {
    //   id: 2,
    //   title: "Circuit Name",
    //   render: (rowData) => rowData.circuit_information?.name||'-',
    // },
    {
      id: 3,
      title: t("StageName"),
      render: (rowData) => rowData.stage_name,
    },
    {
      id: 4,
      title:  t("SelectMachine"),
      render: (rowData) => (
        <Stack flexDirection={'row'} key={rowData.id}>
          <SchemaExtractor
            data={data[rowData.stage_id] || {}}
            // data={rowData.asset_list}
            errors={errors}
            onChange={(e) => handleChange(rowData.map_id, rowData.stage_id, e.target.name, e.target.value)}
            schema={[{
              // title: "Machines",
              placeholder:  t("SelectMachine"),
              type: SCHEMA_TYPES.SELECT,
              name: "machine",
              // multi:true,
              validator: VALIDATORS.REQUIRED,
              keyValues: rowData.asset_list
            },]}
          />
        </Stack>
      ),
    }
  ];

  const handleChange = (map_id, stage_id, name, value) => {
    console.log("-->", "map id:", map_id, "stage--id:", stage_id, "name:", name, "value:", value);
  
    // Correctly updating the `errors` state by creating a new Map instance
    setErrors((prev) => {
      const newErrors = new Map(prev);
      newErrors.delete(`${stage_id}-${name}`);
      return newErrors;
    });
  
    // Correctly updating `data` without directly mutating previous state
    setData((prev) => {
      const updatedData = {
        ...prev,
        [stage_id]: {
          ...prev[stage_id],
          [name]: value
        }
      };
  
      console.log("Updated machine assignment res:", updatedData); // Correctly logs the updated state
      return updatedData;
    });
    if(value ==='' || value === undefined){
      return
    }
    // Optionally call circuitAssign if needed
    circuitAssign(map_id, stage_id, value);
  };
  
  async function circuitAssign(map_id, stage_id,value) {
    let payload ={
      order_type:'LPC',
      asset_id:[value],
      stage_id
    }
    let res = await machineAssign(stage_id,payload,stage_id)
  }

  const handleSubmit = async () => {
    const _errors = Validate(USER_SCHEMA, data);
    setErrors(_errors);
    if (_errors.size) return;
    console.log("handleSubmit----------", data.reason)
    await onSubmit()
    return;
  }
  return (
    <Modal
      open={open}
      onClose={onClose}
      width="min(800px,90vw)"
      hideCloseIcon={crossIcon}
      wrapperClasses="px-6"
    >
      <h3 className="opacity-80">{name} &gt; {t('CircuitStages')}</h3>
    <Stack mb={2}>
      <Table
        columns={COLUMNS}
        rows={tableData}
        // rowsPerPage={10}
        loading={loading === "fetch"}
        titleCellClasses="c-gray fw-700"
        // page={page}
        // setPage={setPage}
        // paginator
        serialize
        // total={10}
        tableBodyColor={COLORS.WHITE}
      />
      </Stack>
      {/* <Stack
        direction="row"
        alignItems="center"
        justifyContent="space-between"
        mt={4}
        mb={1}
      >
        <Button bordered gray onClick={onClose}>
          {t('cancel')}
        </Button>
        <Button color={'light_green'} onClick={handleSubmit} loading={loading}>
          <Typography className="fw-20-500-24">{t('submit')}</Typography>
        </Button>
      </Stack> */}
    </Modal>
  );
};

export default AssignStages;
