import React,{useState,useEffect, useLayoutEffect, use} from "react";
import { useSelector } from "react-redux";
import Modal from "../../components/modal";
import { Stack, Typography } from "@mui/material";
import CancelIcon from '@mui/icons-material/Cancel';
import AddCircleIcon from '@mui/icons-material/AddCircle';
import Button from "../../components/button";
import useMachineGroup from "../../store/group/machine";
import useOperatorGroup from "../../store/group/operator";
import useUserGroup from "../../store/group/user";
import {COLORS, SCHEMA_TYPES,VALIDATORS,} from '../../helpers/constants'
import { REDUCER_TYPES } from "../../store";
import { setToken } from "../../store/api/utils";
import { useTranslation } from 'react-i18next';
import useAuth from "../../store/auth";
import crossIcon from '../../assets/svg/cross-icon.svg'
import { toast } from "react-toastify";
import { t } from "i18next";
import useTeam from "../../store/teamMaster";
import useUser from "../../store/users";


const UserManage = ({ open, onClose, data, setData, onSubmit,modalOpen, loading,edit,tab,rowItem}) => {
  const { t, i18n } = useTranslation();
  const {getMenu,postMenu,getMachines,postMachine} = useAuth()
  const { list, create, update, remove, assetList,getDetail } = useTeam();
  
  const { listByAadhar2,listByAadhar } = useUser()


  const userData = useSelector((state) =>state?.TEAM_ITEM?.data)
  const [userMenu,setUserMenu] = useState([])
  const [allMenu,setAllMenu] = useState([])

  useEffect(() => {
    const fetchMenu = async () => {
        console.log("userData---", userData?.id);
        setUserMenu(userData?.users || []);
        console.log("getMachines item---getDetail-->>>", userData?.users);
    };

    if (modalOpen === "UserManage" && userData?.id) {
        fetchMenu();
    }
  }, [modalOpen, userData?.id]);
 
  const handleAssest = async (vall) => {
    console.log("machine val", vall);
    let val = {
      id: vall.user.id,username:`op_${vall.aadhar_number}`}
  
    // Ensure val is an object with a unique identifier
    if (!userMenu.some((item) => item.id === val.id)) {
      setUserMenu((prevListA) => [...prevListA, val]);
    } else {
      toast.warn("Operator already in the list");
    }
  };

  const handleDelete = (itemToDelete) => {
    setUserMenu(prevItems => 
      prevItems.filter(item => item !== itemToDelete)
    );
  };

  const submitMenu=async ()=>{
    // console.log("menu items---",userMenu)
    let payload ={
      "name":userData?.name,
      "users":userMenu.map((item)=>item.id)
    }
    let res = await update(userData?.id,payload)
    console.log("res----menu items",payload)
    await onClose()
  }

  const fetchOperator = async (name) => {
    let res = await listByAadhar(name);
    console.log("getLineManpowerlist res", res);

    setAllMenu(res);
    // setLoading(false);
  }

  return (
    <Modal
      open={open}
      onClose={onClose}
      width="min(700px,90vw)"
      hideCloseIcon={crossIcon}
      wrapperClasses="px-6" 
    >
      <h3 className="opacity-80" style={{textTransform:'capitalize'}}>{userData?.name} Team Users</h3>
      <Stack sx={{flexDirection:'row',justifyContent:'space-between',mb:1,gap:1,height:400}}>
        <Stack sx={{width:'49%',border:1,borderRadius:2,borderColor:COLORS.LG_GRAY_2,p:1,overflow:'scroll',scrollbarWidth:'none'}}>
          {/* <Typography sx={{px:1}}>{t('permissionsOfUser')}</Typography> */}
          {userMenu.map((item,index)=>{
           return( <Stack key={index} sx={{flexDirection:'row',backgroundColor:COLORS.GREEN_OPAQUE,mt:0.5,borderRadius:2,width:'fit-content',px:0.5}}>
              <Typography variant="h6" key={item.name} sx={{textTransform:'capitalize'}}>{index+1}. {item.username}</Typography>
              <CancelIcon fontSize="small" sx={{color:COLORS.DARK_RED,m:0.2,cursor:'pointer'}} onClick={()=>handleDelete(item)}/>
            </Stack>
           )
          })}
        </Stack>
        <Stack sx={{width:'49%',border:1,borderRadius:2,borderColor:COLORS.LG_GRAY_2,p:1,overflow:'scroll',scrollbarWidth:'none'}}>
          {/* <Typography sx={{px:1}}>{t('allUsers')}</Typography> */}
          <input
          placeholder="Search by Aadhar Number"
          style={{borderRadius:5,borderColor:COLORS.LG_GRAY_2,borderWidth:1,padding:10,marginBottom:5}}
          onChange={(e)=>fetchOperator(e.target.value)}/>
          {allMenu.map((item,index)=>{
           return( <Stack key={index} sx={{flexDirection:'row',backgroundColor:COLORS.GREEN_OPAQUE,mt:0.5,borderRadius:2,width:'fit-content',px:0.5}}>
              <Typography variant="h6" key={item.name} sx={{textTransform:'capitalize'}}>{index+1}. op_{item.aadhar_number}</Typography>
              <AddCircleIcon fontSize="small" sx={{color:COLORS.DARK_GREEN,m:0.2,cursor:'pointer'}} onClick={()=>handleAssest(item)}/>
            </Stack>
           )
          })}
        </Stack>
      </Stack>
      <Stack
        direction="row"
        alignItems="center"
        justifyContent="space-between"
        mt={1}
        mb={1}
      >
        <Button bordered gray onClick={onClose}>
          {t('cancel')}
        </Button>
        <Button color={'light_green'} onClick={()=>submitMenu()} loading={loading}>
        <Typography className="fw-20-500-24">
          {t('submit')}
          </Typography>
        </Button>
      </Stack>
    </Modal>
  );
};

export default UserManage;
