import React, { useEffect, useState } from "react";
import Modal from "../../components/modal";
import Input from "../../components/input";
import { Stack, Typography } from "@mui/material";
import Button from "../../components/button";
import SchemaExtractor from "../../components/UI/SchemaExtractor";
import { SCHEMA_TYPES, VALIDATORS, } from '../../helpers/constants'
import { Validate } from "../../helpers/utils";
import useMatrix from "../../store/skillmatrix";
import { toast } from "react-toastify";
import crossIcon from '../../assets/svg/cross-icon.svg'
import { useTranslation } from 'react-i18next';
import { t } from "i18next";
import useUser from "../../store/users";

const AddUserReport = ({ open, onClose, data, setData, onSubmit, onSubmit2, loading,opid}) => {

  const [errors, setErrors] = useState(new Map());
  const [step, setStep] = useState(0);
  const [completed, setCompleted] = useState(false)
  const { getPerformanceSkill, getPerformanceId ,putPerformanceRecord} = useMatrix()
  const { listByAadharFortestPortal } = useUser()

  async function fetchByAd(val) {
    let res = await listByAadharFortestPortal(val || '')
    return res
  }

  useEffect(() => {
    if (open) {
      // fetchUserProfile(data.opId)
      console.log("data.opId", data.opId)
    }
  },[open, data.opId])
 

  function isValidHexFormat(str) {
    return /^[a-f0-9]{32}$/i.test(str);
  }

  const uploadPerformance=async ()=>{
    var formattedDate = data.plan_date.replace(/-/g, '');
     const payload ={
       "new_entry":{[formattedDate]:{"plan":data.plan, "actual":data.actual,"rejected":data.rejected}}
     }
     await putPerformanceRecord(data.opId,payload)
    //  setModalOpen(false)
     setData({...data,   
       rejected:'',
       actual:'',
       plan:'',
       plan_date:'',
       skill_level:'',
       skill:'',
       operator:'',
       plan_date:new Date().toISOString().split('T')[0]
     })
      // toast.success("Performance updated successfully")
      await onClose()
   }

  const handleChange = async (e) => {
    const { name, value } = e.target;
    setErrors((prev) => {
      prev.delete(name);
      return prev;
    });
    setData((prev) => ({ ...prev, [name]: value }));
    // if (name === 'operator') {
    //   console.log("operator id ", value)
    //   await fetchByAd(value.value)
    //   if (isValidHexFormat(value.key)) {
    //     console.log("operator id value ", value.value)
    //     // fetchUserProfile(value.key)
    //   }
    //   // fetchUserProfile(value)
    // }
  };

  const handleNext = async () => {
      const _errors = Validate(SKILL, data);
      setErrors(_errors);
      if (_errors.size) return;
      await uploadPerformance()
  
  };



  const SKILL = [
      //   {
      //   title: t('operator'),
      //   placeholder: t('selectOperator'),
      //   type: SCHEMA_TYPES.SELECT,
      //   name: 'operator',
      //   validator: VALIDATORS.REQUIRED,
      //   keyValues:users,
      // }
      {
        title: t('operator'),
        type: SCHEMA_TYPES.INPUT,
        name: "operator",
        // validator: VALIDATORS.REQUIRED,
        disabled: true

      }
    ,
{
      title: t('skill'),
      placeholder: t('selectSkill'),
      type: SCHEMA_TYPES.INPUT,
      name: 'skill',
      disabled: true
      // keyValues:skills,
      // inputType:'number',
      // validator: VALIDATORS.REQUIRED,
    },
{
      title: t('skillLevel'),
      placeholder: t('selectSkillLevel'),
      type: SCHEMA_TYPES.INPUT,
      name: 'skill_level',
      disabled: true
      // validator: VALIDATORS.REQUIRED,
      // keyValues:[{value:'Level 1',key:1},{value:"Level 2",key:2},{value:"Level 3",key:3}]
    },
    {
      title: t('date'),
      type: SCHEMA_TYPES.DATE,
      name: 'plan_date',
      validator: VALIDATORS.REQUIRED,
      // keyValues:users,
    },
    {
      title: t('plan'),
      placeholder: "",
      type: SCHEMA_TYPES.INPUT,
      name: 'plan',
      inputType: 'number',
      validator: VALIDATORS.REQUIRED,
    },
    {
      title: t("actual"),
      placeholder: "",
      type: SCHEMA_TYPES.INPUT,
      inputType: 'number',
      name: 'actual',
      validator: VALIDATORS.REQUIRED,
    },
  {
      title: t('rejected'),
      placeholder: t('rejectedCount'),
      type: SCHEMA_TYPES.INPUT,
      inputType: 'number',
      name: 'rejected',
      validator: VALIDATORS.REQUIRED,
    },
  ];

  return (
    <Modal
      open={open}
      onClose={onClose}
      width="min(700px,90vw)"
      hideCloseIcon={crossIcon}
      wrapperClasses="px-6"
    >
      <h3 className="opacity-80">{t('addPerformance')}</h3>
      <SchemaExtractor
        data={data}
        errors={errors}
        onChange={handleChange}
        schema={SKILL}
      />
      <Stack
        direction="row"
        alignItems="center"
        justifyContent="space-between"
        mt={4}
        mb={1}
      >
        <Button bordered gray onClick={onClose}>
          {t('cancel')}
        </Button>

        <Button color={'light_green'} onClick={handleNext} loading={loading}>
          <Typography className="fw-20-500-24">
            {t('submit')}
          </Typography>
        </Button>
      </Stack>
    </Modal>
  );
};

export default AddUserReport;
