import React from "react";
import { resolveResponse } from "../api/utils";
import REQUEST, { CONTENT_TYPES } from "../api";
import { ENDPOINTS } from "../api/endpoints";
import { useSelector } from "react-redux";
import { REDUCER_TYPES } from "..";

const useRedBin = () => {
  const { token } = useSelector((state) => state[REDUCER_TYPES.AUTH]);

  const headers = {
    Authorization: `Bearer ${token}`,
    "Content-Type": CONTENT_TYPES.APPLICATION_JSON,
  };

  const list = async (qr_code) => {
    const client = new REQUEST();
    const res = await client.get(
      `settings/redbin/details/?qr_code=${qr_code}`,
      {},
      headers,
      resolveResponse
    );

    if (res?.data) {
      console.log("res  ",res.data)
      return [res?.data];
    }
    return [[], 0];
  };

  const masterlist = async (page,type) => {
    const client = new REQUEST();
    const res = await client.get(
      `redbin/master/?resolved=${type}&page=${page}&page_size=10`,
      {},
      headers,
      resolveResponse
    );

    if (res?.data) {
      console.log("res  ",res.data)
      return [res?.data];
    }
    return [[], 0];
  };

  const docklist = async (page,type) => {
    const client = new REQUEST();
    const res = await client.get(
      `machining/get-dock-info/`,
      {},
      headers,
      resolveResponse
    );

    if (res?.data) {
      console.log("res  ",res.data)
      return [res?.data];
    }
    return [[], 0];
  };

  const qr_details = async (payload = {}) => {
    const client = new REQUEST();
    const res = await client.post(
      `redbin/get-details/`,
      payload,
      headers,
      (res) => resolveResponse(res, null)
    );
    return res?.data;
  }

  const add_details = async (payload = {}) => {
    const client = new REQUEST();
    const res = await client.post(
      `redbin/add-details/`,
      payload,
      headers,
      (res) => resolveResponse(res, null)
    );
    return res?.data;
  };

  const send_qr_to_circuitboard = async (payload = {}) => {
    const client = new REQUEST();
    const res = await client.post(
      `machining/scan-at-cb/`,
      payload,
      headers,
      (res) => resolveResponse(res, null)
    );
    return res;
  };
  const dockStation = async (payload = {}) => {
    const client = new REQUEST();
    const res = await client.post(
      `machining/confirmation/`,
      payload,
      headers,
      (res) => resolveResponse(res, null)
    );
    return res;
  };

  const resolve_details = async (id,payload = {}) => {
    const client = new REQUEST();
    const res = await client.post(
      `redbin/resolution/`,
      payload,
      headers,
      (res) => resolveResponse(res, null)
    );
    return res?.data;
  };

  const create = async (payload = {},companyId) => {
    const client = new REQUEST();
    const res = await client.post(
      `${ENDPOINTS.ASSET}/${companyId}/${ENDPOINTS.PLANTS}/`,
      payload,
      headers,
      (res) => resolveResponse(res, null, "Plant created successfully!")
    );
    return res?.data;
  };

  const update = async (payload) => {
    const newPayload ={
      "name":payload.name,
      "metadata":payload.metadata
    }
    const client = new REQUEST();
    const res = await client.put(
      `${ENDPOINTS.ASSET}/${ENDPOINTS.PLANT}/${payload.id}/`,
      newPayload,
      headers,
      (res) => resolveResponse(res, null, "Plant Updated successfully!")
    );
    return res?.data;
  };

  const remove = async (id) => {
    const client = new REQUEST();
    const res = await client.delete(
      `${ENDPOINTS.ASSET}/${ENDPOINTS.PLANT}/${id}/`,
      headers,
      (res) => resolveResponse(res, null, "Plant deleted successfully!")
    );
    return res?.data;
  };

  return {
    list,
    create,
    qr_details,
    add_details,
    resolve_details,
    update,
    remove,
    masterlist,
    send_qr_to_circuitboard,
    dockStation,
    docklist
  };
};

export default useRedBin;
