import React, { useEffect, useState } from "react";
import Modal from "../../../components/modal";
import Input from "../../../components/input";
import { Stack, Typography } from "@mui/material";
import Button from "../../../components/button";
import SchemaExtractor from "../../../components/UI/SchemaExtractor";
import { SCHEMA_TYPES, VALIDATORS, } from '../../../helpers/constants'
import { Validate } from "../../../helpers/utils";
import JsonCreator from "../../../components/UI/JsonCreator";
import CheckCircleOutlineIcon from '@mui/icons-material/CheckCircleOutline';
import { COLORS } from "../../../helpers/constants";
import crossIcon from '../../../assets/svg/cross-icon.svg'
import { t } from "i18next";
import gearGif from '../../../assets/gif/gear.gif'
import useJob from "../../../store/operatorPortal/job";
import Downtime from "./Downtime";

import RadialBarChart from './gauge'
import { replace } from "lodash";
import { useLocation, useNavigate } from "react-router-dom";
import Table from "../../../components/table";


const LPCMachineData = ({ open, onClose, data }) => {
  const navigate = useNavigate()
  const location = useLocation()
  const locationData = location.state
  const [errors, setErrors] = useState(new Map());
  const [machineData, setMachineData] = useState({})
  const [machineId, setMachineId] = useState(localStorage.getItem('machineId') || "");
  const [modalOpen, setModalOpen] = useState(false);
  const [currentState,setCurrentState] = useState('')


  const { OrderMaterialList, create, updateOrderStatus, getMachineStatus,updateLPCOrderStatus,currentLPCOrderStatus } = useJob()


  useEffect(() => {
    console.warn("locationData----",locationData)
    MachineStatus()
    const interval = setInterval(() => {
      MachineStatus();
    }, 5000); // Runs every 10 seconds

    return () => clearInterval(interval); // Cleanup when `runningStatus` changes or component unmounts
  }, []);


  const MachineStatus = async () => {
    let res = await currentLPCOrderStatus(locationData)
    if (res) {
      console.log("getMachineStatus respose----", res.state)
      setCurrentState(res?.state)
      // if (res?.event_packet?.downtime_reason) {
      //   setModalOpen(false);
      // } else if (res.asset_state === "downtime") {
      //   setModalOpen("downtime");
      // }
      // setMachineData(res)
      // if (res.total === res.completed) {

      //   navigate('/job', { replace: true })
      // }
    }
  }
  const getColor = (state) => {
    switch (state) {
      case "offline":
        return "#FF0000"; // Red
      case "online":
        return "#27AE60"; // Green
      default:
        return "#FAA036"; // Orange (default case)
    }
  };

  async function UpdateStatus(params) {
    // RUNNING 
    let req = { "status": params }
    let res = await updateLPCOrderStatus(locationData, req)
    await MachineStatus()
    if(params==="COMPLETED"){
    setTimeout(() => {
      navigate('/job',{state:machineId,replace: true})
    }, 1000);
  }
  }


  return (
    <div className="mx-3 my-2">
      <div className=" items-center">
        <h1 className="mt-1">Order Status : {currentState}</h1>
      </div>
      <Stack sx={{ flexDirection: 'row', gap: 3, mb: 2 ,justifyContent:'center'}}>
        {currentState==='RUNNING'?(
        <Button color={'RADIAL_RED'} onClick={() => UpdateStatus('STOPPED')}>
          <Typography color={COLORS.WHITE}>
            Pause
          </Typography>
        </Button>
        ):(
        <Button color={'yellow'} onClick={() => UpdateStatus('RUNNING')}>
          <Typography color={COLORS.WHITE}>
            Resume
          </Typography>
        </Button>
        )}
        <Button color={'light_green'} onClick={() => UpdateStatus('COMPLETED')}>
          <Typography color={COLORS.WHITE}>
            Complete
          </Typography>
        </Button>
      </Stack>
      <Stack>
        <iframe
          src="/pdf/sample.pdf"
          width="100%"
          height="600px"
          style={{ border: "none" }}>
        </iframe>
        {/* <embed src={samplePDF} width="100%" height="600px" type="application/pdf" /> */}

      </Stack>
      {/* <Stack sx={{ alignItems: 'center', justifyContent: 'center' }}>
        <img src={gearGif} alt="uploading" style={{ width: 100, height: 100, objectFit: 'contain' }} />
      </Stack> */}
      {/* <Stack sx={{ flexDirection: 'row', justifyContent: 'space-between', mt: -5 }}>
        <Stack sx={{ justifyContent: 'center', gap: 1 }}>
          <Typography className="fw-24" sx={{}}>Circuit Name : <strong>{machineData?.circuit_name}</strong></Typography>
          <Typography className="fw-24" sx={{}}>Circuit Code : <strong>{machineData?.circuit_code}</strong></Typography>
          <Typography className="fw-24" sx={{ textTransform: 'capitalize' }} >Machine Status : <strong style={{ color: getColor(machineData?.asset_state) }}>{machineData?.asset_state}</strong></Typography>
          <Typography className="fw-24" sx={{}}>Total : <strong>{machineData?.total}</strong></Typography>
          <Typography className="fw-24" sx={{}}>Completed : <strong>{machineData?.completed}</strong></Typography>
        </Stack>
        <Stack>
          <RadialBarChart total={machineData?.total} completed={machineData?.completed || 500} />
        </Stack>
      </Stack> */}
      {/* <div>
      <table style={{ width: "100%" }}>
      <tr >
        {Object.entries(machineData?.circuit_metadata).map(([key, value]) => (
         <th style={{ padding: "8px", border: "1px solid black", fontWeight: "bold", background: "#f0f0f0" ,width:'10%'}}>
           {key.charAt(0).toUpperCase() + key.slice(1)}
         </th>
        ))}
      </tr>
      <tr >
        {Object.entries(machineData?.circuit_metadata).map(([key, value]) => (
            <th style={{ padding: "8px", border: "1px solid black" ,fontWeight:'normal',width:'10%'}}>{value}</th>
        ))}
      </tr>
    </table>
    </div> */}
      <Downtime
        open={modalOpen === "downtime"}
        onClose={() => { setModalOpen(false) }}
        // data={data}
        // edit={edit}
        // setData={setData}
        // onSubmit={handleAdd}
        packetData={machineData}
      />
    </div>

  );
};

export default LPCMachineData;
