import React, { useEffect, useLayoutEffect, useState, useRef } from "react";
import MTable from "@mui/material/Table";
import TableBody from "@mui/material/TableBody";
import TableCell, { tableCellClasses } from "@mui/material/TableCell";
import TableContainer from "@mui/material/TableContainer";
import TableHead from "@mui/material/TableHead";
import TableRow from "@mui/material/TableRow";
import PieChart from '../pie'
import { toast } from "react-toastify";
import { IconButton, Stack, Typography, Grid } from "@mui/material";
import { COLORS } from "../../helpers/constants";
import DhootLogo from '../../assets/png/DhootLogo.png'
import vvdnLogo from '../../assets/png/VVDNLogo.png'
import { useTranslation } from 'react-i18next';

let color_1 = [COLORS.L4, COLORS.L4, COLORS.L4, COLORS.L1]
let color_2 = [COLORS.L2, COLORS.L4, COLORS.L4, COLORS.L1]
let color_3 = [COLORS.L2, COLORS.L3, COLORS.L4, COLORS.L1]
let color_4 = [COLORS.L2, COLORS.L3, COLORS.L5, COLORS.L1]
let color_5 = [COLORS.L4, COLORS.L4, COLORS.L4, COLORS.L4]

const PrintPage = ({ row, skill }) => {
  const [processedSkills, setProcessedSkills] = useState([]);
  const workerRef = useRef(null);

  useEffect(() => {
    console.log("printIndexRef", row)

    // Initialize the worker
    workerRef.current = new Worker(new URL('../../workers/printSkillMatrixWorker.js', import.meta.url));

    // Post data to the worker
    workerRef.current.postMessage({
      row,
      skills: skill,
      colors: [color_1, color_2, color_3, color_4, color_5],
    });

    // Listen for the processed data from the worker
    workerRef.current.onmessage = (event) => {
      setProcessedSkills(event.data.processedSkills);
    };

    return () => {
      // Terminate the worker on component unmount
      if (workerRef.current) {
        workerRef.current.terminate();
      }
    };
  }, [row, skill]);

  return (
    <div className="mx-4" >
      <Stack sx={{ justifyContent: 'center', alignItems: 'center' }}>
        <img
          src={process.env.REACT_APP_BASE_LOGO === 'dhoot' ? DhootLogo : vvdnLogo}
          style={{ width: '230px', height: '75px', objectFit: 'contain' }}
        />
        <Stack sx={{ borderBottom: '2px solid #000', my: 2 }}>
          <Typography className="fw-36" sx={{}}>
            Operator Skill Matrix
          </Typography>
        </Stack>
      </Stack>
      <Stack sx={{ flexDirection: 'row', alignItems: 'center', justifyContent: 'space-between' }}>
        <Stack>
          <Typography>Operator Name : {row.fullname}</Typography>
          <Typography>Aadhaar Number : {row.aadhar}</Typography>
        </Stack>
        <Stack sx={{ flexDirection: 'row', gap: 2, alignItems: 'center', justifyContent: 'center' }}>
          <Stack sx={{ width: 50 }}>
            <PieChart chartColors={color_1} />
            <Typography sx={{ fontSize: 12 }} textAlign="center">
              Induction
            </Typography>
          </Stack>
          <Stack sx={{ width: 50 }}>
            <PieChart chartColors={color_2} />
            <Typography sx={{ fontSize: 12 }} textAlign="center">
              Level 1
            </Typography>
          </Stack>
          <Stack sx={{ width: 50 }}>
            <PieChart chartColors={color_3} />
            <Typography sx={{ fontSize: 12 }} textAlign="center">
              Level 2
            </Typography>
          </Stack>
          <Stack sx={{ width: 50 }}>
            <PieChart chartColors={color_4} />
            <Typography sx={{ fontSize: 12 }} textAlign="center">
              Level 3
            </Typography>
          </Stack>
        </Stack>
      </Stack>
      <Stack>
        <Grid display={'grid'} gap={1} gridTemplateColumns={{ sm: 'repeat(4, 1fr)', xs: 'repeat(4, 1fr)' }}>
          {processedSkills.map((item, index) => (
            <Stack
              key={index}
              sx={{
                border: 2,
                alignContent: 'center',
                alignItems: 'center',
                borderColor: COLORS.TEXT_GRAY,
                justifyContent: 'space-between',
              }}
            >
              <Typography px={0.4}>{item.skillName}</Typography>
              <Stack sx={{ width: 70 }}>
                <PieChart chartColors={item.chartColors} />
              </Stack>
            </Stack>
          ))}
        </Grid>
      </Stack>
      <Typography sx={{ mt: 2 }}> Dhoot Transmission Pvt. Ltd. All Rights Reserved. | Powered by: Smart Udyog</Typography>
    </div>
  );
};

export default PrintPage;
