import React, { useEffect, useLayoutEffect, useState } from "react";
import { useLocation, useNavigate } from "react-router-dom";
import Table from "../../components/table";
import useMaterialMaster from "../../store/materialMaster";
import Button from "../../components/button";
import { toast } from "react-toastify";
import { IconButton, Stack, Typography, Grid } from "@mui/material";
import ArrowBackIcon from '@mui/icons-material/ArrowBack';
import { Delete, Checklist } from "@mui/icons-material";
import AdminPanelSettingsIcon from '@mui/icons-material/AdminPanelSettings';
import EditIcon from '@mui/icons-material/Edit';
import ExpandMoreIcon from '@mui/icons-material/ExpandMore';
import KeyboardArrowRightIcon from '@mui/icons-material/KeyboardArrowRight';
import AddCircleIcon from '@mui/icons-material/AddCircle';
import Category from "../../components/UI/Category";
import { COLORS } from "../../helpers/constants";
import JobDetail from "./JobDetail";
import StartJob from "./startJob"
import JsonFormatter from "../../components/UI/JsonFormatter";
import Input from "../../components/input";
import { t } from "i18next";
import AddPacket from './AddPacket'
// import {formatNumberWithCommas} from "../../helpers/utils";
import {formatNumberWithCommas} from "../../helpers/utils"


const MaterialPacketPage = () => {
  const location = useLocation()
  const navigate = useNavigate()
  const plantId = location.state
  const [users, setUsers] = useState([{'jobid':"JOB_123",'client':"unx@32","operations":5,"qty":200}]);
  const [loading, setLoading] = useState();
  const [total, setTotal] = useState(0);
  const [edit, setEdit] = useState(false)
  const [modalOpen, setModalOpen] = useState(false);
  const [page, setPage] = useState(0);
  const [data, setData] = useState({
    name: "",
    metadata: {}
  });
  const [formData, setFromData] = useState({
  });
  const { list, create, update, remove ,packetList,createPacket,updatePacket} = useMaterialMaster();

  useLayoutEffect(() => {
    fetchCompany()
  }, [page]);

  const fetchCompany = async () => {
    setLoading("fetch");
    const [res] = await packetList(page + 1,plantId.id);
    console.log("results=---",res)
    setUsers(res?.results);
    setTotal(res?.count);
    setLoading(false);
  };

  const handleAdd = async () => {
    const payload = { ...formData }
    let newpayload = Object.fromEntries(
      Object.entries(payload).filter(([_, value]) => value !== null)
    )
    // const payload = { ...formData,id: plantId.id }
    setLoading("fetch");
    await createPacket(newpayload, plantId?.id);
    setData({});
    setModalOpen(false);
    await fetchCompany();
    setLoading(false);
    setEdit(false)
  };

  const handleDelete = async (id) => {
    setLoading("fetch");
    await remove(id);
    await fetchCompany();
    setLoading(false);
  };
  const handleUpdate = async (id, is_admin) => {
    setLoading("fetch");
    const payload = {
      "is_admin": is_admin === true ? false : true
    }
    await update(id, payload);
    await fetchCompany();
    setLoading(false);
  };

  const LABELS = [
    {
      id: 1,
      title: t('grnDate'),
      render: (rowData) => rowData.grn_date,
    },
    {
      id: 1,
      title: t('grnNumber'),
      render: (rowData) => rowData.grn_number ||"-",
    },
    // {
    //   id: 4,
    //   title: t('unit'),
    //   render: (rowData) => rowData?.unit||"-"
    // },
    // {
    //   id: 5,
    //   title: t('packingStandard'),
    //   render: (rowData) => rowData?.packing_standard||"-"
    // },
    // {
    //   id: 6,
    //   title: t('numberOfPackets'),
    //   render: (rowData) => rowData?.number_of_packets||"-"
    // },
    {
      id: 7,
      title: t('totalQuantity'),
      render: (rowData) => <Typography>{rowData.total_quantity?formatNumberWithCommas(rowData.total_quantity): '-'}</Typography>
    },
    {
      id: 6,
      title: t('totalRemainingQuantity'),
      render: (rowData) =><Typography>{rowData.remaining_quantity?formatNumberWithCommas(rowData.remaining_quantity):'-'}</Typography> 
    },
    {
      id: 7,
      title: t('remainingToCollect'),
      render: (rowData) => <Typography>{rowData.remaining_to_collect?formatNumberWithCommas(rowData.remaining_to_collect): '-'}</Typography>
    },
    {
      id: 7,
      title: t('scannedQuantity'),
      render: (rowData) => <Typography>{rowData.collected_quantity?formatNumberWithCommas(rowData.collected_quantity):'-'}</Typography>
    },
    // {
    //   id: 2,
    //   title: t('vendorName'),
    //   render: (rowData) => rowData.vendor_name ||"-",
    // },
    // {
    //   id: 3,
    //   title: t('vendorCode'),
    //   render: (rowData) => rowData?.vendor_code ||"-"
    // },
    {
      id: 5,
      title: t("action"),
      render: (rowData) => (
        <Stack flexDirection={'row'}> 
        <IconButton
        sx={{ height: 25, width: 25 }}
        onClick={()=>{SetHandleEdit(rowData)}}
      >
      <EditIcon color="primary" fontSize="small" />
      </IconButton>
</Stack>
)}    
  ];

  const SetHandleEdit=(rowData)=>{
    
    // const { effective_downtime, applicable_to, id, reason_name, time_limit } = rowData;
    setFromData(prevData => (rowData));
      setEdit(true);
      setModalOpen("add")
      console.log("----------rowData",data)
   
  }

  const handleEdit = async () => { 
    const payload = {...formData} 
    console.log("payload",payload)
    let newpayload = Object.fromEntries(
      Object.entries(payload).filter(([_, value]) => value !== null)
    )
    setLoading("fetch");
    let res =  await updatePacket(formData.id,newpayload);
    setPage(0)
    setFromData({});
    setModalOpen(false);
    await fetchCompany();
  };
  return (
    <div className="mx-3 my-2">
      <div className="flex items-center justify-between mb-1">
        <Typography className="fw-20-500-36">{t('materialDetail')}</Typography> 
        <Button  color={'gray'} onClick={()=>setModalOpen("add")}>
          <Typography className="fw-20-500-24">{t("addPacket")}</Typography></Button>
      </div>

      <Table
      // headColor={COLORS.TEXT_GRAY}
      // headLabelColor={COLORS.WHITE}
        columns={LABELS}
        rows={users}
        rowsPerPage={10}
        loading={loading === "fetch"}
        titleCellClasses="c-gray fw-700"
        page={page}
        setPage={setPage}
        paginator
        serialize
        total={total}
      />
      <JobDetail
        open={modalOpen === "detail"}
        onClose={() => { setModalOpen(false); setEdit(false) }}
        data={data}
        edit={edit}
        setData={setData}
        onSubmit={handleAdd}
      />
      <AddPacket
      open={modalOpen === "add"}
      onClose={() => { setModalOpen(false); setEdit(false) }}
      data={formData}
      edit={edit}
      setData={setFromData}
      onSubmit={edit===true?handleEdit:handleAdd}
      />
    </div>
  );
};

export default MaterialPacketPage;
