import React, { useEffect, useLayoutEffect, useRef } from 'react';
import { Chart, registerables } from 'chart.js';
import 'chartjs-adapter-luxon';
// import 'bootstrap/dist/css/bootstrap.min.css';

Chart.register(...registerables);

const HorizontalStackedBarChart = ({timeLineData,lineTime}) => {
    // console.log(timeLineData);
    const chartRef = useRef(null);
    const data = {
        labels: [''],
        datasets: timeLineData
    };

    useLayoutEffect(() => {
        const ctx = chartRef.current.getContext('2d');
        const config = {
            type: 'bar',
            data,
            options: {
                animation: false, // Disable animations
                responsive: true,
                maintainAspectRatio: false,
                plugins: {
                    legend: { display: false },
                    tooltip: {
                        callbacks: {
                            label: (context) => {
                                return context.dataset.label;
                            },
                        },
                    },
                },
                indexAxis: 'y',
                scales: {
                    x: {
                        beginAtZero: true,
                        type: 'time',
                        time: {
                            unit: 'minute',
                        },
                        min: lineTime?.start_time,
                        max: lineTime?.end_time,
                        ticks: {
                            stepSize: 30,
                            autoSkip: false,
                        },
                    },
                    y: {
                        beginAtZero: false,
                        stacked: true,
                    },
                },
            },
        };

        const myChart = new Chart(ctx, config);

        return () => {
            myChart.destroy();
        };
    }, [timeLineData,lineTime]);

    return (
        <div style={{ position: 'relative', height: '100px', width: '100%',marginBottom:'15px' }}>
            <canvas ref={chartRef}></canvas>
        </div>
    );
};

export default HorizontalStackedBarChart;
