import React, { useState, useRef } from "react";
import Modal from "../../../components/modal";
// import Input from "../../components/input";
import { Stack, Typography } from "@mui/material";
import Button from "../../../components/button";
import SchemaExtractor from "../../../components/UI/SchemaExtractor";
import { COLORS, SCHEMA_TYPES, VALIDATORS, } from '../../../helpers/constants'
import { Validate } from "../../../helpers/utils";
// import JsonCreator from "../../components/UI/JsonCreator";
import warning_amber from '../../../assets/svg/warning_amber.svg'
import crossIcon from '../../../assets/svg/cross-icon.svg'
import { t } from "i18next";
import QRCode from "react-qr-code"
import useJob from "../../../store/operatorPortal/job";
import { useEffect } from "react";

import CheckCircleIcon from '@mui/icons-material/CheckCircle';
import { toast } from "react-toastify";

const DuplicateLabel = ({ open, onClose, message, msgtype = 'success' }) => {

  const [errors, setErrors] = useState(new Map());
  const [start, setStart] = useState(false)
  const [label, setLabel] = useState({})
  const printRef = useRef(null);
  const [assemblyData, setAssemblyData] = useState([])
  const [labelQr, setLabelQr] = useState('')

  const { getAssembly, getLabel } = useJob()

  useEffect(() => {
    getAssemblyData()
  }, [open])

  const getAssemblyData = async () => {
    let res = await getAssembly()
    console.log("res getAssembly", res)
    const newUsers = res?.map(item => ({
      key: item?.id,
      value: item?.name
    }))
    newUsers.sort((a, b) => {
      const numA = parseInt(a.value.split(" ")[1], 10);
      const numB = parseInt(b.value.split(" ")[1], 10);
      return numA - numB;
    });
    setAssemblyData(newUsers)
  }


  const USER_SCHEMA = [
    {
      title: "Order Id",
      placeholder: "Enter Work order Id",
      type: SCHEMA_TYPES.INPUT,
      name: "order",
      validator: VALIDATORS.REQUIRED,
    },
    {
      title: "FG Code",
      placeholder: "Enter FG Code",
      type: SCHEMA_TYPES.INPUT,
      name: "fg_code",
      validator: VALIDATORS.REQUIRED,
    },
    {
      title: "Serial Number",
      placeholder: "Enter serial number",
      type: SCHEMA_TYPES.INPUT,
      name: "serial_number",
      validator: VALIDATORS.REQUIRED,
    },
    {
      title: "Line Name",
      placeholder: "Select Line",
      type: SCHEMA_TYPES.SELECT,
      name: "line",
      validator: VALIDATORS.REQUIRED,
      keyValues: assemblyData
    },

  ];

  const handleChange = (e) => {
    const { name, value } = e.target;
    setErrors((prev) => {
      prev.delete(name);
      return prev;
    });
    setLabel((prev) => ({
      ...prev,
      [name]: name === "fg_code" ? value.toUpperCase() : value, // Auto-capitalize fg_code
    }));
  };
  const handleSubmit = async () => {
    const _errors = Validate(USER_SCHEMA, label);
    setErrors(_errors);
    if (_errors.size) return;
    await SumbitPrinting()
    return;
  }

  const SumbitPrinting = async (status) => {
    const payload = {
      order_id: label?.order,
      fg_code: label?.fg_code,
      serial: label?.serial_number,
      line_id: label?.line
    }

    let res = await getLabel(payload)
    if (res?.qr) {
      console.log("res getLabel", res.qr)
      setLabelQr(res.qr)
      handlePrinting()
    } else {
      toast.error("No data found")
      return
    }
    // await onClose()
    // setLabel({})
  }


  const handlePrinting = () => {
    if (!printRef.current) {
      console.error("printRef is null. Cannot print.");
      toast.error("Unable to print. Please try again.");
      return;
    }

    setTimeout(() => {
      const printContent = printRef.current.innerHTML;
      const originalContent = document.body.innerHTML;
      const style = `
          <style>
            @media print {
              @page { margin: 0; size: auto; }
              body { margin: 0; padding: 0; }
              header, footer { display: none; }
            }
          </style>
        `;
  
      document.body.innerHTML = style + printContent;
      window.print();
      document.body.innerHTML = originalContent;
      window.location.reload(); // Restore UI
    }, 1000);
  };

  return (
    <Modal
      open={open}
      onClose={onClose}
      width="min(700px,90vw)"
      wrapperClasses="px-6"
      hideCloseIcon={crossIcon}
    >
      <Stack alignItems="center" spacing={1} mb={4} gap={1} px={3} py={2}>
        <SchemaExtractor
          data={label}
          errors={errors}
          onChange={handleChange}
          schema={USER_SCHEMA}
        />
        <div style={{ display: "none" }}>

          <div ref={printRef}>
            <Stack sx={{ width: '288px', height: '192px' }}>
              <Typography className="fw-36" sx={{ mt: 2 }} textAlign={"center"}>Dhoot Transmission</Typography>
              <Stack sx={{ justifyContent: 'space-between', gap: 1, flexDirection: 'row' }}>
                <Stack sx={{ flexDirection: 'row', gap: 1 }}>
                  <Stack>
                    <Typography>Order no :</Typography>
                    <Typography>Fg code :</Typography>
                    <Typography>S.No :</Typography>
                  </Stack>
                  <Stack>
                    <Typography>{label?.order}</Typography>
                    <Typography>{label?.fg_code}</Typography>
                    <Typography>{labelQr?.split('#')[2]}</Typography> {/* Extract the 3rd part */}
               
                  </Stack>
                </Stack>

              </Stack>
              <Stack sx={{ marginLeft: '70%' }}>
                <QRCode
                  size={60}
                  // style={{ height: "auto", maxWidth: "100%", width: "100%" }}
                  value={labelQr}
                  viewBox={`0 0 256 256`}
                />
              </Stack>
              {/* <Typography textAlign={"center"}>Powered by : Smart Udyog</Typography> */}
            </Stack>
          </div>

        </div>
        <Button color={'green'} onClick={() => {
          handleSubmit('COMPLETED');
          // handlePrinting()
        }}>
          <Typography color={COLORS.WHITE}>
            Print Label
          </Typography>
        </Button>
      </Stack>
    </Modal>
  );
};

export default DuplicateLabel;
