import React, { useEffect, useState } from "react";
import { useLocation, useNavigate } from "react-router-dom";
import { useDispatch } from "react-redux";
import Table from "../../components/table";
import useTeamDashboard from "../../store/teamDashboard";
import useUser from "../../store/users";
import Button from "../../components/button";
import Add from "./Add";
import SchemaExtractor from "../../components/UI/SchemaExtractor";
import { SCHEMA_TYPES, VALIDATORS, } from '../../helpers/constants'
import { Validate } from "../../helpers/utils";
import { toast } from "react-toastify";
import { IconButton, Stack, Typography, Grid, Tab, Tabs, } from "@mui/material";
import { Delete, Checklist } from "@mui/icons-material";
import JsonFormatter from "../../components/UI/JsonFormatter";
import AdminPanelSettingsIcon from '@mui/icons-material/AdminPanelSettings';
import EditIcon from '@mui/icons-material/Edit';
import ExpandMoreIcon from '@mui/icons-material/ExpandMore';
import KeyboardArrowRightIcon from '@mui/icons-material/KeyboardArrowRight';
import AddCircleIcon from '@mui/icons-material/AddCircle';
import Category from "../../components/UI/Category";
import { COLORS } from "../../helpers/constants";
import { t } from "i18next";
import UserManage from "./UserManagement";
import { setTeamItem } from '../../store/teamMaster/team'
import AddUserReport from './AddUserReport'
import useTeamUser from "../../store/teamUsers";
import useSkill from "../../store/skill";
import moment from "moment";
import AddAssign from "./AddSupervisor&Skill";
import ChangeArea from "./ChangeArea";

const TeamUsers = () => {
  const navigate = useNavigate()
  const dispatch = useDispatch()
  const [users, setUsers] = useState([]);
  const [loading, setLoading] = useState();
  const [total, setTotal] = useState(0);
  const [edit, setEdit] = useState(false)
  const [modalOpen, setModalOpen] = useState(false);
  const [page, setPage] = useState(0);
  const [data, setData] = useState({
    id: '',
    area_name: '',
    description: '',
    supervisor: []
  });
  const [skillData,setSkillData] = useState({
    
  })
  const [tab, setTab] = useState(0)
  const [perData, setPerData] = useState({
    opId: ''
  })
  const [assets, setAssets] = useState([])
  const { list, create, update, remove, assetList, areaList, performanceList,updateArea,AllAreaList } = useTeamUser();
  const { alllist} = useSkill()
  const { superUsers, update: updateStatus,updateProfile ,updateOpSkill} = useUser()
  const [errors, setErrors] = useState(new Map());

  const [superVisor, setSuperVisor] = useState([])
  const [rowItem, setRowItem] = useState('')
  const [tableData, setTableData] = useState([])
  const [performanceData, setPerformanceData] = useState([])
  const [superList,setSuperList] = useState([]);
  const [skillsList,setSkillsList] = useState([]);
  const [areaData, setAreaData] = useState({
    areaId:''
  })
  const [areaLists, setAreaList] = useState([])

  useEffect(() => {
    fetchCompany();
  }, [page]);

  useEffect(() => {
    fetchSuperUsers();
    fetchSkill();
    fetchArea()
  }, []);

  const fetchCompany = async () => {
    setLoading("fetch");
    const [_list] = await list(page + 1);
    console.log("area user list", _list)
    // setUsers(_list);
    // setData((prevData) => ({ ...prevData, stations: _list[0]?.id || '' })); // Preserve previous state and handle undefined case
    setTableData(_list);

    // const newUsers = _list.map(item => ({
    //   value: item.name,
    //   key: item.id
    // }));
    // setSuperVisor(prevUsers => [...prevUsers, ...newUsers]);
    // setTotal(_list.count);
    setLoading(false);
  };

  const fetchPerformance = async (aId) => {
    console.log("aId", data)
    setLoading("fetch");
    const [_list] = await performanceList(aId || data.stations);
    console.log("performance list", _list)
    setPerformanceData(_list);
    setLoading(false);
  }

  const fetchSuperUsers = async () => {
      // setLoading("fetch");
      try {
        const [resData] = await superUsers();
          const superList = resData.results
            .filter(item => !item.is_operator && item.status)
            .map(({ username, id }) => ({ value: username, key: id }));
            setSuperList(superList); 
      } catch (error) {
        console.error("Error fetching users:", error);
      } 
    };
  
    const fetchSkill = async () => {
      const [list] = await alllist();
      const newUsers = list.results.map(item => ({
        value: item.skill_name,
        key: item.id
      }));
      setSkillsList(prevUsers => [...prevUsers, ...newUsers]);
    };

    const fetchArea = async () => {
      const [list] = await AllAreaList();
      const newUsers = list.results.map(item => ({
        value: item.name,
        key: item.id
      }));
      setAreaList(prevUsers => [...prevUsers, ...newUsers]);
    };

  
  const handleAdd = async () => {
    const payload = { ...data }
    const req = {
      name: payload.area_name,
      description: payload.description,
      supervisors: payload.supervisor
    }
    setLoading("fetch");
    let res = await create(req);
    setData({});
    setModalOpen(false);
    await fetchCompany();
    console.log("creat res----", res)
  };

  const handleDelete = async (id) => {
    setLoading("fetch");
    await remove(id);
    setPage(0)
    await fetchCompany();
  };

  const handleUpdate = async (id, status) => {
    setLoading("fetch");
    const payload = {
      "status": status === true ? false : true
    }
    await updateStatus(id, payload);
    await fetchCompany();
    setLoading(false);
  };

  const SCHEMA = [{
    placeholder: t('selectArea'),
    type: SCHEMA_TYPES.SELECT,
    name: "stations",
    validator: VALIDATORS.REQUIRED,
    keyValues: superVisor
  }]

  const COLUMNS = [
    {
      id: 1,
      title: t('operator'),
      render: (rowData) => <Typography sx={{ cursor: 'pointer', textTransform: 'capitalize' }} >Op_{rowData.profile?.aadhar_number}</Typography>,
    },
    {
      id: 22,
      title: t('skill'),
      render: (rowData) => <Typography >{rowData.skill || '-'}</Typography>
    },
    {
      id: 22,
      title: t('skill Level'),
      render: (rowData) => <Typography >{rowData.skill_level || '-'}</Typography>
    },
    {
      id: 2,
      title: t('changeArea'),
      render: (rowData) => <Typography sx={{color:'blue',cursor:'pointer'}} onClick={()=>{
        console.log("rowData rowData.current_area_id", rowData.current_area_id)
        setAreaData((prevData) => ({
          ...prevData,
          areaId: rowData.current_area_id.replace(/-/g, ''), // Remove dashes from the ID
          op_id: rowData.profile.user.id,
          userId: rowData.profile.id,
        }));

        setModalOpen("changeArea")}} >{rowData.current_area}</Typography>
    },
    // {
    //   id: 3,
    //   title: t('changeSuperVisor'),
    //   render: (rowData) => <Typography >{rowData.current_area}</Typography>
    // },
    // {
    //   id: 4,
    //   title: t('changeSkill'),
    //   render: (rowData) => <Typography >{rowData.current_area}</Typography>
    // },
    // {
    //   id: 4,
    //   title: t('superVisor'),
    //   render: (rowData) => (
    //     <Stack>
    //       {rowData.supervisors.map((item) => (
    //         <Typography key={item.id}>{item.username}</Typography>
    //       ))}
    //     </Stack>
    //   ),
    // },
    {
      id: 5,
      title: t("action"),
      render: (rowData) => (
        <Stack flexDirection={'row'} gap={2}>
          {/* <IconButton
            sx={{ height: 25, width: 25 }}
            onClick={() => { SetHandleEdit(rowData) }}
          >
            <EditIcon color="primary" fontSize="small" />
          </IconButton> */}
          <Button color={"light_green"}
            onClick={() => { 
              // handleUpdate(rowData.id, rowData.status)
              setSkillData((prevData) => ({
                ...prevData,
                supervisor: rowData.profile.supervisor.id,
                skill: rowData.skill_id,
                op_id: rowData.profile.user.id,
                userId: rowData.profile.id,
              }));
              setModalOpen('assignSkill')
             }}
          >
           {t('change')}
          </Button>

          {/* <IconButton
            sx={{ height: 25, width: 25 }}
            onClick={() => {setModalOpen('addReport')}}
          >
            <Delete color="error" fontSize="small" />
          </IconButton> */}
        </Stack>
      ),
    },
  ];
  const PERFORMANCE = [
    {
      id: 1,
      title: t('user'),
      render: (rowData) => <Typography sx={{ cursor: 'pointer', textTransform: 'capitalize' }} >{rowData.user}</Typography>,
    },
    {
      id: 2,
      title: t('skill'),
      render: (rowData) => <Typography >{rowData.skill.skill_name}</Typography>
    },
    {
      id: 3,
      title: t('skillLevel'),
      render: (rowData) => <Typography >{rowData.skill_level}</Typography>
    },
    // {
    //   id: 4,
    //   title: t('superVisor'),
    //   render: (rowData) => (
    //     <Stack>
    //       {rowData.supervisors.map((item) => (
    //         <Typography key={item.id}>{item.username}</Typography>
    //       ))}
    //     </Stack>
    //   ),
    // },
    {
      id: 5,
      title: t("action"),
      render: (rowData) => (
        <Stack flexDirection={'row'} gap={2}>
          {/* <IconButton
            sx={{ height: 25, width: 25 }}
            onClick={() => { SetHandleEdit(rowData) }}
          >
            <EditIcon color="primary" fontSize="small" />
          </IconButton> */}
          <Button color={"light_green"}
            onClick={() => {
              setPerData((prevData) => ({
                ...prevData,
                opId: rowData.id,
                skill_level: rowData.skill_level,
                skill: rowData.skill.skill_name,
                operator: rowData.user,
                skill_id: rowData.skill.id
              })); setModalOpen('addReport')
            }}

          >
            Add Performance
          </Button>


        </Stack>
      ),
    },
  ];


  const SetHandleEdit = (rowData) => {
    console.log("defect rowdata", rowData);

    const { id, name, description, supervisors } = rowData;
    setData(prevData => ({
      ...prevData,
      id,
      area_name: name,
      description,
      supervisor: supervisors.map(item => item.id),
    }));
    setEdit(true);
    setModalOpen("add");
    console.log("----------rowData", data);
  }

  const handleEdit = async () => {
    const payload = { ...data }
    console.log("payload", payload)
    const req = {
      name: payload.area_name,
      description: payload.description,
      supervisors: payload.supervisor
    }
    console.log("creat res----", req)
    setLoading("fetch");
    let res = await update(data.id, req);
    setPage(0)
    setData({});
    setModalOpen(false);
    await fetchCompany();
  };



  const handleChange = (e) => {
    setTableData(users.filter(item => item.id === e.target.value)[0])
    // console.log("e.target handleChange", e)

    const { name, value } = e.target;
    setErrors((prev) => {
      prev.delete(name);
      return prev;
    });
    setData((prev) => ({ ...prev, [name]: value }));
    if (tab === 1) {
      fetchPerformance(value)
    }
    console.log("data", data)
  };


    const handleSupervisor = async () => {
      setLoading("fetch");
      const payload = {
        "add_supervisor": skillData.supervisor
      }
      updateProfile(skillData.userId, payload)
      handleSkill()
    }
  
    const handleSkill = async () => {
      setLoading("fetch");
      const payload = {
        "skill": skillData.skill,
        "supervisor": skillData.supervisor
      }
      updateOpSkill(skillData.op_id, payload)
      setModalOpen(false);
      fetchCompany()
      setLoading(false);
    }

    const handleChangeArea = async () => {
     let res = await updateArea(areaData.op_id, { "area_id": areaData.areaId })
     console.log("res updateArea", res)
    }

  return (
    <div className="mx-3 my-2">
      <div className="flex items-center justify-between">
        <Typography className="fw-20-500-36">
          {t("teamUserManagement")}
        </Typography>
        {/* <Stack sx={{ mb: 1 }}>
          <SchemaExtractor
            data={data}
            errors={errors}
            onChange={handleChange}
            schema={SCHEMA}
          />
        </Stack> */}
      </div>
      {/* <Tabs value={tab} onChange={(_e, v) => { setTab(v); setPage(0); if (v === 1) { fetchPerformance() } }} className="mb-2 fw-500">
        {[t('operatorList'), t('operatorsPerformance')].map((label, index) => (
          <Tab label={label} className="fw-500" value={index} />
        ))}
      </Tabs> */}

      {tab === 0 ? (
        <Table
          columns={COLUMNS}
          rows={tableData}
          rowsPerPage={10}
          loading={loading === "fetch"}
          titleCellClasses="c-gray fw-700"
          page={page}
          setPage={setPage}
          // paginator
          serialize
          total={total}
        />
      ) : (
        <Table
          columns={PERFORMANCE}
          rows={performanceData}
          rowsPerPage={10}
          loading={loading === "fetch"}
          titleCellClasses="c-gray fw-700"
          page={page}
          setPage={setPage}
          // paginator
          serialize
          total={total}
        />

      )}
      <Add
        open={modalOpen === "add"}
        onClose={() => { setModalOpen(false); setEdit(false); setData({}); }}
        data={data}
        edit={edit}
        setData={setData}
        onSubmit={edit === true ? handleEdit : handleAdd}
        assetsList={superVisor}
      />
      <UserManage
        open={modalOpen === "UserManage"}
        onClose={() => { setModalOpen(false); setEdit(false); setData({}); }}
        data={data}
        edit={edit}
        setData={setData}
        onSubmit={edit === true ? handleEdit : handleAdd}
        assetsList={superVisor}
        rowItem={rowItem}
        modalOpen={modalOpen}
      />
      <AddUserReport
        open={modalOpen === "addReport"}
        onClose={() => { setModalOpen(false) }}
        // onSubmit={handleAdd}
        data={perData}
        setData={setPerData}
      // onSubmit2={uploadPerformance}
      />
      <AddAssign
        open={modalOpen === "assignSkill"}
        onClose={() => { setModalOpen(false) }}
        onSubmit={handleSupervisor}
        skillData={skillData}
        setSkillData={setSkillData}
        modalOpen={modalOpen}
        superList={superList}
        skillsList={skillsList}
      />
      <ChangeArea
        open={modalOpen === "changeArea"}
        onClose={() => { setModalOpen(false) }}
        onSubmit={handleChangeArea}
        areaData={areaData}
        setAreaData={setAreaData}
        areaList={areaLists}
      />
    </div>
  );
};

export default TeamUsers;
