import React, { useState } from "react";
import Modal from "../../../components/modal";
// import Input from "../../components/input";
import { Stack, Typography } from "@mui/material";
// import Button from "../../components/button";
// import SchemaExtractor from "../../components/UI/SchemaExtractor";
import { COLORS, SCHEMA_TYPES, VALIDATORS, } from '../../../helpers/constants'
// import { Validate } from "../../helpers/utils";
// import JsonCreator from "../../components/UI/JsonCreator";
import warning_amber from '../../../assets/svg/warning_amber.svg'
import crossIcon from '../../../assets/svg/cross-icon.svg'
import { t } from "i18next";
import CheckCircleIcon from '@mui/icons-material/CheckCircle';

const ResModal = ({ open, onClose, message, msgtype='success' }) => {

  const [errors, setErrors] = useState(new Map());
  const [start, setStart] = useState(false)

  return (
    <Modal
      open={open}
      onClose={onClose}
      width="60vw"
      top="20vh"
      wrapperClasses="px-5"
      hideCloseIcon={crossIcon}
    >
      <Stack alignItems="center" spacing={2} mb={4} gap={4}>
     
          {msgtype === 'success' ? ( // Fixed comparison operator
            <CheckCircleIcon sx={{ color: COLORS.GREEN, fontSize: 100 }}/>
         ) : (
           <img
             src={warning_amber}
             alt="warning Icon"
             style={{ width: '100px', height: '100px' }}
           />
         )}
        <Typography className="fw-36" textAlign="center">
          {message || ''}
        </Typography>
        {/* <Button onClick={onClose}>{t("close")}</Button> */}
      </Stack>
    </Modal>
  );
};

export default ResModal;
