import React from "react";
import styles from "./capsule.module.scss";
import { COLORS } from "../../helpers/constants";

export default function Capsule({ capsuleText }) {
  var capsuleBgColor;
  switch (capsuleText) {
    case "PLAN":
      capsuleBgColor = "#fa7474";
      break;
    case "ISSUING":
      capsuleBgColor = "#ffd6b0";
      break;
    case "PLANNED":
      capsuleBgColor = "#dded7f";
      break;
    case "RUNNING":
      capsuleBgColor = "#e9efc5";
      break;
    case "COMPLETED":
      capsuleBgColor = "#E9EFC5";
      break;
    case "SHORTAGE":
      capsuleBgColor = COLORS.PASTEL_RED;
      break;
    default:
      capsuleBgColor = "#fa7474";
  }

  return (
    <div className={styles.capsule} style={{ backgroundColor: capsuleBgColor }}>
      {capsuleText}
    </div>
  );
}
