import React, { useEffect, useState } from "react";
import { useAsyncError, useLocation, useNavigate } from "react-router-dom";
import Table from "../../components/table";
// import useDivision from "../../store/division";
import Button from "../../components/button";
import CheckCircleOutlineIcon from '@mui/icons-material/CheckCircleOutline';
import { toast } from "react-toastify";
import { IconButton, Stack, Typography, Grid } from "@mui/material";
import ArrowBackIcon from '@mui/icons-material/ArrowBack';
import { Delete, Checklist } from "@mui/icons-material";
import AdminPanelSettingsIcon from '@mui/icons-material/AdminPanelSettings';
import EditIcon from '@mui/icons-material/Edit';
import ExpandMoreIcon from '@mui/icons-material/ExpandMore';
import KeyboardArrowRightIcon from '@mui/icons-material/KeyboardArrowRight';
import AddCircleIcon from '@mui/icons-material/AddCircle';
import Category from "../../components/UI/Category";
import { COLORS } from "../../helpers/constants";
// import JobDetail from "./JobDetail";
// import StartJob from "./startJob"
import JsonFormatter from "../../components/UI/JsonFormatter";
import { t } from "i18next";
import useJob from "../../store/operatorPortal/job";
import useRedBin from "../../store/redBin";
import Add from './Assign'

const ReworkPage = () => {
  const location = useLocation()
  const navigate = useNavigate()
  const machineData = location.state 
  const{taskList} = useJob()
  const { list, qr_details, add_details ,resolve_details} = useRedBin()
  const [users, setUsers] = useState([]);
  const [loading, setLoading] = useState();
  const [total, setTotal] = useState(0);
  const [edit, setEdit] = useState(false)
  const [modalOpen, setModalOpen] = useState(false);
  const [page, setPage] = useState(0);
  const [data, setData] = useState({
    name: "",
    metadata: {}
  });

  const [ machineType, setMachineType] = useState('')
  const [matrix , setmatrix] = useState({})
    const [dataResolve, setDataResolve] = useState({
    });
    const [reasonList, setReasonList] = useState([])
    const [dReason,setDReason] = useState("")
  // const { list, create, update, remove } = useDivision();

  useEffect(() => {
    console.log("machineData-----",machineData)
    fetchCompany()
  }, []);

  const fetchCompany = async () => {
    // localStorage.setItem("machineId",machineId)
    setLoading("fetch");
    let res = await qr_details(machineData)
    setUsers(res?.defects);
    // setMachineType(res?.task)
    setmatrix({...matrix,"order_id":res?.order_id,"serial_no":res?.serial_no,"fg_code":res?.fg_code})
          const newList = res.reasons.map(item => ({
        value: `${item.reason_name || item.fg_defect}`,
        key: item.id
      }));
      setReasonList(newList);
    // setTotal(_count);
    setLoading(false);
  };

  const handleAdd = async () => {
    // const payload = { ...data }
    // setLoading("fetch");
    // edit === true ? await update(payload) : await create(payload, plantId);
    // setData({});
    // setModalOpen(false);
    // await fetchCompany();
    // setLoading(false);
    setEdit(false)
  };

  const handleDelete = async (id) => {
    setLoading("fetch");
    // await remove(id);
    await fetchCompany();
    setLoading(false);
  };
  const handleUpdate = async (id, is_admin) => {
    setLoading("fetch");
    const payload = {
      "is_admin": is_admin === true ? false : true
    }
    // await update(id, payload);
    await fetchCompany();
    setLoading(false);
  };

  const LABELS = [
    {
      id: 1,
      title: t("defect"),
      render: (rowData) => <Typography sx={{ cursor: 'pointer', fontWeight:'bold'}}
      //  onClick={()=>setModalOpen("detail")}
       >{rowData.fg_defect}</Typography>,
    },
    {
      id: 2,
      title: t("assignedUser"),
      render: (rowData) => rowData.assigned_user,
    },
    {
      id: 6,
      title: t('comment'),
      render: (rowData) => rowData?.comments
    },
    {
      id: 66,
      title: t("workedBy"),
      render: (rowData) => rowData?.worked_by || '-'
    },
    {
      id: 5,
      title: t("actions"),
      render: (rowData) => 
      rowData.status?(
        <CheckCircleOutlineIcon fontSize="large" sx={{color:COLORS.LIGHT_GREEN}}/>
      ):(
      <Button color={'light_green'} onClick={()=>{setEdit(true);setModalOpen("assign");setDReason(rowData?.fg_defect)}}>
        <Typography color={'black'}>
          Resolve
        </Typography>  
      </Button>
      )
    },
  ];

  const handleAssign = async () => {
    let req = {
      "qr_code": machineData?.qr_code,
      "defect_id": data.reason,
      "assigned_user": data.user,
      "comments": data.comment
    }
    // console.log("data---op",data)
    // return

    let res = await add_details(req)
    // console.log("response----", res)
    if (res) {
      setModalOpen(false)
      setData({})
      fetchCompany()
    }
    return;
  }
  const handleAssignResolve = async () => {
    let req = {
      "order_id": matrix.order_id,
      "worked_by": dataResolve.user,
      "worked_on": dataResolve.date,
      "resolution_comments": dataResolve.comment,
      "qr_code":machineData?.qr_code,
      "defect":dReason
    }
    // console.log("data---op",data)
    // return

    let res = await resolve_details(matrix.id,req)
    console.log("resolution  response",res)
    // console.log("response----", res)
    if (res?.message) {
      // setQr(true)
      setModalOpen({})
      setDataResolve({})
      fetchCompany()
    }
    return;
  }

  
  return (
    <div className="mx-3 my-2">
     <div className="flex items-center justify-between">
        <Typography className="fw-20-500-36">
         { t("redBin")}
          {/* <span className="fs-30 ml-1 c-gray">({users.length})</span> */}
        </Typography>
        <Stack sx={{ flexDirection: "row", gap: 1 }}>
          <Button color={'gray'} onClick={() =>{setEdit(false); setModalOpen("assign")}} loading={loading}>
            <Typography className="fw-20-500-24">
              {t('addNew')}
            </Typography>
          </Button>
        </Stack>
      </div>
      <Stack sx={{flexDirection:'row',gap:2}}>
        <Typography>{t('orderId')} : {matrix?.order_id}</Typography>
        <Typography>{t('FGCode')} : {matrix?.fg_code}</Typography>
        <Typography>{t('serialNumber')} : {matrix?.serial_no}</Typography>
      </Stack>
        {/* <Stack sx={{ flexDirection: 'row', gap: 2 }}>
          <Stack sx={{ backgroundColor: COLORS.LG_GREEN, width: 240, height: 80, borderRadius: 2, flexDirection: 'row',justifyContent:'space-between' }}>
            <Typography sx={{ p: 2, fontSize: 20, fontWeight: 500 }}>Scheduled<br></br> Jobs</Typography>
            <Typography sx={{ p: 2, fontSize: 36, fontWeight: 700 }}>{matrix?.waiting}</Typography>
          </Stack>
          <Stack sx={{ backgroundColor: COLORS.LG_PINK, width: 240, height: 80, borderRadius: 2, flexDirection: 'row',justifyContent:'space-between' }}>
            <Typography sx={{ p: 2, fontSize: 20, fontWeight: 500 }}>Running<br></br>Jobs</Typography>
            <Typography sx={{ p: 2, fontSize: 36, fontWeight: 700 }}>{matrix?.running}</Typography>
          </Stack>
          <Stack sx={{ backgroundColor: '#C4D7FC', width: 240, height: 80, borderRadius: 2, flexDirection: 'row',justifyContent:'space-between' }}>
            <Typography sx={{ p: 2, fontSize: 20, fontWeight: 500 }}>Paused <br></br>Jobs</Typography>
            <Typography sx={{ p: 2, fontSize: 36, fontWeight: 700 }}>{matrix?.paused}</Typography>
          </Stack>
          <Stack sx={{ backgroundColor: COLORS.LG_PURPLE, width: 240, height: 80, borderRadius: 2, flexDirection: 'row',justifyContent:'space-between' }}>
            <Typography sx={{ p: 2, fontSize: 20, fontWeight: 500 }}>Completed <br></br>Jobs</Typography>
            <Typography sx={{ p: 2, fontSize: 36, fontWeight: 700 }}>{matrix?.completed}</Typography>
          </Stack>
        </Stack> */}
   
      <Table
      // headColor={COLORS.TEXT_GRAY}
      // headLabelColor={COLORS.WHITE}
        columns={LABELS}
        rows={users}
        rowsPerPage={5}
        loading={loading === "fetch"}
        titleCellClasses="c-gray fw-700"
        page={page}
        setPage={setPage}
        paginator
        serialize
        total={total}
      />
      <Add
        data={data}
        edit={edit}
        setData={setData}
        dataResolve={dataResolve}
        setDataResolve={setDataResolve}
        open={modalOpen === "assign"}
        onClose={() =>{ setModalOpen(false)}}
        reasonList={reasonList}
        onSubmit={edit?handleAssignResolve:handleAssign}
      />
      {/* <JobDetail
        open={modalOpen === "detail"}
        onClose={() => { setModalOpen(false); setEdit(false) }}
        data={data}
        edit={edit}
        setData={setData}
        onSubmit={handleAdd}
      />
      <StartJob
        open={modalOpen === "start"}
        onClose={() => { setModalOpen(false); setEdit(false) }}
        data={data}
        edit={edit}
        setData={setData}
        onSubmit={handleAdd}
      /> */}
      {/* <Stack
        direction="row"
        alignItems="center"
        justifyContent="space-between"
        mt={4}
        mb={1}
        // sx={{px:6}}
      >
        <Button bordered gray >
          Cancel
        </Button>
        <Button color={'gray'} >
          <Typography color={COLORS.TEXT_GRAY}>
          Next
          </Typography>  
        </Button>
      </Stack> */}
    </div>
  );
};

export default ReworkPage;
