import React, { useEffect, useState, useRef, use } from "react";
import Modal from "../../components/modal";
import Input from "../../components/input";
import { Stack, Typography,IconButton } from "@mui/material";
import Button from "../../components/button";
import SchemaExtractor from "../../components/UI/SchemaExtractor";
import { SCHEMA_TYPES, VALIDATORS, } from '../../helpers/constants'
import { Validate } from "../../helpers/utils";
import { useTranslation } from 'react-i18next';
import { t } from "i18next";
import crossIcon from '../../assets/svg/cross-icon.svg'
import { toast } from "react-toastify";
import { useDispatch, useSelector } from "react-redux";
import Table from "../../components/table";
import { COLORS } from "../../helpers/constants";
import moment from "moment/moment";
import {setFailItem} from '../../store/pending_items/f_items'
import Tooltip from '@mui/material/Tooltip';
import usePendingItems from "../../store/pending_items";
import { formatNumberWithCommas } from "../../helpers/utils";


const AddAssign = ({ open, onClose, openModal, data, setData, onSubmit,QR, loading, tab, setSelectedQuestions, skills, users, testList }) => {
  const groupName = useSelector((state) => state?.Failure_item?.data);
  const inputRef = useRef(null);
  const [payload, setPayload] = useState()
  const dispatch = useDispatch()
  const { list,create,update,getShortMat} = usePendingItems();



  console.warn("click item", groupName)
  const [errors, setErrors] = useState(new Map());
  const [step, setStep] = useState(0);

  useEffect(() => {
    console.warn("openModal----", openModal)
    if (inputRef.current) {
      inputRef.current.focus();
    }
  }, [openModal === "addAssign"]);

  const handleChange = (e) => {
    const { name, value } = e.target;
    setErrors((prev) => {
      prev.delete(name);
      return prev;
    });
    setData((prev) => ({ ...prev, [name]: value }));
  };


  const handleNext = async () => {
    if (step === 0) {
      const _errors = Validate(SKILL, data);
      setErrors(_errors);
      if (_errors.size) return;
      await onSubmit()
      // console.log("data------------",data.admins)
      // setStep((prev) => prev + 1);
      return;
    }
  };
    const COLUMNSS = [
      {
        id: 1,
        title: t('orderNo'),
        render: (rowData) =>
          <Typography>{rowData.work_order?.order_id}</Typography>,
      },
      // {
      //   id: 2,
      //   title: t('FGCode'),
      //   render: (rowData) => <Typography>{rowData.work_order?.name}</Typography>,
      // },
      {
        id: 3,
        title: t('materialName'),
        render: (rowData) => <Typography>{rowData.material?.material_code}</Typography>,
      },
      // {
      //   id: 4,
      //   title: t('resolutionDate'),
      //   render: (rowData) => <Typography>{moment(rowData?.work_order?.date_created).format('ll')}</Typography>,
      // },
      {
        id: 44,
        title: t('shortQty'),
        render: (rowData) => <Typography>{formatNumberWithCommas(rowData?.pending_quantity, 2)}</Typography>,
      },
      // {
      //   id: 5,
      //   title: t('status'),
      //   render: (rowData) => <Typography>{rowData?.status === true ? 'True' : 'False'}</Typography>,
      // },
        {
        id: 8,
        title: t('action'),
        render: (rowData) => 
          <>
       
      <Stack sx={{flexDirection:'row',gap:1}}>
      <Tooltip title="scan item">
        <Button color={'light_green'}
        sx={{ height: 25, width: 25 }}
        onClick={() => {console.log("clicked data-----",rowData);
          createMat(rowData?.work_order?.id)
          // dispatch(setFailItem(rowData))
        }}
      >
        Issue
        {/* <QrCodeScannerIcon color="primary" fontSize="small" /> */}
      </Button>
      </Tooltip>
      </Stack>
      </>
  
        },
    ]

    const createMat = async (work_order_id) => {
      console.log("data in create mat------", groupName)
      const payload = {
        // material_failure_id : groupName?.id,
        workorder_id: work_order_id,
        material_qr: QR,
        // material_code: data['material_code'],
        // material_grn : data['material_grn'],
        // material_quantity : data['material_quantity']
      }
      let res = await create(payload)
      console.log("res - create mat------", res)
      await onClose()
      // let failid = groupName?.id
      // if(res?.message=== 'OK'){
      //   await update(failid,{status:false,comments:data['comment']})
      // }else{
      //   await update(failid,{status:true,comments:data['comment']})
      // }
      // setModalOpen(false)
    }

  const SKILL = [
    {
      title: t('materialQr'),
      placeholder: t('scanQr'),
      type: SCHEMA_TYPES.INPUT,
      name: 'material_qr',
      // validator: VALIDATORS.REQUIRED,
    },
    {
      title: t('materialCode'),
      placeholder: t('enterMaterialCode'),
      type: SCHEMA_TYPES.INPUT,
      name: 'material_code',
      // validator: VALIDATORS.REQUIRED,
    },
    {
      title: t('grnNo'),
      placeholder: t('enterGrnNumber'),
      type: SCHEMA_TYPES.INPUT,
      name: 'material_grn',
      // validator: VALIDATORS.REQUIRED,
    },
    {
      title: t('quantity'),
      placeholder: t('enterMaterialQuantity'),
      type: SCHEMA_TYPES.INPUT,
      name: 'material_quantity',
      // validator: VALIDATORS.REQUIRED,
    },
    {
      title: "Comment",
      placeholder: "Comment",
      type: SCHEMA_TYPES.INPUT,
      name: 'comment',
    }
  ];

  const handleKeyPress = (e) => {
    if (e.key === "Enter") {
      handleScan(e.target.value);
      e.target.value = ""; // Clear the input for the next scan
    }
  };
  const handleScan = (data) => {
    handleBarcodeScannerModalSubmit(data);
  };

  const handleBarcodeScannerModalSubmit = (inputData) => {
    const [material_name, quantityString, unit, grnNumber, grnDate] = inputData.split("#");
    // console.log("scan item", material_name, quantityString, unit, grnNumber, grnDate);

    const quantity = parseFloat(quantityString);
    if (isNaN(quantity)) {
      toast.warn("Invalid quantity");
      return;
    }

    const currentMaterial = groupName?.material?.material_code === material_name

    // console.log("Material found in table data");
    if (!currentMaterial) {
      toast.warn("Material not found in data");
      return;
    }

    setPayload((prevPayload) => {
      const updatedPayload = { ...prevPayload };
      // Calculate the total existing quantity in payload for this material
      const existingQuantity = updatedPayload[material_name]?.reduce(
        (sum, item) => sum + item.quantity,
        0
      ) || 0;

      // Check if adding the new quantity exceeds the allowed quantity
      // if (quantity > currentMaterial.quantity) {
      //   toast.warn(`Cannot add.${material_name} Quantity is more then require.`);
      //   return prevPayload; // Do not update if limit is exceeded
      // }
      if (existingQuantity + quantity > currentMaterial.quantity) {
        toast.warn(`Cannot add more. Max quantity for ${material_name} reached.`);
        return prevPayload; // Do not update if limit is exceeded
      }

      if (!updatedPayload[material_name]) {
        updatedPayload[material_name] = [];
      }
      updatedPayload[material_name].push({
        quantity: quantity,
        grn_number: grnNumber
      });

      // console.log("Updated Payload Inside setPayload:", updatedPayload);
      return updatedPayload;
    });
    if (inputRef.current) {
      inputRef.current.focus();
    }
  };


  return (
    <Modal
      open={open}
      onClose={onClose}
      width="min(900px,90vw)"
      hideCloseIcon={crossIcon}
      wrapperClasses="px-5"
    >
      <h3 className="opacity-80" style={{ width: 'fit-content' }}>{'Issuing item '}</h3>

      <Table
        columns={COLUMNSS}
        rows={users}
        // rowsPerPage={10}
        loading={loading === "fetch"}
        titleCellClasses="c-gray fw-700"
        // page={page}
        // setPage={setPage}
        // paginator
        serialize
        // total={total}
        tableBodyColor={COLORS.WHITE}
      />
      {/* <Stack
        direction="row"
        alignItems="center"
        justifyContent="space-between"
        mt={4}
        mb={1}
      >
        <Button bordered gray onClick={onClose}>
          {t('cancel')}
        </Button>
        <Button color={'light_green'} onClick={handleNext} loading={loading}>
          <Typography className="fw-20-500-24">
            {t('next')}
          </Typography>
        </Button>
      </Stack> */}
    </Modal>
  );
};

export default AddAssign;
