import React from "react";
import { resolveResponse } from "../api/utils";
import REQUEST, { CONTENT_TYPES } from "../api";
import { ENDPOINTS } from "../api/endpoints";
import { useSelector } from "react-redux";
import { REDUCER_TYPES } from "..";

const useTeamUser = () => {
  const { token } = useSelector((state) => state[REDUCER_TYPES.AUTH]);

  const headers = {
    Authorization: `Bearer ${token}`,
    "Content-Type": CONTENT_TYPES.APPLICATION_JSON,
  };

  const areaList = async (date) => {
    const client = new REQUEST();
    const res = await client.get(
      `teams/supervisors-areas/`,
      {},
      headers,
      resolveResponse
    );

    if (res?.data) {
      console.log("res  ",res.data)
      return [res?.data];
    }
    return [[], 0];
  };

  const performanceList = async (areaId) => {
    const client = new REQUEST();
    const res = await client.get(
      `teams/performance-cards/${areaId}/`,
      {},
      headers,
      resolveResponse
    );

    if (res?.data) {
      console.log("res  ",res.data)
      return [res?.data];
    }
    return [[], 0];
  };
  const list = async (page = 1) => {
    const client = new REQUEST();
    const res = await client.get(
      `teams/areas/?page=${page}&page_size=10`,
      {},
      headers,
      resolveResponse
    );

    if (res?.data) {
      console.log("res  ",res.data)
      return [res?.data];
    }
    return [[], 0];
  };

  const getDetail = async (id) => {
    const client = new REQUEST();
    const res = await client.get(
      `teams/area/${id}/`,
      {},
      headers,
      resolveResponse
    );

    if (res?.data) {
      console.log("res  ",res.data)
      return [res?.data];
    }
    return [[], 0];
  };

  const create = async (payload = {}) => {
    const client = new REQUEST();
    const res = await client.post(
      `teams/areas/`,
      payload,
      headers,
      (res) => resolveResponse(res, null)
    );
    return res?.data;
  };
  

  const assetList = async (page = 1) => {
    const client = new REQUEST();
    const res = await client.get(
      `asset/all-assets/?page=1&page_size=100`,
      {},
      headers,
      resolveResponse
    );

    if (res?.data) {
      console.log("res  ",res.data)
      return [res?.data];
    }
    return [[], 0];
  };

  // const create = async (payload = {}) => {
  //   const client = new REQUEST();
  //   const res = await client.post(
  //     `settings/defect-reasons/`,
  //     payload,
  //     headers,
  //     (res) => resolveResponse(res, null)
  //   );
  //   return res?.data;
  // };

  const update = async (id,payload) => {
    const client = new REQUEST();
    const res = await client.put(
      `teams/area/${id}/`,
      payload,
      headers,
      (res) => resolveResponse(res, null)
    );
    return res?.data;
  };

  const remove = async (id) => {
    const client = new REQUEST();
    const res = await client.delete(
      `teams/area/${id}/`,
      headers,
      (res) => resolveResponse(res, null,"Deleted!")
    );
    return res?.data;
  };

  return {
    areaList,
    performanceList,
    list,
    assetList,
    create,
    update,
    remove,
    getDetail
  };
};

export default useTeamUser;
