import React,{useState} from "react";
import Modal from "../../components/modal";
import Input from "../../components/input";
import { Stack ,Typography} from "@mui/material";
import Button from "../../components/button";
import SchemaExtractor from "../../components/UI/SchemaExtractor";
import {SCHEMA_TYPES,VALIDATORS,} from '../../helpers/constants'
import { Validate } from "../../helpers/utils";
import JsonCreator from "../../components/UI/JsonCreator";
import crossIcon from '../../assets/svg/cross-icon.svg'
import { t } from "i18next";

const AddPacket = ({ open, onClose, data, setData, onSubmit, loading,edit}) => {

  const USER_SCHEMA = [
    // {
    //   title: t("materialName"),
    //   placeholder: "Enter Line Name",
    //   type: SCHEMA_TYPES.INPUT,
    //   name: "material",
    //   validator: VALIDATORS.REQUIRED,
    // },
   edit===false&&( {
      title: t("grnNumber"),
      // placeholder: "Enter Line Name",
      type: SCHEMA_TYPES.INPUT,
      name: "grn_number",
      validator: VALIDATORS.REQUIRED,
    }),
    {
      title: t("totalQuantity"),
      // placeholder: "Enter Line Name",
      type: SCHEMA_TYPES.INPUT,
      name: "total_quantity",
      validator: VALIDATORS.REQUIRED,
    },
    edit===false&&( {
      title: t("unit"),
      // placeholder: "Enter Line Name",
      type: SCHEMA_TYPES.INPUT,
      name: "unit",
      validator: VALIDATORS.REQUIRED,
    }),
    {
      title: t("grnDate"),
      // placeholder: "Enter Line Name",
      type: SCHEMA_TYPES.DATE,
      name: "grn_date",
      validator: VALIDATORS.REQUIRED,
    },
    // edit===false&&( {
    //   title: t("vendorCode"),
    //   placeholder: "Enter Line Name",
    //   type: SCHEMA_TYPES.INPUT,
    //   name: "vendor_code",
    //   // validator: VALIDATORS.REQUIRED,
    // }),
    // edit===false&&( {
    //   title: t("vendorName"),
    //   placeholder: "Enter Line Name",
    //   type: SCHEMA_TYPES.INPUT,
    //   name: "vendor_name",
    //   // validator: VALIDATORS.REQUIRED,
    // }),
    // edit===false&&( {
    //   title: t("packingStandard"),
    //   placeholder: "Enter Line Name",
    //   type: SCHEMA_TYPES.INPUT,
    //   name: "packing_standard",
    //   // validator: VALIDATORS.REQUIRED,
    // }),
    // edit===false&&( {
    //   title: t("lotNumber"),
    //   placeholder: "Enter Line Name",
    //   type: SCHEMA_TYPES.INPUT,
    //   name: "inspection_lot_number",
    //   // validator: VALIDATORS.REQUIRED,
    // }),
    // edit===false&&( {
    //   title: t("numberOfPacket"),
    //   placeholder: "Enter Line Name",
    //   type: SCHEMA_TYPES.INPUT,
    //   name: "number_of_packets",
    //   // validator: VALIDATORS.REQUIRED,
    // }),
    {
      title: t("remainingQuantity"),
      // placeholder: "Enter Line Name",
      type: SCHEMA_TYPES.INPUT,
      name: "remaining_quantity",
      // validator: VALIDATORS.REQUIRED,
    },
    {
      title: t("collectedQuantity"),
      // placeholder: "Enter Line Name",
      type: SCHEMA_TYPES.INPUT,
      name: "collected_quantity",
      // validator: VALIDATORS.REQUIRED,
    },
    {
      title: t("remainingToCollect"),
      // placeholder: "Enter Line Name",
      type: SCHEMA_TYPES.INPUT,
      name: "remaining_to_collect",
      // validator: VALIDATORS.REQUIRED,
    },
  ];
  const [errors, setErrors] = useState(new Map());

  const handleChange = (e) => {
    const { name, value } = e.target;
    setErrors((prev) => {
      prev.delete(name);
      return prev;
    });
    setData((prev) => ({ ...prev, [name]: value }));
  };
   const handleSubmit=async ()=>{
            const _errors = Validate(USER_SCHEMA, data);
            setErrors(_errors);
            if (_errors.size) return;
            await onSubmit()
            return;
      }
  return (
    <Modal
      open={open}
      onClose={onClose}
      width="min(700px,90vw)"
      hideCloseIcon={crossIcon}
        wrapperClasses="px-6"
    >
      <h3 className="opacity-80">{edit=== true?"EDIT":"ADD"} Packet</h3>
      
      <SchemaExtractor
          data={data}
          errors={errors}
          onChange={handleChange}
          schema={USER_SCHEMA}
        />
      <Stack
        direction="row"
        alignItems="center"
        justifyContent="space-between"
        mt={4}
        mb={1}
      >
        <Button bordered gray onClick={onClose}>
          {t('cancel')}
        </Button>
        <Button color={'light_green'} onClick={handleSubmit} loading={loading}>
         <Typography className="fw-20-500-24">{t('create')}</Typography> 
        </Button>
      </Stack>
    </Modal>
  );
};

export default AddPacket;
