import "./styles.css";
import { useLocation, useNavigate, useSearchParams } from "react-router-dom";
import React, { useState, useEffect } from "react";
import Table from "../../../components/table";
import TableComponent from "../components/Table";
// components
import Button from "../../../components/button";
import Breadcrumb from "../../../components/breadcrumb";
import { IconButton, Stack } from "@mui/material";
import actionIcon from "../../../assets/svg/action-icon.svg";
import ArrowBackIcon from '@mui/icons-material/ArrowBack';
import { COLORS } from "../../../helpers/constants";
// import { useBOM } from "../../../store/materialPlanner";
import useMaterialPlanning from "../../../store/planningMaterial"
import Add from "./Add";
import ACCAssign from "./AccAssign"
import SchemaExtractor from "../../../components/UI/SchemaExtractor";
import { SCHEMA_TYPES, VALIDATORS, } from '../../../helpers/constants'
import { Validate } from "../../../helpers/utils";
import { t } from "i18next";
import CheckCircleOutlineIcon from '@mui/icons-material/CheckCircleOutline';
import { set } from "lodash";
import { toast } from "react-toastify";


function PlanningMaterialOrderInstruction() {
  const navigate = useNavigate();
  const locaiton = useLocation()
  const locationData = locaiton?.state
  // const [searchParams] = useSearchParams();
  const [tableData, setTableData] = useState([]);
  const { orderTasks ,orderAssetList,machineAssignMultiple} = useMaterialPlanning();
  const [loading, setLoading] = useState();
  const [total, setTotal] = useState(0);
  const [page, setPage] = useState(0);
    const [modalOpen, setModalOpen] = useState(false);
  // const [edit, setEdit] = useState(false); 
  const [data, setData] = useState({
  });
   const [assets, setAssets] = useState()
   const [errors, setErrors] = useState(new Map());
   const [map_id,setMap_id] = useState('')
   const [circuitName,setCircuitName] = useState('')
   const [selectedRows, setSelectedRows] = useState([]); // State to store selected row IDs
   const [checkData, setCheckData] = useState({}); // State to store selected row IDs

   
  useEffect(() => {
    fetchCircuit();
    fetchAssestList()
  }, [])

  async function fetchCircuit() {
    setLoading("fetch")
    let [res] = await orderTasks(page, locationData?.id)
    console.log("circuit data----", page, locationData)
    setTableData(res.circuits)
    res.circuits.map((item)=>{
      // setData({...data,[item.map_id]:item.assigned_asset})
      // handleChange(item.map_id,'machine',item.assigned_asset)
      setData((prev) => ({
        ...prev,
        [item.map_id]: { 
          ...prev[item.map_id], 
          'machine': item.assigned_asset 
        }
      }));
    })
    setLoading(false)
  }
  // async function fetchCircuit() {
  //   let [res] = await orderTasks(page, locationData?.id);
  //   console.log("circuit data----", page, locationData);
  //   setTableData(res.circuits);
  
  //   setData((prevData) => {
  //     const newData = { ...prevData };
  //     res.circuits.forEach((item) => {
  //       newData[item.map_id] = item.assigned_asset;
  //     });
  //     return newData;
  //   });
  // }

  async function fetchAssestList(id) {
    let [res] = await orderAssetList(1,locationData?.id)
    console.log("asset list-------",res)
    const newList = res.map(item => ({
      value: item.name,
      key: item.id
    }));
    newList.sort((a, b) => {
      const numA = parseInt(a.value.split(" ")[1], 10);
      const numB = parseInt(b.value.split(" ")[1], 10);
      return numA - numB;
    });
    setAssets(newList)
  }

  const USER_SCHEMA = [
      {
        // title: "Machines",
        placeholder: "Select Machine",
        type: SCHEMA_TYPES.SELECT,
        name: "machine",
        // multi:true,
        validator: VALIDATORS.REQUIRED,
        keyValues: assets,
        width:'200px'
      }]


const handleCheckboxChange = (id, isChecked) => {
  setSelectedRows((prev) => {
    if (isChecked) {
      return [...prev, id]; // Add the ID if checked
    } else {
      return prev.filter((rowId) => rowId !== id); // Remove the ID if unchecked
    }
  });
};
  const COLUMNS = [
    {
      id: 0,
      title: t("Select"),
      render: (rowData) => (
        <input
          type="checkbox"
          onChange={(e) => handleCheckboxChange(rowData.map_id, e.target.checked)}
        />
      ),
    },
    {
      id: 1,
      title: t("CircuitCode"),
      render: (rowData) => rowData.circuit_information?.circuit_code ||'-',
    },
    // {
    //   id: 2,
    //   title: t("CircuitName"),
    //   render: (rowData) => rowData.circuit_information?.name||'-',
    // },
    // {
    //   id: 3,
    //   title: t("qty"),
    //   render: (rowData) => rowData.quantity,
    // },
    {
      id: 3,
      title: t("wire"),
      render: (rowData) => rowData.circuit_information?.circuit_metadata?.Wire  ||'-',
    },
  
    {
      id: 5,
      title: t("TerminalA"),
      render: (rowData) => rowData.circuit_information?.circuit_metadata?.TerminalA  ||'-',
    },
    {
      id: 6,
      title: t("TerminalB"),
      render: (rowData) => rowData.circuit_information?.circuit_metadata?.TerminalB  ||'-',
    },
    {
      id: 7,
      title: t("PartFittingA"),
      render: (rowData) => rowData.circuit_information?.circuit_metadata?.PartFittingA  ||'-',
    },
    {
      id: 8,
      title: t("PartFittingB"),
      render: (rowData) => rowData.circuit_information?.circuit_metadata?.PartFittingB  ||'-',
    },
    // {
    //   id: 9,
    //   title: t("Seal A"),
    //   render: (rowData) => rowData.circuit_information?.circuit_metadata?.sealA==="Null"?"-":rowData.circuit_information?.circuit_metadata?.sealA,
    // },
    // {
    //   id: 10,
    //   title: t("Seal B"),
    //   render: (rowData) => rowData.circuit_information?.circuit_metadata?.sealB==="Null"?"-":rowData.circuit_information?.circuit_metadata?.sealB,
    // },
    {
      id: 4,
      title:t("Select_Machine_Edit"),
      render: (rowData) => (
      rowData.assigned_asset!==null?(
       <IconButton 
       onClick={()=>{setMap_id(rowData.map_id);setCircuitName(rowData.circuit_information?.circuit_code || rowData.circuit_information?.name);setModalOpen("ACCAssign")}}
       >
       <CheckCircleOutlineIcon color="success"  style={{
          width: "2.78vw",
          height: "2.78vw",
          cursor: "pointer",
        }}/>
       </IconButton> 
      ):
        (
          <IconButton
        onClick={()=>{setMap_id(rowData.map_id);setCircuitName(rowData.circuit_information?.circuit_code || rowData.circuit_information?.name);setModalOpen("ACCAssign")}}
        >
        <img
        src={actionIcon}
        style={{
          width: "2.78vw",
          height: "2.78vw",
          cursor: "pointer",
        }}
        alt="Action Icon"
        />
        </IconButton>
      )  
      ),
    },
  ];


  const handleChange = (id, name, value) => {
    console.log("-->","map id",id,"name---",name,"value----",value)
    setErrors((prev) => {
      prev.delete(`${id}-${name}`);
      return new Map(prev);
    });
  
    setData((prev) => ({
      ...prev,
      [id]: { 
        ...prev[id], 
        [name]: value 
      }
    }));
    console.log("machine assignment res--------",data)

    circuitAssign(id,value)
  };

 
  const ASSETSCHEMA = [
        {
          // title: t("SelectMachine"),
          placeholder: t("SelectMachine"),
          type: SCHEMA_TYPES.SELECT,
          name: "machine",
          // multi:true,
          validator: VALIDATORS.REQUIRED,
          keyValues: assets,
          // width:'200px'
        }]


      async function handleMachineAssign(value) {
        if (!value) return; // Guard clause for empty or undefined values
      
        const payload = {
          order_type: locationData.order_type,
          asset_id: [value],
        };
      
        // Fire all requests in parallel
        try {
          await Promise.all(
            selectedRows.map((rowId) => circuitAssign(rowId, payload))
          );
          console.log("Machine assignment complete.");
          toast.success("Machine assignment complete.");
          setSelectedRows([]); // Clear selected rows after assignment
          fetchCircuit(); // Refresh the circuit data after assignment
        } catch (error) {
          console.error("Error assigning machine:", error);
          toast.error("Error assigning machine.",error);
        }
      }
      
      async function circuitAssign(id, payload) {
        try {
          const res = await machineAssignMultiple(id, payload);
          return res;
        } catch (error) {
          console.error(`Error assigning machine to row ${id}:`, error);
          throw error;
        }
      }
      

  return (
    <div className="mx-3 my-2">
      <Stack sx={{flexDirection:'row',justifyContent:'space-between',alignItems:'center'}}>
      <div style={{ display: 'flex', flexDirection: 'row', gap: 2 ,alignItems:'center'}}>
        <IconButton onClick={() => navigate(-1)}>
          <ArrowBackIcon />
        </IconButton>
        <p className="instruction-text">
          {locationData.name} / {locationData.order_id} / {locationData.order_type} / {locationData.running_status}
        </p>
      </div>
      {selectedRows.length > 0 && (
      <SchemaExtractor
          data={checkData}
          // data={data[map_id] || {}}
          errors={errors}
          onChange={(e) =>handleMachineAssign( e.target.value)}
          schema={ASSETSCHEMA}
        />
      )}
      </Stack>

      {/* <Button onClick={()=>console.log("---------------selectedRows",selectedRows)}>console</Button> */}
     
      <Table
        columns={COLUMNS}
        rows={tableData}
        rowsPerPage={10}
        loading={loading === "fetch"}
        titleCellClasses="c-gray fw-700"
        page={page}
        setPage={setPage}
        // paginator
        serialize
        total={10}
        tableBodyColor={COLORS.WHITE}
      />
       <Add
        open={modalOpen === "add"}
        onClose={() => { setModalOpen(false)}}
        data={data}
        // edit={edit}
        setData={setData}
        // onSubmit={handleAdd}
        assetsList={assets}
      />
       <ACCAssign
        open={modalOpen === "ACCAssign"}
        onClose={() => { setModalOpen(false)}}
        data={data}
        // edit={edit}
        setData={setData}
        circuitName={circuitName}
        map_id={map_id}
        // onSubmit={handleAdd}
        assets={assets}
      />

      {/* Footer   */}
      {/* <div className="material-planner-layout-footer">
        <Button
          onClick={() => navigate("/planning-material")}
          white={true}
          width="8rem"
          height="2.5rem"
          bordered={true}
          customStyle={{
            border: "1px solid #3E3E3E",
            backgroundColor: "#FFFFFF",
          }}
        >
          Back
        </Button>
        <Button
          // onClick={handleSubmit}
          color="green"
          width="8rem"
          height="2.5rem"
        >
          Plan
        </Button>
      </div> */}
    </div>
  );
}

export default PlanningMaterialOrderInstruction;
