import React, { useState } from "react";
import Radio from '@mui/material/Radio';
import RadioGroup from '@mui/material/RadioGroup';
import FormControlLabel from '@mui/material/FormControlLabel';
import FormControl from '@mui/material/FormControl';
import FormLabel from '@mui/material/FormLabel';
import Modal from "../../components/modal";
import Input from "../../components/input";
import { Stack, Typography } from "@mui/material";
import Button from "../../components/button";
import SchemaExtractor from "../../components/UI/SchemaExtractor";
import { SCHEMA_TYPES, VALIDATORS, } from '../../helpers/constants'
import { Validate } from "../../helpers/utils";
import JsonCreator from "../../components/UI/JsonCreator";
import RadioButtonGroup from "../../components/input/radio";
import crossIcon from '../../assets/svg/cross-icon.svg'
import { t } from "i18next";
import useUser from "../../store/users";

const Add = ({ open, onClose, data, setData, dataResolve,setDataResolve, onSubmit, loading, edit, reasonList }) => {
  const { list: userList, listByAadhar2, listByAadharFortestPortal } = useUser();

  async function fetchByAd(val) {
    let res = await listByAadharFortestPortal(val || '')
    return res
  }

  const TEST = [
    {
      title: t('selectReason'),
      placeholder: t('selectReason'),
      type: SCHEMA_TYPES.SELECT,
      name: 'reason',
      validator: VALIDATORS.REQUIRED,
      keyValues: reasonList,
    },
    {
      title: t('operator'),
      placeholder: t('selectOperator'),
      type: SCHEMA_TYPES.MASTER,
      name: "user",
      validator: VALIDATORS.REQUIRED,
      fetch: [fetchByAd],
      noSearch: true
    },
    {
      title: t('comment'),
      placeholder: t('comment'),
      type: SCHEMA_TYPES.INPUT,
      name: "comment",
      // validator: VALIDATORS.REQUIRED,
      // fetch: [fetchByAd],
      // noSearch: true
    },
  ];

  const RESOLVE = [
    // {
    //   title: t('selectReason'),
    //   placeholder: t('selectReason'),
    //   type: SCHEMA_TYPES.SELECT,
    //   name: 'reason',
    //   validator: VALIDATORS.REQUIRED,
    //   keyValues: reasonList,
    // },
    {
      title: t('workedBy'),
      placeholder: t('selectOperator'),
      type: SCHEMA_TYPES.MASTER,
      name: "user",
      validator: VALIDATORS.REQUIRED,
      fetch: [fetchByAd],
      noSearch: true
    },
    // {
    //   title: t('selectUser'),
    //   placeholder: t('selectUser'),
    //   type: SCHEMA_TYPES.SELECT,
    //   name: 'user',
    //   validator: VALIDATORS.REQUIRED,
    //   keyValues: superVisor,
    // },
    {
      title: t('date'),
      placeholder: t('date'),
      type: SCHEMA_TYPES.DATE,
      name: "date",
      validator: VALIDATORS.REQUIRED,
      // fetch: [fetchByAd],
      // noSearch: true
    },
    {
      title: t('comment'),
      placeholder: t('comment'),
      type: SCHEMA_TYPES.INPUT,
      name: "comment",
      // validator: VALIDATORS.REQUIRED,
      // fetch: [fetchByAd],
      // noSearch: true
    },
  ];

  const [errors, setErrors] = useState(new Map());

  const handleSubmit = async () => {
    const _errors = Validate(edit ? RESOLVE : TEST, edit ? dataResolve : data);
    setErrors(_errors);
    if (_errors.size) return;
    await onSubmit()
    return;
  }

  const handleChangee = async (e) => {
    const { name, value } = e.target;
    setErrors((prev) => {
      prev.delete(name);
      return prev;
    });
    if(name==='user'){
      console.log("handle data", 'user', value.value )
      setData((prev) => ({ ...prev, ['user']: value.value }));
    }else{
      setData((prev) => ({ ...prev, [name]: value }));
    }
   
  };
  const handleResolve = async (e) => {
    const { name, value } = e.target;
    setErrors((prev) => {
      prev.delete(name);
      return prev;
    });
    if(name==='user'){
      console.log("handle data", 'user', value.value )
      setDataResolve((prev) => ({ ...prev, ['user']: value.value }));
    }else{
      setDataResolve((prev) => ({ ...prev, [name]: value }));
    }
  };
  
  return (
    <Modal
      open={open}
      onClose={onClose}
      width="min(700px,90vw)"
      hideCloseIcon={crossIcon}
      wrapperClasses="px-6"
    >
      <h3 className="opacity-80">{edit === true ? t('resolveDefect') :t('addDefect') } </h3>

      <Stack sx={{ alignItems: 'flex-start', pl: 3 }}>
        <SchemaExtractor
          data={edit ? dataResolve : data}
          errors={errors}
          onChange={edit?handleResolve:handleChangee}
          schema={edit ? RESOLVE : TEST}
        />
      </Stack>
      <Stack
        direction="row"
        alignItems="center"
        justifyContent="space-between"
        mt={4}
        mb={1}
      >
        <Button bordered gray onClick={onClose}>
          {t('cancel')}
        </Button>
        <Button color={'light_green'} onClick={handleSubmit} loading={loading}>
          <Typography className="fw-20-500-24">{t('submit')}</Typography>
        </Button>
      </Stack>
    </Modal>
  );
};

export default Add;
