import React, { useEffect, useState } from "react";
import { useAsyncError, useLocation, useNavigate } from "react-router-dom";
import Table from "../../components/table";
// import useDivision from "../../store/division";
import Button from "../../components/button";
import CheckCircleOutlineIcon from '@mui/icons-material/CheckCircleOutline';
import { toast } from "react-toastify";
import { IconButton, Stack, Typography, Grid } from "@mui/material";
import ArrowBackIcon from '@mui/icons-material/ArrowBack';
import { Delete, Checklist } from "@mui/icons-material";
import AdminPanelSettingsIcon from '@mui/icons-material/AdminPanelSettings';
import EditIcon from '@mui/icons-material/Edit';
import ExpandMoreIcon from '@mui/icons-material/ExpandMore';
import KeyboardArrowRightIcon from '@mui/icons-material/KeyboardArrowRight';
import AddCircleIcon from '@mui/icons-material/AddCircle';
import Category from "../../components/UI/Category";
import { COLORS } from "../../helpers/constants";
import JobDetail from "./JobDetail";
import StartJob from "./startJob"
import JsonFormatter from "../../components/UI/JsonFormatter";
import { t } from "i18next";
import useJob from "../../store/operatorPortal/job";

const JobPage = () => {
  const location = useLocation()
  const navigate = useNavigate()
  const machineId = location.state 
  const{taskList} = useJob()
  const [users, setUsers] = useState([]);
  const [loading, setLoading] = useState();
  const [total, setTotal] = useState(0);
  const [edit, setEdit] = useState(true)
  const [modalOpen, setModalOpen] = useState(false);
  const [page, setPage] = useState(0);
  const [data, setData] = useState({
    name: "",
    metadata: {}
  });
  const [ machineType, setMachineType] = useState('')
  const [matrix , setmatrix] = useState({})
  // const { list, create, update, remove } = useDivision();

  useEffect(() => {
    fetchCompany()
  }, []);

  const fetchCompany = async () => {
    // localStorage.setItem("machineId",machineId)
    setLoading("fetch");
   let [res] = await taskList(machineId);
    setUsers(res?.items);
    setMachineType(res?.task)
    setmatrix({...matrix,"waiting":res?.waiting,"running":res?.running,"paused":res?.paused,"completed":res?.completed})
    // setTotal(_count);
    setLoading(false);
  };

  const handleAdd = async () => {
    // const payload = { ...data }
    // setLoading("fetch");
    // edit === true ? await update(payload) : await create(payload, plantId);
    // setData({});
    // setModalOpen(false);
    // await fetchCompany();
    // setLoading(false);
    setEdit(false)
  };

  const handleDelete = async (id) => {
    setLoading("fetch");
    // await remove(id);
    await fetchCompany();
    setLoading(false);
  };
  const handleUpdate = async (id, is_admin) => {
    setLoading("fetch");
    const payload = {
      "is_admin": is_admin === true ? false : true
    }
    // await update(id, payload);
    await fetchCompany();
    setLoading(false);
  };

  const LABELS = [
    {
      id: 1,
      title: t('workOrderId'),
      render: (rowData) => <Typography sx={{ cursor: 'pointer', fontWeight:'bold'}}
      //  onClick={()=>setModalOpen("detail")}
       >{rowData.work_order_order_id}</Typography>,
    },
    {
      id: 2,
      title: machineType==="ASSEMBLY"?t('FGCode'):t('CircuitName'),
      render: (rowData) => rowData.circuit_name || rowData.fg_code,
    },
    {
      id: 6,
      title: t('status'),
      render: (rowData) => {
        if (rowData?.running_status === "WAITING") {
          return <Category color={COLORS.LG_GREEN} title={t("waiting")} />
        } else if (rowData?.running_status === "RUNNING") {
          return <Category color={COLORS.LG_PINK} title={t("running")} />
        } else if (rowData?.running_status === "PAUSED") {
          return <Category color={COLORS.LG_PURPLE} title={t("paused")} />
        } else if (rowData?.running_status === "COMPLETED") {
          return <Category color={COLORS.LIGHT_GREEN} title={t("completed")} />
        }
      }
      // render: (rowData) => rowData?.running_status
    },
    {
      id: 66,
      title: t("qty"),
      render: (rowData) => rowData?.quantity
    },
    {
      id: 5,
      title: t("action"),
      render: (rowData) => 
      rowData.running_status==='COMPLETED'?(
        <CheckCircleOutlineIcon fontSize="large" sx={{color:COLORS.LIGHT_GREEN}}/>
      ):(
      <Button color={'light_green'} onClick={()=>handleNavigate(rowData)}>
        <Typography color={'black'}>
          {t('start')}
        </Typography>  
      </Button>
      )
    },
  ];

  async function handleNavigate(rowData) {
    if(machineType==="ACC"){
      navigate('/job-material-scan',{state:rowData})
    }else  if(machineType==="LPC"){
      navigate('/lpc-scan',{state:rowData})
    }else  if(machineType==="ASSEMBLY"){
      navigate('/job-assembly-scan',{state:rowData})
    }
    
  }
  return (
    <div className="mx-3 my-2">
      <div className="flex items-center justify-between mb-2">
      <div style={{ display: 'flex', flexDirection: 'row', gap: 2 ,alignItems:'center'}}>
        <IconButton onClick={() => navigate(-1)}>
          <ArrowBackIcon />
        </IconButton>
        <h1 className="mt-4">{machineType} {t("JobList")}</h1>
        </div>
  

        <Stack sx={{ flexDirection: 'row', gap: 2 }}>
          <Stack sx={{ backgroundColor: COLORS.LG_GREEN, width: 200, height: 80, borderRadius: 2, flexDirection: 'row',justifyContent:'space-between' }}>
            <Typography sx={{ p: 2, fontSize: 20, fontWeight: 500 }}> {t("Scheduled")}<br></br> {t("job")}</Typography>
            <Typography sx={{ p: 2, fontSize: 36, fontWeight: 700 }}>{matrix?.waiting}</Typography>
          </Stack>
          <Stack sx={{ backgroundColor: COLORS.LG_PINK, width: 200, height: 80, borderRadius: 2, flexDirection: 'row',justifyContent:'space-between' }}>
            <Typography sx={{ p: 2, fontSize: 20, fontWeight: 500 }}>{t("Running")}<br></br>{t("job")}</Typography>
            <Typography sx={{ p: 2, fontSize: 36, fontWeight: 700 }}>{matrix?.running}</Typography>
          </Stack>
          <Stack sx={{ backgroundColor: '#C4D7FC', width: 200, height: 80, borderRadius: 2, flexDirection: 'row',justifyContent:'space-between' }}>
            <Typography sx={{ p: 2, fontSize: 20, fontWeight: 500 }}>{t("Paused")}<br></br>{t("job")}</Typography>
            <Typography sx={{ p: 2, fontSize: 36, fontWeight: 700 }}>{matrix?.paused}</Typography>
          </Stack>
          <Stack sx={{ backgroundColor: COLORS.LG_PURPLE, width: 200, height: 80, borderRadius: 2, flexDirection: 'row',justifyContent:'space-between' }}>
            <Typography sx={{ p: 2, fontSize: 20, fontWeight: 500 }}>{t("Completed")} <br></br>{t("job")}</Typography>
            <Typography sx={{ p: 2, fontSize: 36, fontWeight: 700 }}>{matrix?.completed}</Typography>
          </Stack>
        </Stack>
      </div>
      <Table
      // headColor={COLORS.TEXT_GRAY}
      // headLabelColor={COLORS.WHITE}
        columns={LABELS}
        rows={users}
        rowsPerPage={10}
        loading={loading === "fetch"}
        titleCellClasses="c-gray fw-700"
        page={page}
        setPage={setPage}
        paginator
        serialize
        total={total}
      />
      <JobDetail
        open={modalOpen === "detail"}
        onClose={() => { setModalOpen(false); setEdit(false) }}
        data={data}
        edit={edit}
        setData={setData}
        onSubmit={handleAdd}
      />
      <StartJob
        open={modalOpen === "start"}
        onClose={() => { setModalOpen(false); setEdit(false) }}
        data={data}
        edit={edit}
        setData={setData}
        onSubmit={handleAdd}
      />
      {/* <Stack
        direction="row"
        alignItems="center"
        justifyContent="space-between"
        mt={4}
        mb={1}
        // sx={{px:6}}
      >
        <Button bordered gray >
          Cancel
        </Button>
        <Button color={'gray'} >
          <Typography color={COLORS.TEXT_GRAY}>
          Next
          </Typography>  
        </Button>
      </Stack> */}
    </div>
  );
};

export default JobPage;
