import React, { useEffect, useState, useRef } from "react";
import { useLocation, useNavigate } from "react-router-dom";
import Table from "../../../components/table";
// import useDivision from "../../store/division";
import Button from "../../../components/button";
import { toast } from "react-toastify";
import { IconButton, Stack, Typography, Grid } from "@mui/material";
import { COLORS } from "../../../helpers/constants";
import AllMatScan from "./AllMatScan"
import CheckCircleOutlineIcon from '@mui/icons-material/CheckCircleOutline';
import ArrowBackIcon from '@mui/icons-material/ArrowBack';
import { t } from "i18next";
import useJob from "../../../store/operatorPortal/job";

const LPCScan = () => {
  const location = useLocation()
  const navigate = useNavigate()
  const locationData = location.state
  const { OrderMaterialList, createLPC, updateLPCOrderStatus, getMachineStatus,LpcMaterialList } = useJob()
  const [tableData, setTableData] = useState([]);
  const [loading, setLoading] = useState();
  const [total, setTotal] = useState(0);
  const [edit, setEdit] = useState(false)
  const [modalOpen, setModalOpen] = useState(false);
  const [page, setPage] = useState(0);
  const [data, setData] = useState({
    name: "",
    metadata: {}
  });

  const [payload, setPayload] = useState()
  const [machineId, setMachineId] = useState(localStorage.getItem('machineId') || "");

  const [runningStatus, setRunningStatus] = useState(false)
  const [isConfirmDisabled, setIsConfirmDisabled] = useState(true);
  const work_order_order_id = locationData.work_order_order_id
  const [circuitList, setCircuitList] = useState([]);
  const [orderQty, setOrderQty] = useState(500);


  // console.log("locationData-----lpc", locationData)

  useEffect(() => {
    if (!tableData.length) {
      setIsConfirmDisabled(true);
      return;
    }
  
    const allMaterialsScanned = tableData.every((material) => {
      const match = payload?.[material.name]?.reduce(
        (sum, item) => sum + (item.quantity || 0),
        0
      );
      return payload?.[material.name]?.length > 0  && match>=orderQty; // Check if material has been scanned at least once
    });
  
    setIsConfirmDisabled(allMaterialsScanned);
    
    // console.log("allMaterialsScanned", allMaterialsScanned)
  }, [payload, tableData]);


  useEffect(() => {
    // console.log("workOrderTask-locationData-scan---", locationData)
    fetchMaterials();
  }, [page]);

  useEffect(() => {
    // console.log("Updated Payload in useEffect:", payload);
  }, [payload]);

  const inputRef = useRef(null);

  useEffect(() => {
    if (inputRef.current) {
      inputRef.current.focus();
    }
  }, []);

  const fetchMaterials = async () => {
    setLoading("fetch");
    const [_list, _count] = await LpcMaterialList(locationData.id, page + 1);
    const mergedData = [
        ...(_list?.materials || []).map(item => ({ ...item, type: "Material" })),
        ...(_list?.lpc || []).map(item => ({ ...item, type: "LPC Circuit" })),
        ...(_list?.acc || []).map(item => ({ ...item, type: "ACC Circuit" }))
      ];
      console.log("workOrderTask----_list-materials_verified", _list.materials_verified)
     
      if(_list.materials_verified  ===true){
          navigate("/lpc-machine-data",{state:locationData.id})
      }
  
      setTableData(mergedData);
    // setTableData([..._list?.materials,..._list.lpc,..._list.acc]);
    // setTotal(_count);h
    setLoading(false);
  };

  const SubmitScanned = async () => {
    // alert("click")
    // console.log("SubmitScanned updateOrderStatus respose----", payload)
    // return
    setModalOpen('detail')
    // setLoading("fetch");
    let req={
      "materials":payload,
      "acc":circuitList
    }

    let res = await createLPC(locationData.id, req);
    // console.log("SubmitScanned updateOrderStatus respose----", res)
    if (res && res.message === 'OK') {
      let req = { "status": "RUNNING" }
      let res = await updateLPCOrderStatus(locationData.id, req)
      setRunningStatus(true)
      setTimeout(() => {
        navigate("/lpc-machine-data",{state:locationData.id})
      }, 2000);
    }
  };


  const handleAdd = async () => {
    const payload = { ...data }
    setLoading("fetch");
    let res = await createLPC(payload, locationData.id);
    // console.log("handle add respose----", res)
    setModalOpen(false);
    // await fetchMaterials();
    setLoading(false);
    // setEdit(false)
  };

  const handleDelete = async (id) => {
    setLoading("fetch");
    // await remove(id);
    await fetchMaterials();
    setLoading(false);
  };
  const handleUpdate = async (id, is_admin) => {
    setLoading("fetch");
    const payload = {
      "is_admin": is_admin === true ? false : true
    }
    // await update(id, payload);
    await fetchMaterials();
    setLoading(false);
  };

  const LABELS = [
    {
      id: 1,
      title: t('name'),
      render: (rowData) => rowData?.name,
    },
    {
      id: 2,
      title:t('type'),
      render: (rowData) => rowData?.type,
    },
    {
      id: 3,
      title: t("description"),
      render: (rowData) => rowData?.description || rowData?.circuit_description || '-'
    },
    {
      id: 33,
      title: t("scannedQuantity"),
      render: (rowData) =>{ 
        if (rowData.type !== "Material") {
        const match = circuitList.find(item => rowData.name === item.circuit_code);
        return match?.quantity ?? '-';}
        else{
          const match = payload?.[rowData.name]?.reduce(
            (sum, item) => sum + (item.quantity || 0),
            0
          );
          return match || '-';

        }
      }
    },
    {
      id: 6,
      title: t("status"),
      render: (rowData) => {
        // console.log("row data action",rowData)
        if (rowData.type === "Material") {
        if (!payload || !payload[rowData.name]) {
          return "-"; // Default status if payload is not yet available
        }

        const totalScanned = payload[rowData.name].reduce(
          (sum, item) => sum + (item.quantity || 0),
          0
        );
        // console.log(" payload[rowData.name]----------", payload[rowData.name])
        return totalScanned>orderQty ? <CheckCircleOutlineIcon fontSize="large" sx={{ color: COLORS.LIGHT_GREEN }} /> : "-";
      }
    else{
      const match = circuitList.find(item => rowData.name === item.circuit_code);
      const qty= match?.quantity ?? 0;
      // console.log("rowData.quantity",qty)
      return qty>orderQty?<CheckCircleOutlineIcon fontSize="large" sx={{ color: COLORS.LIGHT_GREEN }} /> : "-"
    }
  }
    }
  ];

  const handleKeyPress = (e) => {
    if (e.key === "Enter") {
      handleScan(e.target.value);
      e.target.value = ""; // Clear the input for the next scan
    }
  };
  const handleScan = (data) => {
    const [type, quantityString, unit, grnNumber, grnDate] = data.split("#");
    if(type !=="ACC"){
      handleBarcodeScanMaterial(data);
    }else{
      handleBarcodeScanCircuit(data);
    }
  };

  const handleBarcodeScanMaterial = (inputData) => {
    const [material_name, quantityString, unit, grnNumber, grnDate] = inputData.split("#");
    // console.log("scan item", material_name, quantityString, unit, grnNumber, grnDate);

    const quantity = parseFloat(quantityString);
    if (isNaN(quantity)) {
      console.log("Invalid quantity");
      return;
    }

    const currentMaterial = tableData.find(
      (item) => item.name === material_name
    );
    // console.log("Material found in table data");
    if (!currentMaterial) {
      toast.warn("Material not found in data");
      return;
    }

    setPayload((prevPayload) => {
      const updatedPayload = { ...prevPayload };
      // Calculate the total existing quantity in payload for this material
    //   const existingQuantity = updatedPayload[material_name]?.reduce(
    //     (sum, item) => sum + item.quantity,
    //     0
    //   ) || 0;

      // Check if adding the new quantity exceeds the allowed quantity
      // if (quantity > currentMaterial.quantity) {
      //   toast.warn(`Cannot add.${material_name} Quantity is more then require.`);
      //   return prevPayload; // Do not update if limit is exceeded
      // }
    //   if (existingQuantity + quantity > currentMaterial.quantity) {
    //     toast.warn(`Cannot add more. Max quantity for ${material_name} reached.`);
    //     return prevPayload; // Do not update if limit is exceeded
    //   }

      if (!updatedPayload[material_name]) {
        updatedPayload[material_name] = [];
      }
      updatedPayload[material_name].push({
        quantity: quantity,
        grn_number: grnNumber 
      });

      console.log("Updated Payload Inside setPayload:", updatedPayload);
      return updatedPayload;
    });
    if (inputRef.current) {
      inputRef.current.focus();
    }
  };

  const handleBarcodeScanCircuit = (inputData) => {
    const [type, circuitName, orderId, qty] = inputData.split("#");

    const quantity = parseFloat(qty);
    if (isNaN(quantity)) {
      console.log("Invalid quantity");
      return;
    }
    if (orderId!== work_order_order_id) {
      console.log("work order id not match");
      toast.warn("work order id not match");
      return;
    }

    const currentCircuit = tableData.find(
      (item) => item.name === circuitName
    );
    // console.log("Material found in table data");
    if (!currentCircuit) {
      toast.warn("circuit not found in data");
      return;
    }

    // setPayload((prevPayload) => {
    //   const updatedPayload = { ...prevPayload };
    //   // Calculate the total existing quantity in payload for this material
    // //   const existingQuantity = updatedPayload[material_name]?.reduce(
    // //     (sum, item) => sum + item.quantity,
    // //     0
    // //   ) || 0;

    //   // Check if adding the new quantity exceeds the allowed quantity
    //   // if (quantity > currentMaterial.quantity) {
    //   //   toast.warn(`Cannot add.${material_name} Quantity is more then require.`);
    //   //   return prevPayload; // Do not update if limit is exceeded
    //   // }
    // //   if (existingQuantity + quantity > currentMaterial.quantity) {
    // //     toast.warn(`Cannot add more. Max quantity for ${material_name} reached.`);
    // //     return prevPayload; // Do not update if limit is exceeded
    // //   }

    //   if (!updatedPayload[circuitName]) {
    //     updatedPayload[circuitName] = [];
    //   }
    //   updatedPayload[circuitName].push({
    //     quantity: qty,
    //     circuit_code: circuitName 
    //   });

    //   console.log("Updated Payload Inside setPayload:", updatedPayload);
    //   return updatedPayload;
    // });

    setCircuitList((prevList) => {
      const existingIndex = prevList.findIndex(
        (item) => item.circuit_code === circuitName
      );
    
      if (existingIndex !== -1) {
        // Update the quantity if circuit_code exists
        const updatedList = [...prevList];
        updatedList[existingIndex].quantity += quantity; // Add the new quantity to the existing one
        return updatedList;
      } else {
        // Add a new object if circuit_code does not exist
        return [...prevList, { circuit_code: circuitName, quantity }];
      }
    });

    if (inputRef.current) {
      inputRef.current.focus();
    }
    console.warn("scan item circuit circuitList------", circuitList);
  }

 

  return (
    <div className="mx-3 my-2">
      <div className=" items-center mb-2">
      <div style={{ display: 'flex', flexDirection: 'row', gap: 2,alignItems:'center' }}>
        <IconButton onClick={() => navigate(-1)}>
          <ArrowBackIcon />
        </IconButton>
        <h1 className="mt-4">{locationData.circuit_name} &gt; {locationData.work_order_order_id}</h1>
        </div>
        <Stack sx={{ flexDirection: 'row', justifyContent: 'space-between', alignItems: 'center' }}>
          <h1 className="fw-24 mt-1">{t('scanMaterialCircuit')}</h1>
          <input ref={inputRef} style={{ height: '1.5rem', width: '26rem' }} type="text" onKeyPress={handleKeyPress} />
        </Stack>
      </div>
      <Table
        // headColor={COLORS.TEXT_GRAY}
        // headLabelColor={COLORS.WHITE}
        columns={LABELS}
        rows={tableData}
        // rowsPerPage={5}
        loading={loading === "fetch"}
        titleCellClasses="c-gray fw-700"
        page={page}
        setPage={setPage}
        // paginator
        serialize
        total={total}
      />
      <AllMatScan
        open={modalOpen === "detail"}
        onClose={() => { setModalOpen(false); setEdit(false) }}
        edit={edit}
      />
      <Stack
        direction="row"
        alignItems="center"
        justifyContent="space-between"
        mt={4}
        mb={1}
      // sx={{px:6}}
      >
        <Button bordered gray >
          {t('cancel')}
        </Button>
        {/* <Button bordered gray  onClick={() => console.log("payload and circuitList", payload, circuitList)}>  
          {t('print')}
        </Button> */}
        <Button color={'light_green'} onClick={() => SubmitScanned()} disabled={!isConfirmDisabled}>
          <Typography color={COLORS.TEXT_GRAY}>
            {t('confirm')}
          </Typography>
        </Button>
      </Stack>
    </div>
  );
};

export default LPCScan;
