import React,{useEffect, useState} from "react";
import Modal from "../../components/modal";
import Input from "../../components/input";
import { Stack, Typography } from "@mui/material";
import Button from "../../components/button";
import SchemaExtractor from "../../components/UI/SchemaExtractor";
import {SCHEMA_TYPES,VALIDATORS,} from '../../helpers/constants'
import { Validate } from "../../helpers/utils";
import crossIcon from '../../assets/svg/cross-icon.svg'
import { t } from "i18next";
import useUser from "../../store/users";


const AddAssign = ({ open, onClose, skillData, setSkillData, onSubmit, loading,edit,modalOpen,superList,skillsList}) => {
 

  const [errors, setErrors] = useState(new Map());
  useEffect(() => {
    if (open) {
      console.log("skillData", skillData)
      // setSkillData({
      //   ...skillData,
      //   supervisor: data.supervisor || "",
      //   skill: data.skill || "",
      // });
    }
  }, [open]);

  const handleChange = (e) => {
    const { name, value } = e.target;
    setErrors((prev) => {
      prev.delete(name);
      return prev;
    });
    setSkillData((prev) => ({ ...prev, [name]: value }));
  };
  
  const handleNext = async () => {
      const _errors = Validate(SKILL, skillData);
      setErrors(_errors);
      if (_errors.size) return;
      await onSubmit()
      return;
  };


  const SKILL = [
    {
      title: t('supervisor'),
      placeholder: "Select Supervisor",
      type: SCHEMA_TYPES.SELECT,
      name: 'supervisor',
      validator: VALIDATORS.REQUIRED,
      keyValues:superList,
    },
    {
      title: t('skill'),
      placeholder: t('selectSkill'),
      type: SCHEMA_TYPES.SELECT,
      name: 'skill',
      validator: VALIDATORS.REQUIRED,
      keyValues:skillsList,
    }
   ];

  return (
    <Modal
    open={open}
    onClose={onClose}
    width="min(700px,90vw)"
    hideCloseIcon={crossIcon}
      wrapperClasses="px-6"
    >
      <h3 className="opacity-80">{edit?"Edit":"Add"} Supervisor and Skill</h3>

      <SchemaExtractor
          data={skillData}
          errors={errors}
          onChange={handleChange}
          schema={SKILL}
        />
      
      <Stack
        direction="row"
        alignItems="center"
        justifyContent="space-between"
        mt={4}
        mb={1}
      >
        <Button bordered gray onClick={onClose}>
          {t('cancel')}
        </Button>
        <Button color={'light_green'} onClick={handleNext} loading={loading}>
          <Typography className="fw-20-500-24">
          {t('submit')}
          </Typography>    
        </Button>
      </Stack>
    </Modal>
  );
};

export default AddAssign;
