import React, { useEffect, useState } from "react";
import { useLocation, useNavigate } from "react-router-dom";
import { useDispatch } from "react-redux";
import Table from "../../components/table";
import useTeam from "../../store/teamMaster";
import useUser from "../../store/users";
import Button from "../../components/button";
import Add from "./Add";
import { toast } from "react-toastify";
import { IconButton, Stack, Typography, Grid } from "@mui/material";
import { Delete, Checklist } from "@mui/icons-material";
import JsonFormatter from "../../components/UI/JsonFormatter";
import AdminPanelSettingsIcon from '@mui/icons-material/AdminPanelSettings';
import EditIcon from '@mui/icons-material/Edit';
import ExpandMoreIcon from '@mui/icons-material/ExpandMore';
import KeyboardArrowRightIcon from '@mui/icons-material/KeyboardArrowRight';
import AddCircleIcon from '@mui/icons-material/AddCircle';
import Category from "../../components/UI/Category";
import { COLORS } from "../../helpers/constants";
import { t } from "i18next";
import UserManage from "./UserManagement";
import { setTeamItem } from '../../store/teamMaster/team'

import moment from "moment";

const TeamMasterPage = () => {
  const navigate = useNavigate()
  const dispatch = useDispatch()
  const [users, setUsers] = useState([]);
  const [loading, setLoading] = useState();
  const [total, setTotal] = useState(0);
  const [edit, setEdit] = useState(false)
  const [modalOpen, setModalOpen] = useState(false);
  const [page, setPage] = useState(0);
  const [data, setData] = useState({
    id: '',
    area_name: '',
    description: '',
    supervisor: []
  });
   const [selectedOptions, setSelectedOptions] = useState([]);
  
  const [assets, setAssets] = useState([])
  const { list, create, update, remove, assetList } = useTeam();
  const { superUsers } = useUser()

  const [superVisor, setSuperVisor] = useState([])
  const [rowItem, setRowItem] = useState('')

  useEffect(() => {
    fetchCompany();
  }, [page]);

  useEffect(() => {
    fetchAssets();
  }, []);

  const fetchCompany = async () => {
    setLoading("fetch");
    const [_list] = await list(page + 1);
    setUsers(_list.results);
    setTotal(_list.count);
    setLoading(false);
  };

  const fetchAssets = async () => {
    setLoading("fetch");
    const [_list] = await superUsers(page + 1);
    console.log("assets linst", _list)
    const newUsers = _list.results.map(item => ({
      value: item.username,
      key: item.id
    }));
    setSuperVisor(prevUsers => [...prevUsers, ...newUsers]);

  };

  const handleAdd = async () => {
    const payload = { ...data }
    const req = {
      name: payload.area_name,
      description: payload.description,
      supervisors: selectedOptions.map(item => item.id)
    }
    setLoading("fetch");
    let res = await create(req);
    if(res){
      toast.success("Team created successfully")
    }
    setData({});
    setModalOpen(false);
    await fetchCompany();
    console.log("creat res----", res)
  };

  const handleDelete = async (id) => {
    setLoading("fetch");
    await remove(id);
    setPage(0)
    await fetchCompany();
  };

  const handleUpdate = async (id, status) => {
    setLoading("fetch");
    const payload = {
      "status": status === true ? false : true
    }
    await update(id, payload);
    await fetchCompany();
    setLoading(false);
  };


  const COLUMNS = [
    {
      id: 1,
      title: t('areaName'),
      render: (rowData) => <Typography sx={{ cursor: 'pointer', textTransform: 'capitalize' }} >{rowData.name}</Typography>,
    },
    {
      id: 2,
      title: t('description'),
      render: (rowData) => <Typography >{rowData.description}</Typography>
    },
    {
      id: 4,
      title: t('supervisors'),
      render: (rowData) => (
        <Stack>
          {rowData.supervisors.map((item) => (
            <Typography key={item.id}>{item.username}</Typography>
          ))}
        </Stack>
      ),
    },
    {
      id: 5,
      title: t("action"),
      render: (rowData) => (
        <Stack flexDirection={'row'} gap={2}>
          <IconButton
            sx={{ height: 25, width: 25 }}
            onClick={() => { SetHandleEdit(rowData) }}
          >
            <EditIcon color="primary" fontSize="small" />
          </IconButton>
          <Button color={"light_green"}
            onClick={() => { dispatch(setTeamItem(rowData)); setModalOpen('UserManage') }}
          >
            {t('addOperators')}
          </Button>

          <IconButton
            sx={{ height: 25, width: 25 }}
            onClick={() => handleDelete(rowData?.id)}
          >
            <Delete color="error" fontSize="small" />
          </IconButton>
        </Stack>
      ),
    },
  ];

  const SetHandleEdit = (rowData) => {
    console.log("defect rowdata", rowData);

    const { id, name, description, supervisors } = rowData;
    if (Array.isArray(supervisors)) {
      const newUsers = supervisors.map(item => ({
          label: item.username || item.fullname || "Unknown", // Fallback for label
          id: item.id
      }));
      setSelectedOptions(newUsers); // Replaced appending with overwriting
  }
    setData(prevData => ({
      ...prevData,
      id,
      area_name: name,
      description,
      supervisor: supervisors.map(item => item.id),
    }));
    setEdit(true);
    setModalOpen("add");
    console.log("----------rowData", data);
  }

  const handleEdit = async () => {
    const payload = { ...data }
    console.log("payload", selectedOptions)
    const req = {
      name: payload.area_name,
      description: payload.description,
      supervisors: selectedOptions.map(item => item.id)
    }
    console.log("creat res----", req)
    setLoading("fetch");
    let res = await update(data.id, req);
    if(res){
      toast.success("Team updated successfully")
    }
    setPage(0)
    setData({});
    setModalOpen(false);
    await fetchCompany();
  };
 
  return (
    <div className="mx-3 my-2">
      <div className="flex items-center justify-between">
        <Typography className="fw-20-500-36">
          {t("teamMaster")}
        </Typography>
        <Stack sx={{ flexDirection: "row", gap: 1 }}>
          <Button color={'gray'} onClick={() => {
            setData(prevData => ({
              ...prevData,
              type: 'false'
            })); setModalOpen("add")
          }} loading={loading}>
            <Typography className="fw-20-500-24">
              {t('addTeamMaster')}
            </Typography>
          </Button>
        </Stack>
      </div>
    
      <Table
        columns={COLUMNS}
        rows={users}
        rowsPerPage={10}
        loading={loading === "fetch"}
        titleCellClasses="c-gray fw-700"
        page={page}
        setPage={setPage}
        paginator
        serialize
        total={total}
      />
      <Add
        open={modalOpen === "add"}
        onClose={() => { setModalOpen(false); setEdit(false); setData({}); }}
        data={data}
        edit={edit}
        setData={setData}
        onSubmit={edit === true ? handleEdit : handleAdd}
        assetsList={superVisor}
        setSelectedOptions={setSelectedOptions}
        selectedOptions={selectedOptions}
      />
      <UserManage
        open={modalOpen === "UserManage"}
        onClose={() => { setModalOpen(false); setEdit(false); setData({}); }}
        data={data}
        edit={edit}
        setData={setData}
        onSubmit={edit === true ? handleEdit : handleAdd}
        assetsList={superVisor}
        rowItem={rowItem}
        modalOpen={modalOpen}
      />
    </div>
  );
};

export default TeamMasterPage;
