import "./stylee.scss";
import React from 'react';
import {  TextField,IconButton } from '@mui/material';
import { useNavigate } from 'react-router-dom';
import Table from "../../components/tablee";
import './PlanningMaterial.css';
import Button from "../../components/buttonn"
import { useState,useEffect } from 'react';
import { useOrder } from '../../store/machiningOrders';
import useMaterialPlanning from '../../store/planningMaterial'
import CapsuleComponent from '../../components/capsule'
import actionIcon from "../../assets/svg/action-icon.svg";
import RefreshIcon from "@mui/icons-material/Refresh";
import { t } from "i18next";
import { COLORS } from "../../helpers/constants";
import CheckCircleOutlineIcon from '@mui/icons-material/CheckCircleOutline';
import { render } from "@testing-library/react";
import {formatNumberWithCommas} from "../../helpers/utils";


const orders = [
  { id: 1, orderID: '100', name: '100_SZXB0601', qty: 25, code: 'SZXB0601', description: 'ACC', orderType: 'ACC', status: 'Issuing' },
  { id: 2, orderID: '101', name: '200_SZXB0601', qty: 25, code: 'SZXB0601', description: 'Desc', orderType: 'LPC', status: 'PLAN' },
  { id: 3, orderID: '102', name: 'SZXB0601', qty: 25, code: 'SZXB0601', description: '', orderType: 'ASSEMBLY', status: 'RUNNING' },
  { id: 4, orderID: '140', name: 'HERO887677889', qty: 50, code: 'HERO887677889', description: '', orderType: 'ASSEMBLY', status: 'PLAN' },
  { id: 5, orderID: '140', name: 'HERO887677889', qty: 50, code: 'HERO887677889', description: '', orderType: 'ASSEMBLY', status: 'PLAN' },
  { id: 6, orderID: '140', name: 'HERO887677889', qty: 50, code: 'HERO887677889', description: '', orderType: 'ASSEMBLY', status: 'PLAN' },
];

const ITEMS_PER_PAGE = 10;

const PlanningMaterial = () => {
  const navigate = useNavigate();
  const {
    // fetchOrders,
    fetchOrdersMatrixView,
    fetchOrdersById,
    getOrderHoldQuantity,
    submitBOM,
    error: orderError,
    loading: ordersLoading,
  } = useOrder();

  const {matrixCount,orderList,fetchOrders} = useMaterialPlanning()
  const [currentPage, setCurrentPage] = useState(1);
  const [inputPage, setInputPage] = useState('1');
  const [fgCodeFilter, setFgCodeFilter] = useState('');
  const [orderTypeFilter, setOrderTypeFilter] = useState('');
  const [statusFilter, setStatusFilter] = useState('');

   const [isOrderDetailsModalOpen, setIsOrderDetailsModalOpen] = useState(false);
    const [selectedOrderDetailsData, setSelectedOrderDetailsData] = useState({}); // state to display order details on order details modal
    const [page, setPage] = useState(1);
    const [ordersCount, setOrdersCount] = useState(null);
    const [pagesCount, setPagesCount] = useState(null);
    const [resultsPerPage, setResultsPerPage] = useState(10);
    const [isPrevPageAvailable, SetIsPrevPageAvailable] = useState(null);
    const [isNextPageAvailable, SetIsNextPageAvailable] = useState(null);

    const [lastRefreshTime, setLastRefreshTime] = useState(null);
    const [isSortedByCode, setIsSortedByCode] = useState(null); // null means not sorted, true means ascending, false means descending
    const [isSortedByOrderType, setIsSortedByOrderType] = useState(null); // null means not sorted, true means ascending, false means descending
    const [isSortedByStatus, setIsSortedByStatus] = useState(null); // null means not sorted, true means ascending, false means descending
    const [isSortedByOrderId, setIsSortedByOrderId] = useState(true); // null means not sorted, true means ascending, false means descending
    const [isSortedByName, setIsSortedByName] = useState(null); // null means not sorted, true means ascending, false means descending
    const [isSortedByQuantity, setIsSortedByQuantity] = useState(null); // null means not sorted, true means ascending, false means descending
    const [sortingOrder, setSortingOrder] = useState(["order_id"]); // sort by order id by default
    const [orderIdFilterValue, setOrderIdFilterValue] = useState(null);
    const [fgCodeFilterValue, setFgCodeFilterValue] = useState(null);
    const [orderTypeFilterValue, setOrderTypeFilterValue] = useState(null);
    const [orderStatusFilterValue, setOrderStatusFilterValue] = useState(null);
    const [ordersData, setOrdersData] = useState([]);
     const [pageinputValue, setPageinputValue] = useState(page);

     const [matrixData,setMatrixData] = useState({})

     useState(()=>{
      fetchMatrix()
     },[])

    async function fetchMatrix(){
      let res = await matrixCount()
      console.log("fetchMatrix ---",res)
      setMatrixData(res[0])
     }

  const filteredOrders = orders.filter(order => {
    return (
      (fgCodeFilter === '' || order.code.toLowerCase().includes(fgCodeFilter.toLowerCase())) &&
      (orderTypeFilter === '' || order.orderType.toLowerCase().includes(orderTypeFilter.toLowerCase())) &&
      (statusFilter === '' || order.status.toLowerCase().includes(statusFilter.toLowerCase()))
    );
  });

  const totalPages = Math.ceil(filteredOrders.length / ITEMS_PER_PAGE);

  const handleNext = () => {
    if (currentPage < totalPages) {
      const nextPage = currentPage + 1;
      setCurrentPage(nextPage);
      setInputPage(nextPage.toString());
    }
  };

  const handlePrev = () => {
    if (currentPage > 1) {
      const prevPage = currentPage - 1;
      setCurrentPage(prevPage);
      setInputPage(prevPage.toString());
    }
  };

  const handlePageChange = (e) => {
    const value = e.target.value;
    setInputPage(value);

    const page = parseInt(value, 10);
    if (!isNaN(page) && page >= 1 && page <= totalPages) {
      setCurrentPage(page);
    }
  };

  const handleInputBlur = () => {
    if (inputPage === '' || isNaN(parseInt(inputPage, 10)) || parseInt(inputPage, 10) < 1 || parseInt(inputPage, 10) > totalPages) {
      setInputPage(currentPage.toString());
    }
  };

  const handleKeyPress = (e) => {
    if (e.key === 'Enter') {
      handleInputBlur();
    }
  };

  const startIndex = (currentPage - 1) * ITEMS_PER_PAGE;
  const currentOrders = filteredOrders.slice(startIndex, startIndex + ITEMS_PER_PAGE);

  React.useEffect(() => {
    setCurrentPage(1);
    setInputPage('1');
  }, [fgCodeFilter, orderTypeFilter, statusFilter]);
  
  useEffect(() => {
    fetchOrders(
      page,
      isSortedByCode,
      isSortedByOrderType,
      isSortedByStatus,
      isSortedByOrderId,
      isSortedByName,
      isSortedByQuantity,
      sortingOrder,
      orderIdFilterValue,
      fgCodeFilterValue,
      orderTypeFilterValue,
      orderStatusFilterValue
    ).then((data) => {
      setOrdersData(data.results);
      setOrdersCount(data.count);
      const totalPages = Math.ceil(data.count / resultsPerPage);
      setPagesCount(totalPages);

      setPageinputValue(page);

      if (data.previous) {
        SetIsPrevPageAvailable(true);
      } else {
        SetIsPrevPageAvailable(false);
      }
      if (data.next) {
        SetIsNextPageAvailable(true);
      } else {
        SetIsNextPageAvailable(false);
      }
    });
  }, [
    page,
    isSortedByCode,
    isSortedByOrderType,
    isSortedByStatus,
    isSortedByOrderId,
    isSortedByName,
    isSortedByQuantity,
    orderIdFilterValue,
    fgCodeFilterValue,
    orderTypeFilterValue,
    orderStatusFilterValue,
  ]);

  async function fetchOrderss(){
    let [res] = await orderList(
          page,
          isSortedByCode,
          isSortedByOrderType,
          isSortedByStatus,
          isSortedByOrderId,
          isSortedByName,
          isSortedByQuantity,
          sortingOrder,
          orderIdFilterValue,
          fgCodeFilterValue,
          orderTypeFilterValue,
          orderStatusFilterValue
    )
    console.log("all order -------",res)
    setOrdersData(res.results);
        setOrdersCount(res.count);
        const totalPages = Math.ceil(res.count / resultsPerPage);
        setPagesCount(totalPages);
  
        setPageinputValue(page);
  
        if (res.previous) {
          SetIsPrevPageAvailable(true);
        } else {
          SetIsPrevPageAvailable(false);
        }
        if (res.next) {
          SetIsNextPageAvailable(true);
        } else {
          SetIsNextPageAvailable(false);
        }
  }

  
  const COLUMNS = (navigate) => [
    {
      id: 1,
      title: t("date"),
      render: (orderData) =>orderData.date_created
    },
    {
      id: "order_id",
      title: `${t("orderId")} ${
        isSortedByOrderId === true
          ? ""
          : isSortedByOrderId === false
          ? ""
          : ""
      }`,
      render: (rowData) => (
        <span
          // onClick={() => handleOrderClick(rowData.id)}
          style={{ cursor: "pointer"}}
        >
          {rowData.order_id}
        </span>
      ),
    },
    {
      id: "name",
      title: `${t("name")} ${
        isSortedByName === true ? "" : isSortedByName === false ? "" : ""
      }`,
      render: (rowData) => rowData.name,
    },
    {
      id: "quantity",
      // title: "Qty",
      title: `${t("qty")} ${
        isSortedByQuantity === true
          ? ""
          : isSortedByQuantity === false
          ? ""
          : ""
      }`,
      render: (rowData) => formatNumberWithCommas(rowData.quantity),
    },
    {
      id: "fg_code",
      title: `${t("code")} ${
        isSortedByCode === true ? "" : isSortedByCode === false ? "" : ""
      }`,
      render: (rowData) => rowData.fg_code,
    },
    // {
    //   id: 5,
    //   title: "Description",
    //   render: (rowData) => rowData.fg_description,
    // },
    {
      id: "order_type",
      title: `${t("orderType")} ${
        isSortedByOrderType === true
          ? ""
          : isSortedByOrderType === false
          ? ""
          : ""
      }`,
      render: (rowData) => rowData.order_type,
    },
    {
      id: "status",
      title: `${t("status")} ${
        isSortedByStatus === true
          ? ""
          : isSortedByStatus === false
          ? ""
          : ""
      }`,
      render: (rowData) => <CapsuleComponent capsuleText={rowData.running_status} />,
    },
    {
      id: 9,
      title: t("action"),
      render: (orderData) => {
        return orderData.running_status !== "COMPLETED" ? (
          <img
            src={actionIcon}
            style={{
              width: "2.78vw",
              height: "2.78vw",
              cursor: "pointer",
            }}
            alt="Action Icon"
            onClick={() => {
              if (orderData.order_type === "ACC") {
                navigate("/planning-material-plan", { state: orderData });
              } else if (orderData.order_type === "LPC") {
                navigate("/planning-lpc-material", { state: orderData });
              } else if (orderData.order_type === "ASSEMBLY") {
                navigate("/planning-assembly-material", { state: orderData });
              }
            }}
          />
        ) : (
          <IconButton
            onClick={() => {
              if (orderData.order_type === "ACC") {
                navigate("/planning-material-plan", { state: orderData });
              } else if (orderData.order_type === "LPC") {
                navigate("/planning-lpc-material", { state: orderData });
              } else if (orderData.order_type === "ASSEMBLY") {
                navigate("/planning-assembly-material", { state: orderData });
              }
            }}
          >
            <CheckCircleOutlineIcon fontSize="large" sx={{ color: COLORS.LIGHT_GREEN }} />
          </IconButton>
        );
      },
    }
    
  ];

  const handleColumnHeadClick = (columnId) => {
    switch (columnId) {
      case "fg_code":
        setIsSortedByCode((prevState) => {
          switch (prevState) {
            case null:
              console.log("a is null");
              // add fg_code string to sortingOrder state array as isSortedbyCode is being set true from previous null value
              setSortingOrder((prevState) => {
                return [...prevState, columnId];
              });
              return true;
            case true:
              console.log("a is true");
              // no change in sortingOrder array as sorting by fg_code is still present, just the order is reversed to descending.
              return false;
            case false:
              console.log("a is false");
              // remove fg_code string from sortingOrder state array as isSortedbyCode is being set to null value
              setSortingOrder((prevState) => {
                return prevState.filter((id) => id !== columnId);
              });
              return null;
            default:
              console.log("a is an unexpected value");
              return prevState;
          }
        });
        console.log("code case");
        break;
      case "order_type":
        setIsSortedByOrderType((prevState) => {
          switch (prevState) {
            case null:
              console.log("a is null");
              setSortingOrder((prevState) => {
                return [...prevState, columnId];
              });
              return true;
            case true:
              console.log("a is true");
              return false;
            case false:
              console.log("a is false");
              setSortingOrder((prevState) => {
                return prevState.filter((id) => id !== columnId);
              });
              return null;
            default:
              console.log("a is an unexpected value");
              return prevState;
          }
        });
        console.log("order type case");
        break;
      case "status":
        setIsSortedByStatus((prevState) => {
          switch (prevState) {
            case null:
              console.log("a is null");
              setSortingOrder((prevState) => {
                return [...prevState, columnId];
              });
              return true;
            case true:
              console.log("a is true");
              return false;
            case false:
              console.log("a is false");
              setSortingOrder((prevState) => {
                return prevState.filter((id) => id !== columnId);
              });
              return null;
            default:
              console.log("a is an unexpected value");
              return prevState;
          }
        });
        console.log("status case");
        break;
      case "order_id":
        setIsSortedByOrderId((prevState) => {
          switch (prevState) {
            case null:
              console.log("a is null");
              setSortingOrder((prevState) => {
                return [...prevState, columnId];
              });
              return true;
            case true:
              console.log("a is true");
              return false;
            case false:
              console.log("a is false");
              setSortingOrder((prevState) => {
                return prevState.filter((id) => id !== columnId);
              });
              return null;
            default:
              console.log("a is an unexpected value");
              return prevState;
          }
        });
        console.log("order id case");
        break;
      case "name":
        setIsSortedByName((prevState) => {
          switch (prevState) {
            case null:
              console.log("a is null");
              setSortingOrder((prevState) => {
                return [...prevState, columnId];
              });
              return true;
            case true:
              console.log("a is true");
              return false;
            case false:
              console.log("a is false");
              setSortingOrder((prevState) => {
                return prevState.filter((id) => id !== columnId);
              });
              return null;
            default:
              console.log("a is an unexpected value");
              return prevState;
          }
        });
        console.log("order id case");
        break;
      case "quantity":
        setIsSortedByQuantity((prevState) => {
          switch (prevState) {
            case null:
              console.log("a is null");
              setSortingOrder((prevState) => {
                return [...prevState, columnId];
              });
              return true;
            case true:
              console.log("a is true");
              return false;
            case false:
              console.log("a is false");
              setSortingOrder((prevState) => {
                return prevState.filter((id) => id !== columnId);
              });
              return null;
            default:
              console.log("a is an unexpected value");
              return prevState;
          }
        });
        console.log("order id case");
        break;
      default:
        console.log(columnId, "clicked");
    }
  };
  const handleOrderIdFilterInput = (orderIdFilterValue) => {
    console.log("handleOrderIdFilterInput", orderIdFilterValue);
    setOrderIdFilterValue(orderIdFilterValue);
  };
  const handleFgCodeFilterInput = (fgCodeFilterValue) => {
    console.log("handleFgCodeFilterInput", fgCodeFilterValue);
    setFgCodeFilterValue(fgCodeFilterValue);
  };
  const handleOrderTypeFilterInput = (orderTypeFilterValue) => {
    console.log("handleOrderTypeFilterInput", orderTypeFilterValue);
    setOrderTypeFilterValue(orderTypeFilterValue);
  };
  const handleOrderStatusFilterInput = (orderStatusFilterValue) => {
    console.log("handleOrderStatusFilterInput", orderStatusFilterValue);
    setOrderStatusFilterValue(orderStatusFilterValue);
  };

  const handlePageInputChange = (e) => {
    const value = e.target.value;
    if (/^\d*$/.test(value)) {
      // Allow only digits
      setPageinputValue(value);
    }
  };
  const handlePageInputBlur = () => {
    const newPage = Math.ceil(pageinputValue);
    if (!isNaN(newPage) && newPage > 0 && newPage <= pagesCount) {
      setPage(newPage);
    } else {
      setPageinputValue(page); // Revert to current page if input is invalid
    }
  };

  return (
    <div>
      <div style={{marginBottom:10}} className="planning-overview-heading">
        {t('planning')}
      </div>
      <div className="planning-overview-metrics-container">
      <div className="planning-metrics-box1">
          <p>{t('plan')}</p>
          <p><strong>{matrixData.plan}</strong></p>
        </div> 
      <div className="planning-metrics-box4">
          <p>{t('planned')}</p>
          <p><strong>{matrixData.planned}</strong></p>
        </div>
        
        <div className="planning-metrics-box2">
          <p>{t('running')}</p>
          <p><strong>{matrixData.running}</strong></p>
        </div>
        <div className="planning-metrics-box3">
          <p>{t('completed')}</p>
          <p><strong>{matrixData.completed}</strong></p>
        </div>
        {/* <div className="planning-last-refresh">
          <div className="planning-left-part">
            <p>Last Refresh</p>
            <p>12/29/2024, 4:31:53 PM</p>
          </div>
          <div className="planning-right-part">
            
            <IconButton >
                <RefreshIcon />
              </IconButton>
          
          </div>
        </div> */}
      </div>
   
      <div style={{padding:10}}>
       <Table
       mode= {'planning'}
          // edit={edit}
          columns={COLUMNS(navigate)}
          onTableHeadCellClick={(title) => {
            handleColumnHeadClick(title);
          }}
          rows={ordersData}
          rowsPerPage={resultsPerPage}
          loading={ordersLoading}
          titleCellClasses="c-gray fw-700"
          // setPage={setPage}
          page={page - 1}
          // paginator
          serialize
          total={ordersCount}
          handleOrderIdFilterInput={(value) => {
            handleOrderIdFilterInput(value);
          }}
          handleFgCodeFilterInput={(value) => {
            handleFgCodeFilterInput(value);
          }}
          handleOrderTypeFilterInput={(value) => {
            handleOrderTypeFilterInput(value);
          }}
          handleOrderStatusFilterInput={(value) => {
            handleOrderStatusFilterInput(value);
          }}
        />
        </div>
        <div style={{display:'flex',flexDirection:'row',justifyContent:'space-between',paddingLeft:30,paddingRight:30,marginBottom:20}} className="pagination-controls">
          <Button
            onClick={() => {
              setPage(page - 1);
              console.log("prev page");
            }}
            white={true}
            width="auto"
            height="auto"
            bordered={true}
            disabled={!isPrevPageAvailable}
            customStyle={{
              border: "1px solid #3E3E3E",
              backgroundColor: !isPrevPageAvailable ? "#CCCCCC" : "#FFFFFF",
              color: !isPrevPageAvailable ? "#999999" : "#000000",
              cursor: !isPrevPageAvailable ? "not-allowed" : "pointer",
            }}
          >
            {t('prev')}
          </Button>
          <div style={{display:'flex',flexDirection:'row',gap:10,alignItems:'center'}} className="page-details">
            <div>{t('page')}</div>{" "}
            <input
              type="number"
             style={{width:18,padding:10,alignContent:'center'}}
              value={pageinputValue}
              onChange={handlePageInputChange}
              onBlur={handlePageInputBlur}
              // className="page-number-inputt"
            />
            <div>of </div>
            <div>
              <strong>{pagesCount}</strong>
            </div>
          </div>
          <Button
            onClick={() => {
              setPage(page + 1);
              console.log("next page");
            }}
            color="green"
            width="auto"
            height="auto"
            disabled={!isNextPageAvailable}
            customStyle={{
              backgroundColor: !isNextPageAvailable ? "#CCCCCC" : "#27ae60",
              color: !isNextPageAvailable ? "#999999" : "#FFFFFF",
              cursor: !isNextPageAvailable ? "not-allowed" : "pointer",
            }}
          >
            {t('next')}
          </Button>
        </div>
    </div>  
  );
};

export default PlanningMaterial;